@use "./_colors.scss" as *;
@use "./_fonts.scss" as *;
@use "./_mixins.scss" as *;

@mixin heading1 {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  color: $pen-font-color-secondary;
  font-family: $font-main-display-large;
  font-size: 58px;
  font-style: regular;
  letter-spacing: 0;
  line-height: 60px;
  text-transform: uppercase;

  &:lang(zh-HK),
  &:lang(zh-TW),
  &:lang(zh-Hant) {
    font-family: $font-cn-large;
  }

  @include screen(sm, true) {
    font-size: 32px;
    line-height: 36px;

    &:lang(zh-HK),
    &:lang(zh-TW),
    &:lang(zh-Hant) {
      letter-spacing: 2px;
      font-family: $font-cn-large;
    }
  }
}

@mixin heading2 {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  color: $pen-font-color-secondary;
  font-family: $font-main-display-large;
  font-size: 44px;
  font-style: regular;
  letter-spacing: 0;
  line-height: 48px;
  text-transform: uppercase;

  &:lang(zh-HK),
  &:lang(zh-TW),
  &:lang(zh-Hant) {
    font-family: $font-cn-large;
  }

  @include screen(sm, true) {
    font-size: 30px;
    line-height: 36px;
    letter-spacing: 2px;
    font-family: $font-main-display-small;

    &:lang(zh-HK),
    &:lang(zh-TW),
    &:lang(zh-Hant) {
      font-family: $font-cn-large;
      font-size: 28px;
    }
  }
}

@mixin heading3($color: $pen-font-color-secondary) {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  color: $color;
  font-family: $font-main-display-large;
  font-size: 36px;
  font-style: regular;
  letter-spacing: 0;
  line-height: 44px;
  text-transform: uppercase;

  &:lang(zh-HK),
  &:lang(zh-TW),
  &:lang(zh-Hant) {
    font-family: $font-cn-large;
  }

  @include screen(sm, true) {
    font-size: 24px;
    line-height: 30px;
    letter-spacing: 2px;
    font-family: $font-main-display-small;

    &:lang(zh-HK),
    &:lang(zh-TW),
    &:lang(zh-Hant) {
      font-family: $font-cn-large;
    }
  }
}

@mixin heading4 {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  color: $pen-font-color-secondary;
  font-family: $font-main-display;
  font-size: 24px;
  font-style: Book;
  letter-spacing: 0;
  line-height: 28px;

  &:lang(zh-HK),
  &:lang(zh-TW),
  &:lang(zh-Hant) {
    font-family: $font-cn-large;
  }

  @include screen(sm, true) {
    font-size: 18px;
    line-height: 22px;
    letter-spacing: 1px;
    font-family: $font-main-display-small;

    &:lang(zh-HK),
    &:lang(zh-TW),
    &:lang(zh-Hant) {
      font-family: $font-cn-large;
    }
  }
}

@mixin heading5 {
  color: $pen-font-color-secondary;
  font-family: $font-main-display;
  font-size: 20px;
  font-style: Book;
  letter-spacing: 0;
  line-height: 24px;

  &:lang(zh-HK),
  &:lang(zh-TW),
  &:lang(zh-Hant) {
    font-family: $font-cn;
  }

  @include screen(sm, true) {
    font-size: 16px;
    line-height: 20px;
    letter-spacing: 1px;
    font-family: $font-main-display-small;

    &:lang(zh-HK),
    &:lang(zh-TW),
    &:lang(zh-Hant) {
      font-family: $font-cn-large;
    }
  }
}

@mixin heading6 {
  color: $pen-font-color-secondary;
  font-family: $font-main-display;
  font-size: 13px;
  font-style: Book;
  letter-spacing: 1.3px;
  line-height: 22px;
  text-transform: uppercase;

  &:lang(zh-HK),
  &:lang(zh-TW),
  &:lang(zh-Hant) {
    font-family: $font-cn;
  }

  @include screen(sm, true) {
    font-family: $font-main-display-small;

    &:lang(zh-HK),
    &:lang(zh-TW),
    &:lang(zh-Hant) {
      font-family: $font-cn-large;
    }
  }
}

@mixin caption {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  font-size: 14px;
  font-family: $font-main-display-small;
  letter-spacing: .1px;
  line-height: 22px;
  text-transform: uppercase;

  &:lang(zh-HK),
  &:lang(zh-TW),
  &:lang(zh-Hant) {
    font-family: $font-cn;
  }
}

h1 {
  @include heading1;
}

h2 {
  @include heading2;
}

h3 {
  @include heading3;

  a {
    @include heading3($pen-gold-secondary);
  }
}

h4 {
  @include heading4;
}

h5 {
  @include heading5;
}

h6 {
  @include heading6;
}

@mixin body-type {
  color: $pen-font-color-secondary;
  font-family: $font-main-display;
  font-size: 18px;
  line-height: 22px;
  letter-spacing: 0;

  &:lang(zh-HK),
  &:lang(zh-TW),
  &:lang(zh-Hant) {
    font-size: 16px;
    font-family: $font-cn;
  }
}

body {
  @include body-type;
}

.caption {
  @include caption;
}

.error {
  color: $error-red;
  line-height: 22px;
  font-size: 13px;
}

.hidden {
  display: none !important;
  /* stylelint-disable */
}

.invisible {
  position: absolute;
  width: 1px;
  height: 1px;
  left: -5000px;
  display: none;
  overflow: hidden;
}

.disableAccessibility {
  visibility: hidden;
}

@mixin card-wyiwyg-content {
  h3 {
    color: $pen-gold-primary;
  }

  h3,
  p {
    margin-bottom: 1.5rem;
  }

  a {
    text-decoration: underline;
  }
}

// below spared

@mixin type-button-base {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  font-size: 1.4rem;
  letter-spacing: 1px;
  text-transform: uppercase;
  min-width: 188px;
  min-height: 50px;
  padding: 14px 20px;
  box-sizing: border-box;
}

@mixin type-button {
  @include type-button-base;
  color: $pen-gold-primary;
  display: inline-block;
}