.modal {
    display: none;
    position: fixed;
    z-index: 1000;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0);
    transition: background-color 0.3s ease;
}

.show {
    display: block;
    background-color: rgba(0, 0, 0, 0.7);
}

.modalContent {
    position: relative;
    background-color: #fefefe;
    margin: auto;
    padding: 20px;
    max-width: fit-content;
    max-height: 80vh;
    top: 45%;
    transform: translateY(-50%) scale(0.9);
    text-align: center;
    opacity: 0;
    transition: all 0.3s ease;
    border-radius: 6px;
}

.show .modalContent {
    opacity: 1;
    top: 50%;
    transform: translateY(-50%) scale(1);
}

.close {
    position: absolute;
    right: 10px;
    top: 5px;
    color: #aaa;
    font-size: 28px;
    font-weight: bold;
    cursor: pointer;
    transition: color 0.2s ease;
}

.close:hover {
    color: #a68f5e;
}

.modalQrCode {
    max-width: 100%;
    max-height: calc(80vh - 100px);
    width: auto;
    height: auto;
}

.qrContainer img {
    width: 160px;
    height: 160px;
}

.icon {
    cursor: pointer;
}

@media (max-width: 768px) {
    .modalContent {
        max-width: 95%;
        padding: 15px;
        box-sizing: border-box;
    }
}