// Color variables

// Gold Palette
$pen-dark-gold: #67553c; // rgb(103, 85, 60, 1)
$pen-gold: #867050; // rgb(134, 112, 80, 1)
$pen-light-gold: #ae9364; // rgba(174, 147, 100, 1)

// Greyscale
$origin-black: #000000;
$pen-dark: #262626; // rgba(38, 38, 38, 1)
$pen-darker-grey: #444444; //footer text color (mobile)
$pen-dark-grey: #4a4a4a; // rgb(74, 74, 74)
$pen-mid-grey: #6d6e71;  // rgba(109, 110, 113, 1);
$pen-light-grey: #828387;
$pen-border-grey: #dbdbdb; // rgba(219, 219, 219, 1)
$pen-slick-grey: #d9d9d9; // rgba(217, 217, 217, 1)
$ccc-grey: #cccccc; // rgba(204, 204, 204, 1)
$g6-grey: #f2f2f2; // rgba(242, 242, 242, 1)
$f8-grey: #f8f8f8; // rgba(248, 248, 248, 1)
$background-grey: #f2f2f3;
$background-grey2: #f9f9f9; // rgba(249,249,249)
$banner-inactive-line: #ffffff66; // rgba(255, 255, 255, 0.4)
$banner-inactive-text: #ffffffb3; // rgba(255, 255, 255, 0.7)
$origin-white: #ffffff;

// Others
$error-red: #912018;
$blue-outline-light: #5c95eb;
$blue: #89b2f1;
$beige-bg: #f7f4ef; // rgba(247, 244, 239, 1);

$background-dark: #262626;




// Color adapter (Please only use adapter color to control the global color)
$pen-gold-primary: $pen-dark-gold;
$pen-gold-secondary: $pen-gold;
$pen-gold-tertiary: $pen-light-gold;

$pen-font-color-primary: $pen-dark-grey;
$pen-font-color-secondary: $origin-black;
$pen-font-color-tertiary: $origin-white;


