@use "../_colors.scss" as *;
@use "../_mixins.scss" as *;
@use "../_fonts.scss" as *;

.hsh-brand-logo {
    $gap-space: 30px;

    .logo-container {
        padding: 0 160px;

        @include screen(xl, true) {
            padding: 0 80px;
        }

        @include screen(sl, true) {
            padding: 0 60px;
        }

        @include screen(md, true) {
            padding: 0 30px;
        }
    }

    .border {
        border: 3px solid rgba(219, 219, 219, 0.5);

        @include screen(lg, true) {
            padding: 0 20px;
        }
    }

    .title {
        margin-top: 45px;
        margin-bottom: 20px;
        font-size: 28px;
        font-weight: 400;
        //hsh-143: mobile keep 18px
        font-size: 18px;
        line-height: 30px;
        letter-spacing: 5%;
        text-align: center;
        text-transform: uppercase;

        @include applyFont($font-main-display-large, $font-cn-large);

        // @include screen(md, true) {
        //     font-size: 15px;
        // }
    }

    .logo-grid {
        display: flex;
        flex-wrap: wrap;
        gap: $gap-space;
        justify-items: center;
        align-items: center;
        padding-bottom: 55px;
        width: 900px;
        margin: 0 auto;

        @include screen(lg, true) {
            width: 100%;
            margin: auto;
        }
    }

    .logo-item {
        flex: 1 0 calc(20% - $gap-space);
    }

    @include screen(md) {
        // force to look like figma
        .logo-item:nth-child(1),
        .logo-item:nth-child(2),
        .logo-item:nth-child(3),
        .logo-item:nth-child(4) {
            flex: 1 0 calc(25% - $gap-space);
        }
    }

    @include screen(md, true) {
        .logo-item {
            flex: 1 0 calc(50% - $gap-space);
        }

        // force to look like figma
        .logo-item:nth-child(n+7) {
            flex: 1 0 calc(33% - $gap-space);
        }
    }

    .logo-item img {
        width: 100%;
        height: auto;
    }
}