@use "@/styles/_colors.scss" as *;
@use "@/styles/_mixins.scss" as *;
@use "@/styles/_fonts.scss" as *;

// Main container
:global(.pressDetailsHeader){
  background-color: #fff;
  .wrapper {  
    padding-top: 80px;

    max-width: 800px;
    text-align: center;
    margin: 0 auto;

    @include screen(md, true) {
      padding-top: 30px;
    }

    // @include responsiveWidth( $maxWidth: 800px );
    @include responsivePadding();

    .date{
      font-family: Futura Std;
      font-weight: 400;
      font-style: Book;
      font-size: 14px;
      line-height: 24px;
      letter-spacing: 0%;
      text-align: center;
      color: $pen-gold;

      margin-bottom: 10px;
    }
    h1{
      font-weight: 400;
      font-style: Regular;
      font-size: 32px;
      line-height: 44px;
      letter-spacing: 0%;
      text-align: center;
      color: $origin-black;
      margin-bottom: 20px;
      // hsh-325 comment
      text-transform: capitalize;
    }
    :global(.share){
      .shareBtn{
        display: inline-flex;
        flex-flow: row nowrap;
        gap: 5px;
        align-items: center;
        font-family: Futura Std;
        font-weight: 400;
        font-style: Book;
        font-size: 14px;
        line-height: 24px;
        letter-spacing: 0%;
        text-align: center;
        color: $pen-gold;
        cursor: pointer;
        >svg{
          width: 16px;
          height: 16px;
        }
      }
    }
  }
}