@use "@/styles/_colors.scss" as *;
@use "@/styles/_mixins.scss" as *;
@use "@/styles/_fonts.scss" as *;

// Main container
:global(.pressParagraph){
  .wrapper {  
    box-sizing: border-box;
    // padding: 40px 50px;
    margin-top: 50px;
    margin-bottom: 50px;

    // width: calc(100% - 100px); 
    text-align: left;

    @include screen(md, true) {
      // width: calc(100% - 40px);
    }

    >div{
      &:not(:last-child){
        padding-bottom: 60px;
      }

      h2{
        font-family: $font-main-display-small;
        font-weight: 400;
        font-size: 24px;
        line-height: 28px;
        letter-spacing: 0px;
        text-transform: uppercase;

        color: $origin-black;
        margin-bottom: 12px;
      }

      .desc{
        font-family: $font-main-display;
        font-weight: 400;
        letter-spacing: 0%;
        text-align: left;
        color: $pen-dark-grey;
        @include descFontSize();
        ul,
        ol{
          box-sizing: border-box;
          margin: 0;           /* Vertical spacing */
          padding-left: 20px;      /* Indentation */

          li{
            display: list-item;  
            list-style: inherit;
            &:not(:last-child){
              margin-bottom: 24px;
            }
          }
        }

        ul{
          list-style: disc outside;
        }
        ol{
          list-style: decimal outside;
        }
        p{
          margin: 0;
          &:not(:last-child){
            margin-bottom: 24px;
          }
        }
      }
    }
  }
}