@use "../_colors.scss" as *;
@use "../_mixins.scss" as *;

.hsh-inline-cta-wrapper {
    margin-top: -40px;
    display: flex;
    flex-direction: column;
    row-gap: 20px;
    margin-bottom: 80px;

    @include screen(md, true){
        row-gap: 30px;
        margin-top: -30px;
        margin-bottom: 50px;
    }
}

.hsh-inline-cta {
    display: flex;
    justify-content: center;

    @include screen(md, true){
        padding: 0 20px;
    }

    &:hover {
        svg {
            margin-left: 16px;
            transition: margin-left .3s;
        }
    }

    span {
        font-size: 14px;
        leading-trim: CAP_HEIGHT;
        line-height: 22px;
        letter-spacing: 0%;
    }

    svg {
        margin-left: 9px;
        vertical-align: middle;
    }
}