@use '../_fonts.scss' as *;
@use '../_mixins.scss' as *;
@use '../_colors.scss' as *;

// page-wrapper is the parent class given by hsh groupSite
.page-wrapper .mapboxComponent {
    .circle-marker {
        >span {
            color: $origin-black;
            font-weight: 400;
            font-size: 18px;
            line-height: 28px;
            letter-spacing: 0px;
            text-transform: uppercase;
            white-space: nowrap;
            display: none;
            @include applyFont($font-main-display-large, $font-cn-large);
        }

        &.active-circle-marker>span {
            display: block;
            padding-left: 40px;
        }
    }
}