@use '@/styles/_mixins.scss' as *;
@use "@/styles/_fonts.scss" as *;

.textWrapper {
    width: 100%;
    padding: 0 23.5%;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    flex-direction: column;

    @include screen(md, true) {
        padding: 0 40px;
    }

    @include screen(sm, true) {
        padding: 0 20px;
    }
}

.title {
    text-align: center;
    font-weight: 400;
    font-size: 58px;
    line-height: 65px;
    text-transform: uppercase;
    margin-bottom: 20px;

    @include screen(md, true) {
        font-size: 32px;
        line-height: 36px;
    }
}

.desc {
    text-align: center;
    margin-bottom: 40px;
    @include descFontSize();
}

.mediaItemsContainer {
    display: flex;
    width: 100%;
    justify-content: center;
    gap: 0 4.8%;

    @include screen(md, true) {
        flex-direction: column;
        align-items: center;
    }
}

.mediaWrapper {
    flex: 1;
    max-width: 320px;
    display: flex;
    flex-direction: column;
    align-items: center;

    @include screen(md, true) {
        padding-bottom: 30px;
        width: 320px;
    }
}

.imageWrapper {
    aspect-ratio: 313/444;
    width: 100%;
    max-width: 313px;

    img {
        width: 100%;
        height: auto;
        object-fit: contain;
    }
}

.textContainer {
    padding: 0 10%;

    @include screen(md, true) {
        margin-top: 20px;
        padding: 0 5%;
    }
}

.name {
    font-size: 18px;
    line-height: 28px;
    text-align: center;
    margin-bottom: 20px;
    margin-top: 20px;
}

.btnContainer {
    display: flex;
    justify-content: center;
}

.ctaCustom {
    min-width: auto !important;
    min-height: 40px !important;
    padding: 5px 20px !important;
    line-height: 1;
}