@use "@/styles/_colors.scss" as *;
@use "@/styles/_mixins.scss" as *;
@use "@/styles/_fonts.scss" as *;



.wrapper {  
    overflow: hidden;
    padding: 20px;
    margin: 10px 0 20px;

    .wrapperBody {
        max-width: 1280px;
        margin: 0 auto ;
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 20px;
        box-sizing: border-box;
    }

    @include screen(md, true) {
        margin: 0 0 20px;
        .wrapperBody {
            grid-template-columns: 1fr;
        }
        padding-top: 20px;
        padding-bottom:32px;
    }


    .title {
        font-weight: 400;
        letter-spacing: 10%;
        text-transform: uppercase;
        text-align: center;
        margin-bottom: 20px;
        font-family: $font-main-display-large;

        @include screen(sm,true) {
            font-size: 24px;
            line-height: 30px;
        }
    }
    .gridItem {
        background-color: #f9f9f9;
        padding: 40px;
        > h3 {
            margin-bottom: 20px;
            font-weight: 400;
            font-size: 36px;
            line-height: calc(44px/36px);
        }
        @include screen(md, true) {
            padding: 20px;
            > h3 {
                font-size: 24px;
                line-height: calc(30px/24px);
            }
        }
    }

    .stockInfoFrame {
        min-height: 240px;
        width: 100%;
        position: relative;
        top: 4px;
        iframe {
            position: absolute;
            left: 0;
            top: 0;
            border: none;
            width: 100%;
            height: 100%;
        }
        @include screen(md, true) {
            min-height: 240px;
        }
    }

    .eventList {
        margin: 0 ;
        padding: 0 ;
        list-style: none;
        .eventItem {
            padding: 0 ;
            margin: 0 0 20px;
            display: flex;
            flex-direction: row;
            justify-content: flex-start;
            gap: 20px;
            .eventDate {
                aspect-ratio: 1;
                width: 60px;
                height: 60px;
                text-align: center;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;;
                border: 1px solid #DBDBDB;
                color: #867050;
                line-height: 1em;
                > .eventDateDay {
                    font-size: 28px;
                    line-height: 28px;
                    font-family: $font-main-display-small;
                }
                > .eventDateMonth {
                    font-size: 12px;
                    line-height: 24px;
                    text-transform: uppercase;
                    font-family: $font-main-display;
                    font-weight: 400;
                }
            }
            .eventContent {
                display: flex;
                flex-direction: column;
                justify-content: flex-start;
                .eventTitle {
                    font-size: 18px;
                    font-weight: 400;
                    line-height: calc(28px/18px);
                    color: #4A4A4A;
                    font-family: $font-main-display;
                    font-weight: 400;
                }
                .eventInfo {
                    margin: 0;
                    padding: 0;
                    list-style-type: none;
                    color: #867050;
                    font-size: 12px;
                    font-family: $font-main-display;
                    font-weight: 400;

                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    gap: 10px;
                    > li {
                        .cta {
                            padding: 0;
                            min-height: 20px;
                        }
                        &:not(:last-child)::after {
                            content: " ";
                            margin-left: 10px;
                            border-right: 1px solid #DBDBDB;
                            height: 15px;
                        }
                    }

                    @include screen(sm, true) {
                        flex-direction: column;
                        align-items: flex-start;
                        gap: 5px;
                        > li {
                            &::after {
                                display: none;
                            }
                        }
                    }
                }
            }
        }
    }

}