@mixin accordion-arrow($direction: right, $color: white) {
  position: relative;
  display: inline-block;

  &::after,
  &::before {
    content: "";
    display: inline-block;
    width: 10px;
    height: 1.5px;
    background: $color;
    position: absolute;
  }
  @if ($direction == "right") {
    &::after {
      transform: rotate(45deg);
      top: 0;

    }

    &::before {
      transform: rotate(-45deg);
      top: 7px;
    }
  }
  @if ($direction == "left") {
    &::after {
      transform: rotate(-45deg);
      top: 0;

    }

    &::before {
      transform: rotate(45deg);
      top: 7px;
    }
  }
  @if ($direction == "down") {
    &::after {
      transform: rotate(-45deg);
      top: 0;
    }

    &::before {
      transform: rotate(45deg);
      top: 0;
      right: -1px;
    }
  }
}