@use "@/styles/_colors.scss" as *;
@use "@/styles/_mixins.scss" as *;
@use "@/styles/_fonts.scss" as *;

:global(.HistoryBlock){
  position: relative;
  @include responsivePadding();

  .wrapper {
    padding-top: 100px;
    padding-bottom: 100px;
    @include screen(md, true) {
      padding-top: 50px;
      padding-bottom: 50px;
    }

    // @include responsiveWidth();
    .customContainer {
      display: flex;
      width: 100%;
      box-sizing: border-box;
      flex-flow: row nowrap;
      gap: 40px 90px;

      // @media screen and (max-width: 1440px) {
      //   padding: 0 80px;
      // }
      @include screen(md, true) {
        flex-direction: column-reverse;
        padding-bottom: 10px !important;
      }
      // @include screen(sm, true) {
      //   padding: 0 20px;
      // }

      .textContents {
        width: 44%;
        @include screen(md, true) {
          width: 100%;
        }
        > span {
          font-weight: 400;
          font-size: 20px;
          line-height: 24px;
          letter-spacing: 0%;
          color: $pen-light-gold;
          margin-bottom: 10px;
          display: block;

          @include screen(md, true) {
            font-size: 18px;
          }
        }

        .title{
          font-weight: 400;
          font-size: 36px;
          line-height: 44px;
          letter-spacing: 1px;
          // text-transform: uppercase;
          color: $pen-light-gold;
          margin-bottom: 10px;
          @include applyFont($font-main-display-large, $font-cn-large);

          @include screen(md, true) {
            font-size: 24px;
            line-height: 30px;
            letter-spacing: 0%;
            @include applyFont($font-main-display-large, $font-cn-large);
          }
        }

        .desc {
          font-weight: 400;
          letter-spacing: 0%;
          color: #fff;
          text-align: left;
          @include descFontSize();
        }

        .cta{
          border-bottom: 1px solid rgba($pen-light-gold, 0.5);
          padding: 28px 13px 34px;
          >span{
            display: flex;
            flex-flow: row nowrap;
            align-items:center;
            width: 100%;
            .label{
              flex-grow: 1;
              display: flex;
              align-items:center;
              text-transform: none;

              font-weight: 400;
              font-size: 20px;
              line-height: 24px;
              letter-spacing: 0%;
              color: $pen-light-gold;
            }
            :global(.gotoSvg){
              width: 24px;
              display: flex;
              align-items:center;
              svg{
                width: 24px;
                height: 24px;
              }
            }
          }

            &:hover {
              >span{
                margin-right: 0 !important; // overwrite default hover
                :global(.gotoSvg){
                  transform: translateX(5px);
                }
              }
            }

          &:first-of-type {
            border-top: 1px solid $pen-light-gold;
            margin-top: 28px;
            @include screen(md, true) {
              margin-top: 30px;
            }
          }

          

        }
      }

      .imgWrapper {
        width: 54%;
        @include screen(md, true) {
          width: 100%;
        }

        img {
          width: 100%;
          height: auto;
          object-fit: contain;
          display: block;
        }
      }
    }
  }
  :global(.BackToTopBtn){
    position: absolute;
    z-index: 10;
    right: 0;
    bottom: 0;
  }
}
