@use "@/styles/_colors.scss" as *;
@use "@/styles/_mixins.scss" as *;
@use "@/styles/_fonts.scss" as *;

// Main container
:global(.indexFeeds){
  background-color: #fff;
  .wrapper {  
    padding-top: 50px;
    padding-bottom: 80px;

    @include screen(md, true) {
      padding-bottom:50px;
    }

    // @include responsiveWidth( $maxWidth: 800px );
    @include responsivePadding();

    .category{
      text-align: center;

      &:not(:last-child){
        margin-bottom: 100px;
        @include screen(md, true) {
          margin-bottom:50px;
        }
      }
      h2{
        font-family: $font-main-display-large;
        font-weight: 400;
        font-size: 36px;
        line-height: 44px;
        letter-spacing: 1px;
        text-transform: uppercase;
        color: $origin-black;
        margin-bottom: 30px;
        text-align: center;
        @include screen(md, true) {
          font-size: 28px;
          line-height: 28px;
          margin-bottom:20px;
        }
      }
      .list{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 50px 21px;

        @include screen(md, true) {
          display: flex;
          flex-flow: column nowrap;
          gap: 30px;
        }
      }
      .cta{
        margin-top: 60px;
        display: inline-block;
        min-width: auto;
        min-height: auto;
        padding: 15px 20px;
        box-sizing: border-box;
        width: 188px;
        height: 40px;

        @include screen(md, true) {
          margin-top: 50px;
          height: 50px;
        }
        span{
          font-family: $font-main-display;
          font-weight: 400;
          font-size: 14px;
          line-height: 100%;
          letter-spacing: 5%;
          text-align: center;
          text-transform: uppercase;
          display: block;
        }
      }

      & + .category{
        border-top: 1px solid $pen-border-grey;
        padding-top: 69px;
        @include screen(md, true) {
          padding-top: 50px;
        }
      }
    }
  }
}