@use "@/styles/_colors.scss" as *;
@use "@/styles/_mixins.scss" as *;
@use "@/styles/_fonts.scss" as *;

.wrapper {
    background-color: $beige-bg;
}

.title {
    font-weight: 400;
    font-style: Regular;
    font-size: 36px;
    line-height: 44px;
    letter-spacing: 0px;
    text-align: center;
    color: $origin-black;
    margin-bottom: 20px;

    @include applyFont($font-main-display-large, $font-cn-large);

    @include screen(md, true) {
        padding: 0 20px;
        font-size: 28px;
        line-height: 120%;
        letter-spacing: 0%;
    }
}

.descWrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 50px;

    @include screen(md, true) {
        font-size: 13px;
        line-height: 22px;
        letter-spacing: 0%;
    }
}

.desc {
    font-weight: 400;
    text-align: center;
    color: $pen-dark-grey;
    // max-width: 800px;
    padding: 0 320px;

    @include descFontSize();

    @include screen(sl, true) {
        padding: 0 200px;
    }

    @include screen(lg, true) {
        padding: 0 160px;
    }

    @include screen(md, true) {
        padding: 0 20px;
    }
}

.mediaContainer {
    width: 100%;
    box-sizing: border-box;
    padding: 0 185px;

    @include screen(md, true) {
        padding: 0 20px;
    }

    .mediaTitle {
        font-size: 24px;
        font-weight: bold;
        color: $pen-gold-primary;
        margin-bottom: 10px;
    }

    .mediaDesc {
        color: $pen-font-color-primary;
        text-align: center;
        margin-bottom: 20px;

        @include descFontSize();
    }

    // .mediaContent {
    //     box-sizing: border-box;
    //     width: 100%;
    //     padding: 0 185px;
    //     // max-width: 800px;

    //     @include screen(md, true) {
    //         padding: 0 20px;
    //     }

    //     // @include screen(sm) {
    //     //     max-width: 600px;
    //     // }
    // }
}