@use "@/styles/_colors.scss" as *;
@use "@/styles/_mixins.scss" as *;
@use "@/styles/_fonts.scss" as *;

// Main container
:global(.pressKeyHighlights){
  .wrapper {  
    box-sizing: border-box;
    padding: 40px 50px;
    border: 5px solid $pen-border-grey;
    
    width: calc(100% - 100px);
    text-align: center;
    @include responsiveWidth(
      $maxWidth: 900px,
      $margin: 0 auto 50px
    );

    @include screen(md, true) {
      padding: 30px 20px;
      width: calc(100% - 40px);
      margin-bottom: 33px;
    }
    
    h2{
      font-family: $font-main-display-large;
      font-weight: 400;
      font-size: 24px;
      line-height: 28px;
      letter-spacing: 1px;
      text-align: center;
      text-transform: uppercase;
      color: $origin-black;
      margin-bottom: 32px;

      @include screen(md, true) {
        margin-bottom: 19px;
      }
    }

    .desc{
      font-family: $font-main-display;
      font-weight: 400;
      letter-spacing: 0%;
      color: $pen-dark-grey;
      text-align: left;
      @include descFontSize();
      ul,
      ol{
        box-sizing: border-box;
        margin: 0;           /* Vertical spacing */
        padding-left: 20px;      /* Indentation */
        li{
          display: list-item;  
          list-style: inherit;
          &:not(:last-child){
            margin-bottom: 24px;
          }
        }
      }
      ul{
        list-style: disc outside;
      }
      ol{
        list-style: decimal outside;
      }
      p{
        margin: 0;
        &:not(:last-child){
          margin-bottom: 24px;
        }
      }
    }
  }
}