@use '@/styles/_fonts.scss' as *;
@use '@/styles/_mixins.scss' as *;
@use '@/styles/_colors.scss' as *;

.statContainer {
    box-sizing: border-box;
    display: flex;
    width: 100%;
    justify-content: center;
    height: 220px;

    @include screen(md, true) {
        height: 240px;
    }
}

.item {
    width: 300px;
    text-align: center;
    padding-top: 18px;

    &.noP {
        padding-top: 0;
    }

    @include screen(md, true) {
        height: 220px;
    }
}

.mainTitle {
    font-family: $font-main-display-large;
    font-weight: 400;
    font-style: Regular;
    font-size: 18px;
    leading-trim: NONE;
    line-height: 1;
    letter-spacing: 0%;
    text-align: center;
    margin: 0;
    color: rgba(0, 0, 0, 1);

    &:lang(zh-HK),
    &:lang(zh-TW),
    &:lang(zh-Hant) {
        font-family: $font-cn-large;
        font-size: 18px;
        line-height: 30px;
        letter-spacing: 2px;
    }
}

.sub-title {
    font-weight: 400;
    font-style: Regular;
    font-size: 12px;
    leading-trim: NONE;
    line-height: 1;
    letter-spacing: 0%;
    text-align: center;
    margin: 0;
    margin-top: 5px;
    color: rgba(74, 74, 74, 1);
}

.line {
    width: 1px;
    height: 100%;
    background-color: rgba(219, 219, 219, 1);
}

.ctaContainer {
    display: flex;
    justify-content: center;
    margin-top: 35px;
}

.cta {
    display: inline-block;
    box-sizing: border-box;
    text-align: center;
    padding: 15px 20px;
    min-width: 190px;
    min-height: auto;
    color: $pen-gold;

    &:lang(zh-HK),
    &:lang(zh-TW),  
    &:lang(zh-Hant) {
        font-size: 16px;
        line-height: 100%;
        letter-spacing: 1.3px;
        font-family: $font-cn;
    }
}

.sliderWrapper {
    width: 100%;
    display: flex;
    justify-content: center;
}

.slider {
    width: 100%;
}

.slickContainer {
    justify-content: center;
}

.slickRow {
    display: flex;
    width: 100%;
    justify-content: center;
}