@use "@/styles/_colors.scss" as *;
@use "@/styles/_mixins.scss" as *;
@use "@/styles/_fonts.scss" as *;

// Main container
:global(.historyList){
  background-color: $background-grey2;

  @mixin title-style {
    &{
      font-family: $font-main-display-small;
      font-weight: 400;
      font-style: Display;
      font-size: 36px;
      line-height: 100%;
      letter-spacing: 0%;
      text-transform: uppercase;
      padding-bottom: 65px;
      color: $pen-gold;
      text-align: center;
      position: relative;
    }

    @include screen(md, true) {
      font-size: 32px;
    }

    &::after {
      display: block;
      content: "";
      position: absolute;
      width: 1px;
      height: 60px;
      left: calc(50% - .5px);
      bottom: 0;
      background-color: $pen-gold;;
    }
  }
  .wrapper {  
    padding-top: 65px;
    padding-bottom: 77px;

    @include screen(md, true) {
      padding-top: 40px;
      padding-bottom: 24px;
    }

    // @include responsiveWidth();
    @include responsivePadding();

    .tablist{
      margin-bottom: 60px;
      @include screen(md, true) {
        margin-bottom: 50px;
      }
      
      
      &{
        width: 100%;
      }
      @include screen(lg, true) {
        margin-left: -45px;
        margin-right: -45px;
        width: calc(100% + 90px);
      }
      @include screen(sm, true) {
        margin-left: -20px;
        margin-right: -20px;
        width: calc(100% + 40px);
      }
      >div{
        padding: 0;
        // gap: 55px;
        // width: auto;
        // justify-content: center;
        
        // @include screen(md, true) {
        //   width: 100%;
        //   gap: 30px;
        //   justify-content:space-between;
        // }
        width: 100%;
        gap: 30px;
        justify-content:space-between;

        >:global(.tab){
          flex-grow: 0;
          max-width: 120px;

          font-weight: 400;
          // font-size: 16px;
          font-size: clamp(14px, 1vw, 16px);
          line-height: 24px;
          letter-spacing: 0%;
          text-transform: uppercase; 
          white-space: nowrap;

          @include screen(md, true) {
            flex-grow: 1;
            font-size: 15px;
          }
        }

        >:global(.scrollBtn){
          height: 100%;
          top:0;
          background-color: $background-grey2;
        }
      }
    }

    >h2{
      margin-bottom: 16px;

      @include screen(md, true) {
        margin-bottom: 20px;
      }
      @include title-style;
    }

    .timeline{
      .contentBlock{
        padding: 50px;
        background-color: #fff;
        display: flex;
        flex-flow: column nowrap;
        gap: 30px;
        img{
          display: block;
          height: auto;
          width: 100%;
        }
        @include screen(md, true) {
          padding: 24px;
          gap: 20px;
        }
        &{
          .textBox{
            display: flex;
            gap: 10px;
            flex-flow: column nowrap;
            @include screen(md, true){
              gap: 0;
            }
            .year{
              font-family: $font-main-display-small;
              font-weight: 400;
              font-style: italic;
              font-size: 44px;
              line-height: 65px;
              letter-spacing: 0%;
              text-transform: uppercase;
              color: $pen-gold;

              @include screen(md, true){
                font-size: 32px;
                line-height: 100%;
              }
            }
            :global(.richText){
              font-weight: 400;
              font-style: Book;
              font-size: 16px;
              line-height: 24px;
              letter-spacing: 0%;
              color: $pen-dark-grey;
              text-align: left;
            }
          }
        }

        &.srOnly{
          @include screen(md) {
            @include sr-only;
          }
        }

        &.itemFull{
          flex-flow: row nowrap;
          gap: 51px;
          align-items: center;

          img{
            width: calc(60% - 25px);
          }
          >div{
            width: calc(40% - 25px);
          }
          @include screen(md, true) {
            flex-direction: column;
            gap: 20px;
            img,
            >div{
              width: 100%;
            }
          }
        }
      }

      .twoSide{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 50px;

        >div{
          display: flex;
          flex-flow: column nowrap;
          gap: 85px;
        }

        &:first-child{
          .right{
            padding-top: 50px;
          } 
        }

        @include screen(md, true) {
          grid-template-columns: repeat(1, 1fr);
          .left{
            gap: 20px;
          }
          .right{
            display: none;
          }
        }
      }

      >div:not(:last-child){
        margin-bottom: 60px;
        @include screen(md, true) {
          margin-bottom: 20px;
        }
      }
    }

    .nextItemBtn{
      @include title-style;
      &{
        margin-top: 80px;
        cursor: pointer;
        @include screen(md, true) {
          margin-top: 40px;
        }
      }
    }


    

