.ReactModal__Body--open {
    overflow-y: hidden;

    // .ReactModalPortal {
    //     display: block;
    // }

    .modal-overlay {
        height: 100%;
    }

    .ReactModal__Content {
        height: 100%;
    }

    .GP_MANAGE {
        .modal-img-block {
            display: none;
        }

        .modal-content {
            background-color: transparent;
        }
    }

    .modal-content {
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: flex-start;
        background-color: white;

        @include screen(md, true) {
            align-items: flex-end;
            background-color: rgba(0, 0, 0, 0.35);
        }

        .overlay {
            position: fixed;
            left: 0;
            top: 0;
            background-color: black;
            opacity: 0.65;
            width: 100%;
            height: 100%;
        }

        .modal-img-block {
            // flex: 1;
            // margin: auto;
            // position: fixed;
            display: none;

            // @include screen(md, true) {
            //     display: none;
            // }

            // div,
            // img {
            //     width: 100%;
            //     height: 100%;
            // }
        }

        .modal-content-block {
            width: 47.5%;
            position: fixed;
            z-index: 10;
            background-color: white;
            height: calc(100% - 80px); // header height
            top: 80px;
            right: 0;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            padding: 0 50px;
            box-sizing: border-box;

            @media screen and (max-width: 1290px) {
                height: calc(100% - 60px); // header collaspe height
                top: 60px;
            }

            @include screen(md, true) {
                width: 100%;
                height: 70%;
                top: auto;
                padding: 50px 15px 40px;
                bottom: 0;
            }

            .modal-content-inner {
                width: 100%;
                height: 100%;
                padding: 50px 0;
                box-sizing: border-box;
                overflow-y: auto;

                @include screen(md, true) {
                    padding: 0;
                    padding-bottom: 20px;
                }
            }

            .title-container {
                display: flex;
                justify-content: space-between;
                margin-bottom: 8px;

                .title {
                    font-weight: 400;
                    font-size: 24px;
                    line-height: 44px;
                    letter-spacing: 0%;
                    text-transform: uppercase;
                    @include applyFont($font-main-display-large, $font-cn-large);

                    @include screen(md, true) {
                        padding-right: 40px;
                        @include applyFont($font-main-display-large, $font-cn-large);
                    }
                }

                .close-button {
                    line-height: 0;
                    margin-top: 12px;
                    margin-bottom: auto;
                    z-index: 10;

                    @include screen(md, true) {
                        position: absolute;
                        top: 40px;
                        right: 30px;
                    }
                }
            }

            .subTitle {
                font-weight: 400;
                font-size: 15px;
                line-height: 28px;
                letter-spacing: 0%;
                text-transform: uppercase;
                margin-bottom: 12px;
            }

            .subTitle~.subTitle {
                margin-top: 17px;
            }

            ul {
                width: 95%;
                display: flex;
                flex-direction: column;
                gap: 5px 0;

                li {
                    list-style: disc;
                    position: relative;
                    font-weight: 400;
                    font-size: 18px;
                    left: 20px;
                    line-height: 28px;
                    letter-spacing: 0%;
                    color: $pen-dark-grey;

                    @include screen(md, true){
                        font-size: 16px;
                    }
                }
            }

            .pdf-cta {
                display: inline-block;
                margin-top: 30px;
                margin-bottom: 50px;
            }
        }
    }
}