@use "./_mixins.scss" as *;
@use "./_colors.scss" as *;

$dot-size: 16px;
$overlay-height: 30px;
$slideBodyBottomP: 104px;

// page-wrapper is the parent class given by hsh groupSite
.page-wrapper .slick-dots {
    box-sizing: border-box;
    // overwrite default(absolute)
    position: relative;
    z-index: 2;

    li {
        width: 20px;
        height: 20px;
        margin: 0;
        padding: 0;
        cursor: pointer;
        transition: width .2s cubic-bezier(.57, .37, .71, .68);

        button {
            width: 20px;
            height: 20px;
            border-radius: 50%;
            position: relative;

            &::before {
                // reset the dot content to none
                content: "";
                // force opacity to 1 no matter focus or not
                opacity: 1;
                width: 4px;
                height: 4px;
                border-radius: 50%;
                background-color: $pen-slick-grey;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
            }
        }
    }

    li.slick-active {
        width: 60px;
        margin: 0 8px;

        button {
            width: 100%;

            &::before {
                border-radius: 15%;
                width: 100%;
                height: 2px;
                // margin: 0 16px;
                background-color: $pen-light-gold;
            }
        }
    }
}

.TopBanner {
    .slick-dots {
        bottom: 0;
        width: auto;

        li {
            button {
                padding: 2px;

                &::before {
                    background-color: $banner-inactive-line;
                }
            }
        }

        li.slick-active {
            button {
                width: 100%;

                &::before {
                    background-color: $origin-white;
                }
            }
        }
    }

    .dots-overlay-container {
        background-color: rgba(0, 0, 0, .1);
        backdrop-filter: blur(15px);
        -webkit-backdrop-filter: blur(15px);
        height: 30px;
        border-radius: 30px;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 5px 10px;
        box-sizing: border-box;
    }
}

.home-stat {

    .y-standard {
        padding: 100px 0;
    }

    .slick-dots {
        bottom: 0;
        width: auto;

    }
}

.StoryCarousel {
    .slider-arrow {
        @include screen(md, true) {
            display: none;
        }
    }
}