@use '../_mixins.scss' as *;
@use '../_colors.scss' as *;

.hsh-redirect-btn {
    display: flex;
    border: 1px solid rgba(219, 219, 219, 1);
    justify-content: center;
    align-items: center;
    cursor: pointer;

    &:hover {
        border-color: $pen-light-gold;
    }

    >a {
        padding: 16px 8px;
    }

    @include screen(md, true) {

        >a {
            display: flex;
            gap: 5px;

            >svg {
                flex-shrink: 0;
                align-self: center;
            }
        }

        .span-normal {
            text-align: center;
        }
    }
}