@use "../_fonts.scss" as *;
@use "../_colors.scss" as *;
@use "../_mixins.scss" as *;

$mb-for-card-cta: 30px;

.hsh-threeTiles {

    .headline {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        box-sizing: border-box;

        @include screen(md, true) {
            padding: 0 20px;
        }
    }

    .headline-center {
        display: flex;
        align-items: center;
        flex-direction: column;
        margin-bottom: 30px;
        // max-width: 800px;
        padding: 0 320px;
        box-sizing: border-box;

        @include screen(sl, true) {
            padding: 0 200px;
        }

        @include screen(lg, true) {
            padding: 0 160px;
        }

        @include screen(md, true) {
            padding: 0 20px;
        }
    }

    .headline-primary {
        font-weight: 400;
        letter-spacing: 1px;
        text-align: center;
        text-transform: uppercase;
        color: $origin-black;

        span {
            font-family: $font-main-display-small;
            font-size: 40px;
            line-height: 44px;
            letter-spacing: 1px;
            text-align: center;
            margin-left: 10px;
        }

        @include screen(md, true) {
            font-size: 28px;
            line-height: 28px;
            letter-spacing: 0%;

            span {
                font-size: 28px;
                line-height: 28px;
                letter-spacing: 0%;
            }
        }
    }

    .headline-secondary {
        font-family: $font-main-display;
        font-size: 18px;
        line-height: 28px;
        letter-spacing: 0%;
        text-align: center;
        color: $pen-dark-grey;
        margin-top: 20px;

        &:lang(zh-HK),
        &:lang(zh-TW),
        &:lang(zh-Hant) {
            font-family: $font-cn;
        }

        @include screen(md, true) {
            font-size: 16px;
            line-height: 24px;

            &:lang(zh-HK),
            &:lang(zh-TW),
            &:lang(zh-Hant) {
                font-family: $font-cn;
            }
        }
    }

    .threeTiles-cards {
        padding: 0 160px;
        padding-top: 80px;
        box-sizing: border-box;
        position: relative;
        margin-bottom: $mb-for-card-cta;

        @include screen(xl, true) {
            padding: 0 80px;
            padding-top: 80px;
        }

        @include screen(sl, true) {
            padding: 0 60px;
            padding-top: 60px;
        }

        @include screen(md, true) {
            padding: 0 50px;
            padding-top: 50px;
            margin-bottom: 0;
        }

        &.no-py {
            padding-top: 0;
            padding-bottom: 0;
        }
    }

    .threeTiles-row {
        display: flex;
        gap: 20px;
        width: 100%;

        @include screen(md, true) {
            flex-direction: column;
            gap: 30px;
        }
    }

    .span-normal {
        font-size: 14px;
        text-transform: uppercase;
        letter-spacing: 1px;
        text-align: center;

        &:lang(zh-HK),
        &:lang(zh-TW),
        &:lang(zh-Hant) {
            font-family: $font-cn;
        }
    }

    .cardArticle {
        flex: 1;
    }

    .cardArticle-title {
        font-size: 22px;
        line-height: 30px;
        vertical-align: bottom;
        font-weight: 400;
        margin-bottom: 5px;

        &:lang(zh-HK),
        &:lang(zh-TW),
        &:lang(zh-Hant) {
            line-height: 28px;
            letter-spacing: 2px;
        }

        @include screen(md, true) {
            font-family: $font-main-display;
            font-size: 18px;

            &:lang(zh-HK),
            &:lang(zh-TW),
            &:lang(zh-Hant) {
                font-family: $font-cn;
            }
        }
    }

    .cardArticle-desc {
        letter-spacing: 0%;
        color: $pen-dark-grey;

        @include descFontSize();

        &:lang(zh-HK),
        &:lang(zh-TW),
        &:lang(zh-Hant) {
            font-size: 16px;
            line-height: 24px;
        }
    }

    .cardArticle-cta {
        // resume independant
        margin-top: 20px;
        font-size: 14px;
        line-height: 100%;
        letter-spacing: 0.6px;
        text-transform: uppercase;

        &:lang(zh-HK),
        &:lang(zh-TW),
        &:lang(zh-Hant) {
            font-size: 16px;
            line-height: 24px;
            letter-spacing: 0.13em;
        }

        @include screen(md) {
            // make all cta in same line, take (.threeTiles-cards) as ref
            position: absolute;
            bottom: -$mb-for-card-cta;
        }

        &:hover {
            .linkCTA-arrow {
                margin-left: 16px;
                transition: margin-left .3s;
            }
        }
    }

    .cardArticle>.imageWrapper {
        width: 100%;
        height: auto;
        aspect-ratio: 415/519;
        margin-bottom: 20px;
        line-height: 0;
    }

    .cardArticle>.imageWrapper>img {
        width: 100%;
        height: 100%;
    }

    .linkCTA-arrow {
        margin-left: 10px;
    }

    .hsh-redirect-btn {
        display: flex;
        border: 1px solid rgba(219, 219, 219, 1);
        justify-content: center;
        align-items: center;

        >a {
            padding: 16px 20px;
        }

        @include screen(md, true) {
            margin: 0 25px;

            >a {
                display: flex;
                line-height: 100%;
                padding: 12px 40px;
                gap: 5px;

                >svg {
                    flex-shrink: 0;
                    align-self: center;
                }
            }
        }
    }
}

.hsh-threeTiles.whatsOn {
    .cardArticle-desc {
        letter-spacing: 0%;
        color: $pen-dark-grey;

        @include descFontSize();

        &:lang(zh-HK),
        &:lang(zh-TW),
        &:lang(zh-Hant) {
            font-size: 16px;
            line-height: 24px;
        }
    }
}