@use '@/styles/_mixins.scss' as *;
@use '@/styles/_colors.scss' as *;

.listContainer {
    display: flex;
    flex-direction: column;
    gap: 1px 0;
}

.row {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}

.name {
    font-size: 18px;
    line-height: 28px;
    color: $pen-dark-grey;

    @include screen(sm, true) {
        font-size: 16px;
        line-height: 24px;
        letter-spacing: 0%;
    }
}

.ctaContainer {
    display: flex;
}

.cta {
    min-width: auto;
    min-height: auto;
    padding: 0;
    color: $pen-gold;

    >span {
        white-space: nowrap;
        line-height: 100%;
    }

    :global(.cta-arrow) {
        >svg {
            transform: translateY(1px) !important;
        }
    }
}