@use "@/styles/_colors.scss" as *;
@use "@/styles/_mixins.scss" as *;
@use "@/styles/_fonts.scss" as *;

// Main container
:global(.pressTable){
  .wrapper {  
    box-sizing: border-box;
    border: 5px solid $pen-border-grey;
    padding: 50px;
    margin-bottom: 58px;

    width: calc(100% - 100px); 
    max-width: 800px;
    text-align: left;

    @include screen(md, true) {
      width: 100%; 
      border: 0;
      padding: 0;
      margin: 0 0 70px;
      overflow-x: visible;
    }
    @include responsiveWidth(
      $maxWidth: 800px,
      $margin: 0 auto 58px
    );
    
    h2{
      font-family: $font-main-display-large;
      font-weight: 400;
      font-size: 24px;
      line-height: 28px;
      letter-spacing: 0px;
      text-align: center;
      text-transform: uppercase;
      color: $origin-black;
      text-align: center;
      margin-bottom: 20px;
    }
    div{
      width: 100%;
      overflow-x: auto;
      box-sizing: border-box;
      @include custom-scrollbar;
      @include screen(md, true) {
        padding: 0 20px;
      }
      $head-border-style: 1px solid $pen-light-grey;
      table{
        width: 100%;
        border-spacing: 0;
        tr{
          th{
            border-bottom: $head-border-style;
            padding: 0;
            font-family: $font-main-display;
            font-weight: 400;
            font-size: 16px;
            line-height: 24px;
            letter-spacing: 0%;
            color: $origin-black
          }
          td{
            &.catHead{
              padding: 19px 0 10px;
              width: 100%;
              font-family: $font-main-display;
              font-weight: 400;
              font-size: 16px;
              line-height: 24px;
              letter-spacing: 0%;
              color: $origin-black;
              border-bottom: $head-border-style;
            }

            &:not(.catHead){
              font-family: $font-main-display;
              font-weight: 400;
              font-size: 14px;
              line-height: 50px;
              letter-spacing: 0%;
              color: $origin-black;
              border-bottom: 1px solid $pen-border-grey;
            }
          }
          th,
          td{
            &:first-child{
              width: 100%;
              text-align: left;
              @include screen(md, true) {
                min-width: 160px;
              }
            }
            &:not(:first-child){
              width: 11%;
              min-width: 100px;
              padding-left: 40px;
              text-align: right;
              @include screen(md, true) {
                padding-left: 20px;
              }
            }
          }
        }
      }
    }
  }
}