@use "@/styles/_colors.scss" as *;
@use "@/styles/_mixins.scss" as *;
@use "@/styles/_fonts.scss" as *;

// Main container
.blockContainer {
  width: 100%;
  box-sizing: border-box;
  padding: 0 80px;

  @include screen(sm, true) {
    padding: 0 20px;
  }

  // Each comment set
  .row {
    display: flex;
    align-items: center;
    margin-bottom: 75px;
    gap: 0 80px;

    // Even sets (image left, text right)
    &.even {
      .image {
        order: 1;
      }

      .text {
        order: 2;
      }
    }

    // Odd sets (image right, text left)
    &.odd {
      .image {
        order: 2;
      }

      .text {
        order: 1;
      }
    }
  }

  :nth-last-child(1 of .row) {
    margin-bottom: 0 !important;
  }

  // Image styling
  .image {
    flex: 1;
    line-height: 0;

    @include screen(md, true) {
      max-width: none;
      width: 100%;
      margin-bottom: 20px;
    }

    img {
      width: 100%;
      height: auto;
    }
  }

  // Text content styling
  .text {
    width: 37vw;
    // max-width: 550px;
    box-sizing: border-box;

    @include screen(md, true) {
      width: 100%;
      max-width: none;
      padding: 0;
    }

    .title {
      font-weight: 400;
      font-size: 24px;
      line-height: 28px;
      letter-spacing: 1px;
      text-transform: uppercase;
      margin-bottom: 20px;

      @include applyFont($font-main-display-large, $font-cn-large);
    }
  }

  .desc {
    font-weight: 400;
    font-style: Book;
    letter-spacing: 0%;
    color: $pen-dark-grey;
    text-align: start;

    @include descFontSize();

    @include screen(md, true) {
      margin-bottom: 20px;
    }

    svg {
      flex-shrink: 0;
      position: relative;
      top: -0.35em;
    }

    p {
      margin: 0;
      display: inline;
    }
  }

  .gusetName {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
  }

  .jobTitle {
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    color: $pen-mid-grey;
    // text-decoration: underline;
    // text-decoration-style: solid;
    // text-decoration-offset: 0%;
    // text-decoration-thickness: 0%;
  }

  .cta {
    display: inline-block;
    margin-top: 20px;
    min-width: auto;
    color: $pen-gold;
  }

  // Mobile responsiveness
  @include screen(md, true) {
    .row {
      flex-direction: column;
      margin-bottom: 50px;

      &.even,
      &.odd {

        .image,
        .text {
          order: initial;
        }
      }
    }
  }
}