@use "@/styles/_colors.scss" as *;
@use "@/styles/_mixins.scss" as *;
@use "@/styles/_fonts.scss" as *;

// Main container
.wrapper {  
  background-color: $background-dark;
  .wrapperBody {
    padding-top: 70px;
    padding-bottom: 70px;

    text-align: center;

    // @include responsiveWidth( $maxWidth: 700px );
    @include responsivePadding();

    @include screen(md, true) {
      padding: 50px 46px;
    }
  }
  h2{
    font-family: $font-main-display-large;
    font-weight: 400;
    font-size: 36px;
    line-height: 44px;
    letter-spacing: 1px;
    text-align: center;
    text-transform: uppercase;

    color: $pen-light-gold;
    margin-bottom: 20px;

    @include screen(md, true) {
      font-size: 24px;
      line-height: 30px;
      letter-spacing: 0%;
    }
  }
  .line{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
    padding: 0px;
    gap: 5px;
    font-family: $font-main-display;
    font-weight: 400;
    font-size: 16px;
    letter-spacing: 0%;
    line-height: 28px;
    text-align: center;
    color: #fff;

    :global(.richText ) {
      font-family: $font-main-display;
      color: #fff;
      font-size: 16px;
      text-align: center;
      :global(a) {
        color: $pen-light-gold;
      }
    }

  }
}
