@use "@/styles/_colors.scss" as *;
@use "@/styles/_mixins.scss" as *;
@use "@/styles/_fonts.scss" as *;

// Main container
:global(.pressContactPerson){
  background-color: $background-grey2;

  .wrapper {
    box-sizing: border-box;
    padding-top: 50px;
    padding-bottom: 50px;

    @include screen(md, true) {
      padding-top: 40px;
    }

    // @include responsiveWidth();
    @include responsivePadding();

    h2{
      font-family: $font-main-display;
      font-weight: 400;
      font-size: 16px;
      line-height: 24px;
      letter-spacing: 0%;
      text-align: center;
      text-transform: capitalize;
      color: $pen-dark-grey;
      margin-bottom: 30px;
      text-align: center;
      @include screen(md, true) {
        text-align: left;
      }
    }
    .people{
      display: flex;
      flex-flow: row nowrap;
      justify-content: center;
      gap: 9vw;
      margin-bottom: 35px;
      align-content: space-between;

      @include screen(md, true) {
        flex-direction: column;
        gap: 60px;
        margin-bottom: 30px;
      }
      >.person{
        display: flex;
        flex-flow: column nowrap;

        width: 33%;
        max-width: 273px;
        position: relative;
        &:after{
          content: "";
          display: block;
          position: absolute;
          height: 100%;
          width: 1px;
          top: 0;
          right: -4.5vw;
          background-color: $pen-border-grey;
        }
        &:last-child:after{
          display: none;
        }
        @include screen(md, true) {
          width: 100%;
          max-width:none;
          &:after{
            height: 1px;
            width: 100%;
            top: auto;
            bottom: -30px;
            right: auto;
          }
          &:last-child{
            padding-bottom: 30px;
            &:after{
              display: block;
              bottom: 0;
            }
          }
        }

        .name{
          font-weight: 400;
          font-style: Book;
          font-size: 18px;
          line-height: 24px;
          letter-spacing: 0%;
          color: $origin-black;
          margin-bottom: 22px;
        }
        .position{
          font-family: $font-main-display;
          font-weight: 400;
          font-size: 14px;
          line-height: 22px;
          letter-spacing: 0%;
          // text-decoration: underline;
          // text-decoration-style: solid;
          // text-decoration-offset: 0%;
          // text-decoration-thickness: 0%;
          color: $pen-dark-grey;
          margin: -22px 0 22px;
        }
        .tel,
        .email{
          font-family: $font-main-display;
          font-weight: 400;
          font-size: 16px;
          line-height: 24px;
          letter-spacing: 0%;

          color: $pen-dark-grey;
        }
        .email{
          a{
            overflow-wrap: anywhere;
          }
        }
      }
    }

    .websites{
      display: flex;
      flex-flow: row wrap;
      justify-content: center;
      gap: 0 20px;

      font-family: $font-main-display;
      font-weight: 400;
      font-size: 16px;
      line-height: 22px;
      letter-spacing: 0%;
      text-align: center;

      >span{
        display: block;
        width: 100%;
        text-align: center;
        color: $pen-dark-grey;
      }
      a{ width: auto;}

      @include screen(md, true) {
        flex-direction: column;
        a{
          width: 100%;
        }
      }
    }
  }
}