@use "@/styles/_colors.scss" as *;
@use "@/styles/_mixins.scss" as *;

.styledSelect{
  padding: 13px 20px !important;
  border: 1px solid #{$pen-border-grey} !important;
  min-width: 190px;
  border-radius: 0 !important;
  // transition: box-shadow .2s;

  &:focus,
  &:focus-within{
    box-shadow: none !important; /* Prevents shadow-based growth */
    // box-shadow: 0 0 0 3px rgba($pen-gold, 0.2) !important;
  }
  &:focus-visible{
    outline: none;
  }
  :global(.react-dropdown-select-content){
    display: flex;
    align-items: center;
    >span{
      font-family: Futura Std;
      font-weight: 400;
      font-style: Book;
      font-size: 15px;
      leading-trim: CAP_HEIGHT;
      line-height: 21px;
      letter-spacing: 0%;
      text-transform: capitalize;
      color: $pen-dark-grey;
      padding-right: 10px;
    }
    input{
      display: none;
    }
  }
  :global(.react-dropdown-select-dropdown-handle){
    display: flex;
    justify-content: center;
    align-items: center;
    width: 24px;
    height: 24px;
    margin: 0;
    >svg{
      transform: rotate(180deg);
    }
  }
  :global(.react-dropdown-select-dropdown){
    max-height: 513px;
    box-shadow:none;
    border-color: $pen-border-grey;
    transform: translateX(1px); //workaround
    @include custom-scrollbar;
    :global(.react-dropdown-select-item){
      transition: background .2s, color .2s;
      padding: 11px 19px;
      font-family: Futura Std;
      font-weight: 400;
      font-style: Book;
      font-size: 14px;
      leading-trim: CAP_HEIGHT;
      line-height: 100%;
      letter-spacing: 0%;
      text-transform: capitalize;
      color: $pen-dark-grey;

      &:hover{
        background-color: rgba($pen-gold, .2);
      }

      &:global(.react-dropdown-select-item-selected){
        background-color: $pen-gold;
        color: #fff
      }
    }
  }

  @include screen(xs,true){
    min-width: auto;
  }
}