@use "@/styles/_colors.scss" as *;
@use "@/styles/_typography.scss" as *;
@use "@/styles/_variables.scss" as *;
@use "@/styles/_mixins.scss" as *;
@use "@/styles/_fonts.scss" as *;

// HSH-129, request by comment
$cta-font-size: 14px;


.buttonPrimary {
    background-color: $pen-gold;
    color: $origin-white;
    border: 1px solid $pen-light-gold;
    font-size: $cta-font-size;
    transition: $animation-hover-speed all;

    &:hover {
        background-color: $origin-white;
        color: $pen-dark-gold;
    }
}

.buttonSecondary {
    background-color: $origin-white;
    color: $pen-gold;
    border: 1px solid $pen-border-grey;
    font-size: $cta-font-size;
    transition: $animation-hover-speed all;

    &:hover {
        border: 1px solid $pen-light-gold;
    }
}

.linkCtaBase:not(.buttonPrimary):not(.buttonSecondary) {
    &:hover {
        text-decoration: underline;
    }
}

.linkCtaBase:not(.buttonPrimary):not(.buttonSecondary):has(.noArrow) {
    &:hover {
        text-decoration: unset;
        .noArrow{
            margin-right: 5px;
        }
    }
}


.buttonPrimary,
.buttonSecondary {
    @include applyFont($font-main-display, $font-cn);
}

.linkCtaBase {
    @include type-button;
    @include animation-arrow-cta-link;
    cursor: pointer;
    display: flex;
    align-items: center;

    .iconRight,
    .iconArrowRight,
    .iconArrowLeft {
        // @include create-icon(arrow-right);
        line-height: 1;

        &::before {
            /* stylelint-disable-next-line */
            color: inherit;
        }

        display: inline-block;
        vertical-align: middle;
    }

    .iconRight,
    .iconArrowRight {
        :global(svg) {
            margin-left: 10px;
        }
    }

    .iconArrowRight {
        :global(svg) {
            margin-left: 10px;
        }
    }

    .iconArrowLeft {
        // @include create-icon(arrow-left);

        vertical-align: bottom;
    }

    .linkCtaLabel {
        display: inline-block;
        margin-right: 0;

        &:not(.noArrow) {
            margin-right: 1rem;
        }
    }
}

.linkCtaButton {
    display: inline-block;
}

.linkCtaSmall {
    font-size: 1.1rem;
}

.linkCtaLeft {
    .linkCtaLabel {
        margin-right: 0;
        margin-left: 1rem;
    }
}

// left aligned language
[dir*=rtl] {
    .linkCtaBase {
        .iconArrowRight {
            /* stylelint-disable-next-line */
            // @include create-icon(arrow-left);
        }

        .iconArrowLeft {
            /* stylelint-disable-next-line */
            // @include create-icon(arrow-right);
        }

        .linkCtaLabel {
            margin-right: 0;
            margin-left: 1rem;
        }

        &Left {
            .linkCtaLabel {
                margin-left: 0;
                margin-right: 1rem;
            }
        }
    }
}