@use '@/styles/_colors.scss' as *;

.videoPlayBtnContainer {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 3;
}

.videoPlayBtn {
    width: 80px;
    height: 80px;
    border: 1px solid $origin-white;
    background-color: rgba(0, 0, 0, 0.6);
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    
}