@use '@/styles/colors' as *;
@use '@/styles/mixins' as *;
@use '@/styles/fonts' as *;
@use '@/styles/arrow-settings' as arrowSettings;

.searchContainer {
  margin-top: 82px;
  position: fixed;
  inset: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.8);
  z-index: 1500;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  transform: translateY(-40px);
  max-height: 0;
  animation: searchDropDown 0.4s cubic-bezier(0.4, 0, 0.2, 1) forwards;
}

@keyframes searchDropDown {
  to {
    opacity: 1;
    max-height: 100%;
    transform: translateY(0);
    margin-bottom: 0;
  }
}

.searchContainer.show {
  // max-height: 100%;
  opacity: 1;
}

.searchHeader {
  background-color: $f8-grey;
  // background-color: $origin-white;
  height: 82px;
}

.searchInputWrapper {
  width: 100%;
  padding: 2rem 0 1rem 0;
  background: transparent;
  display: flex;
  justify-content: center;
  margin-left: 34px;
  align-items: center;
}

.searchInput {
  // background: url(../images/search-header-icon.png) 7px center no-repeat;
  background-size: 22px auto;
  font-family: "Futura LT W01 Book", Helvetica, Arial, sans-serif;
  font-size: 24px;
  width: 100%;
  height: 37px;
  border: 0;
  background-color: transparent;
  margin-left: 10px;
}

.searchInput:lang(en) {
  font-family: "Futura LT W01 Book", Helvetica, Arial, sans-serif;
}

.searchInput:lang(zh) {
  font-family: "Times New Roman, MingLiu, LiSong Pro", serif !important;
}


.searchInput:focus {
  outline: none;
}

.searchButton {
  margin-left: 1rem;
  padding: 0.75rem 1.5rem;
  font-size: 1.2rem;
  border: none;
  border-radius: 8px;
  color: $origin-white;
  cursor: pointer;
  // transition: background 0.2s;
}

.closeBtn {
  position: absolute;
  top: 24px;
  right: 32px;
  background: none;
  border: none;
  font-size: 30px;
  cursor: pointer;
  color: $origin-black;
  z-index: 2;
}

.pageBtn {
  background: none;
  border: none;
  padding: 0.5rem 0.8rem;
  margin: 0 2px;
  font-size: 18px;
  cursor: pointer;
  color: $ccc-grey;
}

.pageBtn:hover {
  color: $pen-gold;
}

.activePageBtn {
  background: none;
  border: none;
  padding: 0.5rem 0.8rem;
  margin: 0 2px;
  font-size: 18px;
  cursor: pointer;
  color: $pen-gold;
}

.ellipsis {
  padding: 0 6px;
  color: #888;
  font-size: 1rem;
}

.searchResults {
  background-color: $origin-white;
}

.arrowRight {
  div {
    display: flex;
  }
}

.arrowleft {
  div {
    display: flex;
  }
}

.searchResultsWapper {
  overflow: auto;
  background-color: $origin-white;

  // height: calc(100% - 164px); // Adjust height to account for header
}

.searchResultsContainer {
  width: 970px;
  margin: 0px auto;
  color: $origin-black;
  padding-bottom: 100px;

  li {
    padding: 40px 30px;
    border-bottom: 1px solid $pen-border-grey;

    h4 {
      text-transform: none;
      text-align: left;
      font-size: 20px;
      letter-spacing: 1px;
      margin-bottom: 20px;
      font-weight: 400;
    }

    .captionSearchRresult {
      color: $origin-black;
      margin-bottom: 0;
      font-size: 16px;
    }

    &:last-child {
      border-bottom: none;
    }
  }
}

.pagination {
  text-align: center;
  margin-top: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
}

.noResults {
  text-align: center;
  padding: 92px 0;

  h3 {
    font-size: 24px;
    text-transform: none;
    letter-spacing: 2px;
    margin-bottom: 30px;
    text-align: center;
  }

  p {
    text-align: center;
    margin-bottom: 0;
    font-size: 16px;
  }
}

@include screen(mw, true) {
  .searchContainer {
    margin-top: 60px;
  }

  .searchResultsContainer {
    width: 80%;
  }

}

@include screen(sm, true) {
  .searchResultsContainer {
    width: 100%;
  }
}