@use "@/styles/_colors.scss" as *;
@use "@/styles/_mixins.scss" as *;
@use "@/styles/_fonts.scss" as *;




.wrapper {  
    max-width: 800px;
    margin: 0 auto;
    margin-top: 20px;
    margin-bottom: 20px;


    .title {
      @include title-styles($font-main-display-large, $font-cn-large);
    }

    .slide {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 0 20px;
        text-align: center;

        .slideTitle {

            font-weight: 400;
            letter-spacing: 10%;
            text-transform: uppercase;
            text-align: center;
            margin-bottom: 20px;
            font-family: $font-main-display-large;
            color: #867050;
            font-size: 24px;

            @include screen(sm,true) {
                font-size: 18px;
                line-height: 22px;
            }
        }


        .slideChart {
            width: 100%;
            height: 480px;
            @include screen(sm,true) {
                height: 360px;
            }
        }
    }
}