@use "@/styles/_colors.scss" as *;
@use "@/styles/_mixins.scss" as *;
@use "@/styles/_fonts.scss" as *;

// Main container
:global(.boardList) {
  background-color: $background-grey2;

  .wrapper {
    padding-top: 40px;
    padding-bottom: 83px;

    // @include responsiveWidth();
    @include responsivePadding();

    .tablist {
      margin-bottom: 74px;
      margin-left: -20px;
      margin-right: -20px;
      width: calc(100% + 40px);

      @include screen(md, true) {
        margin-bottom: 48px;
      }

      >div {
        padding: 0;
        gap: 50px;
        width: auto;
        justify-content: center;

        @include screen(md, true) {
          width: 100%;
          gap: 30px;
          justify-content: space-between;
        }

        >:global(.tab) {
          flex-grow: 0;
          max-width: 242px;

          font-weight: 400;
          font-size: 16px;
          line-height: 24px;
          letter-spacing: 0%;
          text-transform: uppercase;

          @include screen(md, true) {
            flex-grow: 1;
            // max-width: 30%;
            font-size: 15px;
          }
        }

        >:global(.scrollBtn) {
          height: 100%;
          top: 0;
          background-color: $background-grey2;
        }
      }
    }

    .subCategory {
      &:not(:last-child) {
        margin-bottom: 80px;

        @include screen(md, true) {
          margin-bottom: 40px;
        }
      }

      h3 {
        font-weight: 400;
        font-size: 24px;
        line-height: 28px;
        letter-spacing: 1px;
        text-align: center;
        text-transform: uppercase;
        margin-bottom: 38px;
        color: $origin-black;

        @include applyFont($font-main-display-large, $font-cn-large);

        @include screen(md, true) {
          font-size: 18px;
          line-height: 22px;
          letter-spacing: 0%;
          margin-bottom: 30px;
          @include applyFont($font-main-display-large, $font-cn-large);
        }
      }

      >div {
        display: flex;
        flex-flow: row wrap;
        justify-content: center;
        gap: 50px 30px;

        @include screen(md, true) {
          gap: 20px;
        }

        >.member {
          width: calc(25% - 22.5px);
          padding-bottom: 20px;
          cursor: pointer;
          display: block;

          @include screen(md, true) {
            width: calc(50% - 10px);
          }

          >img {
            width: 100%;
            height: auto;
            display: block;
            margin-bottom: 20px;
          }

          .name {
            font-weight: 400;
            font-size: 19px;
            line-height: 30px;
            letter-spacing: 0%;
            text-align: center;
            color: $origin-black;
            display: block;
            margin-bottom: 10px;
            @include applyFont($font-main-display, $font-cn);

            @include screen(md, true) {
              font-size: 14px;
              line-height: 100%;
              @include applyFont($font-main-display, $font-cn);
            }

            @include screen(xs, true) {
              // 2x line-height; try to make jobTitle align
              min-height: 28px;
            }
          }

          .position {
            font-weight: 400;
            font-size: 14px;
            line-height: 22px;
            letter-spacing: 0%;
            text-align: center;
            color: $pen-mid-grey;
            display: block;

            @include screen(md, true) {
              line-height: 100%;
            }
          }
        }
      }
    }
  }
}