@use '@/styles/_mixins.scss' as *;
@use '@/styles/_colors.scss' as *;
@use '@/styles/_fonts.scss' as *;

.container {
    display: flex;
    flex-direction: column;
    // gap: 1px 0;

    .accorItem {
        border-bottom: 1px solid $pen-border-grey;

        .tabContainer {
            display: flex;
            width: 100%;
            justify-content: space-between;
            align-items: center;
            padding-top: 30px;
            padding-bottom: 20px;

            .tabTitle {
                font-weight: 400;

                @include screen(md,true){
                    font-size: 20px;
                    line-height: 24px;
                    @include applyFont($font-main-display, $font-cn);
                }
            }

            >svg {
                transition: transform .3s;
            }
        }

        .desc {
            text-align: start;
            padding-bottom: 20px;

            @include descFontSize();
        }

        .cta {
            display: inline-block;
            min-width: auto;
            background-color: inherit;

            >span {
                white-space: nowrap;
            }
        }

        &:not(:global(.open)) {
            .tabContainer {
                >svg {
                    transform: rotate(180deg);
                }
            }
        }
    }
}

.truncate {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;

    @include screen(sm, true) {
        font-size: 16px;
        line-height: 24px;
        letter-spacing: 0%;
    }
}