@use "@/styles/_colors.scss" as *;
@use "@/styles/_mixins.scss" as *;
@use "@/styles/_fonts.scss" as *;


:global(.more-hotels) {

    .mainTitle {
        font-weight: 400;
        letter-spacing: 1px;
        text-align: center;
        text-transform: uppercase;
        margin-bottom: 40px;

        @include screen(md, true) {
            font-size: 28px;
            line-height: 28px;
            letter-spacing: 0%;
        }
    }

    .propertiers {
        width: 100%;
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 55px 40px;

        @include screen(md, true) {
            grid-template-columns: repeat(1, 1fr);
            gap: 30px;
        }

        .property {
            $visible-txt-area: 120px;
            padding-bottom: $visible-txt-area;
            position: relative;
            overflow: hidden;
            display: block;

            @include screen(md, true) {
                padding-bottom: 0;
            }

            img {
                display: block;
                width: 100%;
                height: auto;
            }

            .textBox {
                box-sizing: border-box;
                display: block;
                width: 100%;
                height: auto;
                min-height: $visible-txt-area;
                position: absolute;
                left: 0;
                bottom: 0;
                transform: translateY(calc(100% - $visible-txt-area));

                background-color: $origin-white;
                padding: 10px 20px 11px;

                transition: transform .3s;

                @include screen(md, true) {
                    position: relative;
                    left: auto;
                    bottom: auto;
                    transform: none;
                    transition: none;

                    padding: 0;
                }

                .upper {
                    box-sizing: border-box;
                    width: 100%;
                    height: $visible-txt-area;
                    overflow: hidden;
                    padding-bottom: 13px;
                    display: flex;
                    flex-flow: column nowrap;
                    justify-content: center;
                    align-items: center;

                    @include screen(md, true) {
                        padding: 20px 19px 13px;
                    }

                    .subCat {
                        font-weight: 500;
                        font-style: Medium;
                        font-size: 12px;
                        line-height: 100%;
                        letter-spacing: 5%;
                        text-align: center;
                        text-transform: uppercase;
                        color: $pen-gold;
                        margin-bottom: 10px;
                        white-space: nowrap;
                    }

                    .title {
                        font-weight: 400;
                        font-size: clamp(14px, 1.8vw, 20px);
                        width: fit-content;

                        line-height: 26px;
                        letter-spacing: 0%;
                        text-align: center;
                        color: $origin-black;
                        // white-space: nowrap;

                        &:hover {
                            color: $pen-gold;
                        }

                        @include screen(md, true) {
                            font-size: 20px;
                        }
                    }

                    .location {
                        font-weight: 400;
                        font-size: 14px;
                        line-height: 30px;
                        letter-spacing: 0%;
                        text-align: center;
                        text-transform: uppercase;
                        color: $pen-mid-grey;
                        white-space: nowrap;
                    }
                }

                .lower {
                    $border: 1px solid $pen-border-grey;
                    border-top: $border;
                    border-bottom: $border;
                    padding: 10px;
                    color: #4a4a4a;

                    >div {
                        max-width: 194px;
                        margin: 0 auto;
                        display: grid;
                        grid-template-columns: 74% 1fr;
                        gap: 0 22.68px;

                        >div {
                            font-weight: 400;
                            font-size: 15px;
                            line-height: 32px;
                            letter-spacing: 0%;
                            white-space: nowrap;

                            &:nth-child(even) {
                                text-align: right;
                            }
                        }
                    }
                }
            }

            &:hover {
                .textBox {
                    // top: auto;
                    // bottom: 0;
                    transform: translateY(0);
                }
            }
        }
    }

    .moreInfoBtn {
        display: flex;
        justify-content: center;
        margin-top: 20px;

        >a {
            text-align: center;
            min-width: 188px;
            display: inline-block;
            padding: 20px;
            background-color: transparent;

            >span {
                font-weight: 400;
                font-style: Book;
                font-size: 14px;
                leading-trim: CAP_HEIGHT;
                line-height: 100%;
                letter-spacing: 5%;
                text-align: center;
                text-transform: uppercase;
                color: $pen-gold;
            }
        }
    }
}