@use '@/styles/_mixins.scss' as *;
@use '@/styles/_colors.scss' as *;

.genericLightBox{
  display: block;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  visibility: visible;
  padding: 112px 30px 30px;
  background-color: rgba($origin-black, .9);
  position: fixed;
  z-index: 999999999;
  box-sizing: border-box;

  @include screen(md, true) {
    padding: 136px 20px 20px;
  }

  &:not(.isOpen){
    &:not(.offscreenMode){
      display: none;
    }
    &.offscreenMode{
      display: block;
      visibility: hidden;
      left: -200%;
      top: -200%;
      width: 0;
      height: 0; 
      overflow: hidden; 
      background-color: transparent; 
      padding: 0 !important;
    }
  }

  .btnGroup{
    display: flex;
    flex-flow: row nowrap;
    gap: 10px;

    position: absolute;
      right: 30px;
      top: 47px;
      z-index: 100;
  
    .CloseBtn{
      display: flex;
      align-items: center;
      justify-content: center;
      width: 45px;
      height: 45px;
      // position: absolute;
      // right: 30px;
      // top: 47px;
      border: $pen-mid-grey 1px solid;
      cursor: pointer;

      transition: border .2s, background .2s;
      >svg{
        width: 17px;
        height: 17px;
        right: 20px;
        top: 20px;
        >path{
          transition: fill .2s;
        }
      }

      &:hover{
        border-color: $pen-light-gold;
        background-color: #fff;
        >svg{
          >path{
            fill: $pen-dark-gold;
          }
        }
      }
    }

    :global(.share){
      :global(.sharePop){
        left: auto;
        right: -56px;
        transform: none;
        background: rgba($origin-black, .7);
        >button{
          background-color: transparent !important;
        }
      }
      :global(.wechatShare){
        right: -13px;
        left: auto;
        transform: none;
      }
    }
  }

  
}