@use "../_fonts.scss" as *;
@use "../_colors.scss" as *;
@use "../_mixins.scss" as *;

.hsh-anchor-banner {

    .mainWrapper {
        position: relative;
    }

    .imgWrapper {
        line-height: 0;
        width: 100%;

        img {
            width: 100%;
            height: auto;
            object-fit: contain;
        }

        .desktop-img {
            display: inline;
        }

        .mobile-img {
            display: none;
        }


        @include screen(md, true) {
            .desktop-img {
                display: none;
            }

            .mobile-img {
                display: inline;
            }
        }
    }

    .text-wrapper {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        color: $origin-white;
        text-align: center;
    }

    .text-container {}

    .sub-heading {
        font-weight: 400;
        font-size: 13px;
        line-height: 22px;
        letter-spacing: 10%;
        text-align: center;
        text-transform: uppercase;
        color: $origin-white;
        margin-bottom: 2px;

        @include screen(md, true) {
            font-weight: 500;
        }
    }

    .title {
        margin: 0;
        margin-bottom: 10px;
        font-weight: 400;
        line-height: 60px;
        text-transform: uppercase;
        color: white;

        @include screen(md, true) {
            font-size: 42px;
            line-height: 45px;
            letter-spacing: 0%;
            white-space: normal;
            word-wrap: break-word;
            overflow-wrap: break-word;
        }
    }

    .hsh-redirect-btn {
        display: inline-block;
        max-width: 65%;

        >a {
            padding: 15px 40px;
        }
    }

    .cta-btn {
        color: $origin-white;
        text-transform: uppercase;
        font-size: 14px;
    }
}