@use "../_colors.scss" as *;
@use "../_mixins.scss" as *;
@use "../_fonts.scss" as *;

.hsh-top-intro {

    .content-center {
        display: flex;
        justify-content: center;
    }

    .container {
        max-width: 1000px;
        box-sizing: border-box;

        @include screen(lg, true) {
            max-width: 850px;
        }

        @include screen(md, true) {
            padding: 0 20px;
        }
    }

    .sub-title {
        font-weight: 400;
        text-align: center;
        font-size: 32px;
        line-height: 44px;
        letter-spacing: 0px;
        text-transform: capitalize;
        margin-top: 15px;

        @include screen(md, true) {
            @include applyFont($font-main-display-large, $font-cn-large);
            font-size: 24px;
            line-height: 30px;
            margin-bottom: -10px;
        }
    }

    .description {
        // align with pm,
        // type-4(gold quote) special handling
        @include descFontSize();
    }

    &:lang(zh-HK),
    &:lang(zh-TW),
    &:lang(zh-Hant) {
        .description {
            //hsh-120
            letter-spacing: 1.3px !important;
        }
    }

    .hsh-redirect-btn {
        text-transform: uppercase;
        font-size: 14px;
        line-height: 18px;
        letter-spacing: 0.05em;

        &:lang(zh-HK),
        &:lang(zh-TW),
        &:lang(zh-Hant) {
            font-size: 16px;
        }
    }

    // type-1 to type-5

    &.type-1 {
        .main-title {
            font-weight: 400;
            font-size: 36px;
            line-height: 44px;
            letter-spacing: 0%;
            text-align: center;
            text-transform: capitalize;

            @include screen(md, true) {
                font-size: 28px;
                line-height: 120%;
            }
        }

        .small-heading {
            font-weight: 400;
            font-size: 13px;
            line-height: 22px;
            letter-spacing: 10%;
            text-align: center;
            text-transform: uppercase;
            color: $pen-gold;
            margin-bottom: 5px;

            &:lang(zh-HK),
            &:lang(zh-TW),
            &:lang(zh-Hant) {
                font-size: 18px;
            }

            @include screen(md, true) {
                font-size: 11px;
                letter-spacing: 6%;

                &:lang(zh-HK) {
                    font-size: 16px;
                }
            }
        }

        .description {
            font-weight: 400;
            text-align: center;
            color: $pen-dark-grey;
            margin-top: 20px;

            @include screen(md, true) {
                max-width: none;
                width: 100%;
                letter-spacing: 0%;
            }
        }

        .HshRedirectBtn-wrapper {
            display: flex;
            justify-content: center;
            margin-top: 30px;
        }

        .hsh-redirect-btn {
            text-transform: uppercase;
            font-size: 14px;
            letter-spacing: 0.05em;

            >a {
                padding: 15px 20px;
            }
        }
    }

    &.type-2,
    &.type-6 {
        .main-title {
            font-weight: 400;
            font-size: 58px;
            line-height: 65px;
            text-align: center;
            letter-spacing: 0%;
            text-transform: uppercase;

            @include screen(md, true) {
                font-size: 32px;
                line-height: 36px;
                letter-spacing: 0%;
            }
        }

        .small-heading {
            font-weight: 400;
            font-size: 58px;
            line-height: 22px;
            letter-spacing: 10%;
            text-align: center;
            text-transform: uppercase;
            color: $pen-gold;
            margin-bottom: 5px;

            @include screen(md, true) {
                font-weight: 400;
                font-size: 11px;
                letter-spacing: 6%;
            }
        }

        .description {
            font-weight: 400;
            text-align: center;
            color: $pen-dark-grey;
            margin-top: 20px;

            @include screen(md, true) {
                max-width: none;
                width: 100%;
                letter-spacing: 0%;
            }
        }

        .HshRedirectBtn-wrapper {
            display: flex;
            justify-content: center;
            margin-top: 30px;
        }

        .hsh-redirect-btn {
            text-transform: uppercase;

            >a {
                padding: 20px 40px;
            }
        }
    }

    &.type-3 {
        .main-title {
            text-align: center;
            font-weight: 400;
            // hsh-77: reason why change font size
            font-size: 58px;
            line-height: 65px;
            letter-spacing: 0%;
            text-transform: uppercase;
            margin-bottom: 20px;

            @include screen(md, true) {
                // hsh-77: reason why change font size
                font-size: 32px;
                line-height: 36px;
                letter-spacing: 0%;
            }
        }

        .description {
            text-align: center;
            color: $pen-dark-grey;
        }
    }

    &.type-4 {
        .description {
            text-align: center;
            color: $pen-gold;
            margin: 0 auto;
            font-weight: 500;
            font-style: Italic;
            font-size: 24px;
            line-height: 35px;
            letter-spacing: 0%;
            @include applyFont('Baskervville-Italic', $font-cn-large);

            @include screen(md, true) {
                max-width: none;
                width: 100%;
                font-size: 18px;
                line-height: 28px;
                letter-spacing: 0%;
            }

            svg {
                flex-shrink: 0;
                position: relative;
                top: -0.35em;
            }
        }

        .citation {
            margin-top: 30px;
        }

        .citation__logo {
            display: flex;
            justify-content: center;
            width: 100%;

            div {
                width: 155px;
                aspect-ratio: 1;
                line-height: 0;

                @include screen(md, true){
                    width: 80px;
                }
            }

            .citation__img {
                width: 100%;
                height: 100%;
                border-radius: 50%;
                object-fit: contain;
            }
        }

        .citation__content {

            margin-top: 12px;

            .name,
            .jobTitle {
                text-align: center;
            }

            .name {
                font-size: 16px;
                line-height: 24px;
                color: $origin-black;
            }

            .jobTitle {
                margin: 0;
                font-size: 14px;
                line-height: 22px;
                letter-spacing: 0%;
                // text-decoration: underline;
                // text-decoration-style: solid;
                // text-decoration-thickness: 0%;
                color: $pen-mid-grey;
            }
        }
    }

    &.type-5 {

        .container {
            max-width: 900px;

            @include screen(lg, true) {
                max-width: 900px;
            }

            @include screen(md, true) {
                padding: 0 20px;
            }
        }

        >section {
            padding-top: 0;
        }

        &:nth-child(1 of .type-5) {
            padding-top: 80px;

            @include screen(md, true) {
                padding-top: 50px;
            }
        }

        .main-title {
            font-weight: 400;
            font-size: 24px;
            line-height: 28px;
            letter-spacing: 1px;
            text-align: center;
            text-transform: uppercase;
            margin-top: 10px;
        }

        .description {
            letter-spacing: 0%;
            text-align: center;
            margin-top: 20px;
            color: $pen-dark-grey;
        }

        .HshRedirectBtn-wrapper {
            display: flex;
            justify-content: center;
            margin-top: 30px;

            .hsh-redirect-btn {
                line-height: 1;

                a {
                    padding: 15px 20px;
                    line-height: 0;

                    @include screen(md, true) {
                        display: flex;
                        align-items: center;
                    }
                }
            }

            span {
                font-weight: 400;
                font-style: Book;
                font-size: 14px;
                line-height: 18px;
                letter-spacing: 5%;
                text-align: center;
                text-transform: uppercase;
            }

            svg {
                position: relative;
                top: 2px;
                margin-left: 5px;

                @include screen(md, true) {
                    top: auto;
                    flex-shrink: 0;
                }
            }
        }
    }

    &.type-6 {
        .hsh-redirect-btn {
            border: 0;

            &:hover {
                .span-normal {
                    text-decoration: underline;
                }
            }
        }
    }
}