@use "@/styles/_fonts.scss" as *;
@use "@/styles/_colors.scss" as *;
@use "@/styles/_mixins.scss" as *;

// Main container
.guestComments {
  width: 100%;
  box-sizing: border-box;
  padding: 0 80px;

  @include screen(sm, true) {
    padding: 0 20px;
  }

  // Each comment set
  .set {
    display: flex;
    align-items: center;
    margin-bottom: 75px;
    gap: 0 80px;

    // Even sets (image left, text right)
    &.even {
      .image {
        order: 1;
      }

      .text {
        order: 2;
      }
    }

    // Odd sets (image right, text left)
    &.odd {
      .image {
        order: 2;
      }

      .text {
        order: 1;
      }
    }
  }

  // Image styling
  .image {
    flex: 1;
    line-height: 0;

    @include screen(md, true) {
      max-width: none;
      width: 100%;
      margin-bottom: 20px;
    }

    img {
      width: 100%;
      height: auto;
    }
  }

  // Text content styling
  .text {
    width: 37vw;
    // max-width: 550px;
    box-sizing: border-box;

    @include screen(md, true) {
      width: 100%;
      max-width: none;
      padding: 0;
    }
  }

  .comments {
    font-family: $font-main-display-large;
    font-family: 'Baskervville-Italic';
    font-weight: 500;
    font-style: Italic;
    font-size: 21px;
    line-height: 35px;
    color: $pen-gold;
    text-align: start;
    margin-bottom: 20px;
    position: relative;

    &:lang(zh-HK),
    &:lang(zh-TW),
    &:lang(zh-Hant) {
      font-family: $font-cn-large;
    }

    @include screen(md, true) {
      font-size: 18px;
      line-height: 28px;
      // for left quote indent
      padding-left: 18px;
    }

    svg {
      flex-shrink: 0;
      position: relative;
      top: -0.25em;

      @include screen(md, true) {
        top: -.15em;
      }

      &:global(.quote-start) {
        position: absolute;
        left: -.8em;
        top: .45em;

        @include screen(md, true) {
          left: 0;
          top: .3em;
        }
      }
    }

    p {
      margin: 0;
      display: inline;
    }
  }

  .guestName {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
  }

  .jobTitle {
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    color: $pen-mid-grey;
    // text-decoration: underline;
    // text-decoration-style: solid;
    // text-decoration-offset: 0%;
    // text-decoration-thickness: 0%;
  }

  // Mobile responsiveness
  @include screen(md, true) {
    .set {
      flex-direction: column;

      &.even,
      &.odd {

        .image,
        .text {
          order: initial;
        }
      }
    }
  }
}