@use "@/styles/_colors.scss" as *;
@use "@/styles/_mixins.scss" as *;

.container {
    padding: 0 160px;

    @include screen(md, true) {
        padding: 0 40px;
    }
}

.siteMapTitle {
    text-align: center;
    margin-bottom: 50px;
    font-weight: 400;
}

.row {
    display: flex;
    justify-content: space-between;
    padding: 10px;
    border-bottom: 1px solid $pen-border-grey;
    margin-bottom: 10px;
    gap: 0 20px;
}

.left {
    flex: 1;
    margin: 5px 0;
}

.right {
    flex: 1;
}