@use '@/styles/_mixins.scss' as *;
@use '@/styles/_colors.scss' as *;
@use '@/styles/_fonts.scss' as *;

$carouselPadding: 80px;

.mainContainer {
    display: flex;
    padding: 0 80px;

    @include screen(md, true) {
        flex-direction: column;
        padding: 0;
    }

    div {
        color: $origin-white;
        box-sizing: border-box;
    }
}

.subjectContainer {
    width: 30%;

    @include screen(md, true) {
        width: 100%;
        padding: 0 30px;
    }
}

.title {
    margin-bottom: 10px;
    font-weight: 400;
    color: $g6-grey;
    text-align: start;

    @include screen(md, true) {
        font-size: 28px;
        line-height: 28px;
        letter-spacing: 0%;

        @include applyFont($font-main-display-large, $font-cn-large)
    }
}

.remarks {
    font-size: 18px;
    line-height: 28px;
    letter-spacing: 0%;
    min-height: 50px;

    @include screen(md, true) {
        font-size: 15px;
        line-height: 24px;
        padding-bottom: 20px;
    }
}

[class*=UpdateCarousel] {
    .subjectCta {
        border: 1px solid white;
        display: inline-block;
        min-width: auto;
        min-height: auto;
        padding: 15px 33px;
        margin-top: 50px;
        color: white;

        &:hover {
            border-color: $pen-light-gold;
            color: $pen-light-gold;
        }

        @include screen(md, true) {
            display: none;
        }
    }

    .slideItemCta {
        padding: 0;
        margin-top: 30px;
        min-height: auto;
        min-width: auto;
        line-height: 1;
        color: white;

        &:lang(zh-HK),
        &:lang(zh-TW),
        &:lang(zh-Hant) {
            font-size: 16px;
            line-height: 100%;
            letter-spacing: 1.3px;
        }

        :global(.cta-arrow) {
            transform: translateY(1px);
            >svg {
                transform: none;
                width: 13px;
                height: 13px;
            }
        }
    }

    [class*=slick-dots] {
        bottom: 0;
    }

    .mobileCtaContainer {
        display: flex;
        justify-content: center;
    }

    .mobileCta {
        display: none;

        @include screen(md, true) {
            margin-top: 25px;
            display: flex;
            border: 1px solid white;
            min-width: auto;
            width: auto;
            min-height: auto;
            padding: 15px 33px;
            color: white;
        }
    }
}


.sliderWrapper {
    display: flex;
    flex: 1;
    justify-content: center;
    width: calc(70% + 80px);
    position: relative;
    left: 80px;

    @include screen(md, true) {
        position: static;
        width: 100%;
        // assume no rtl handling
        padding-left: 30px;
    }
}

.slider {
    width: 100%;
}

.slickContainer {
    padding-right: 10px;
}

.slickRow {
    display: flex;
    width: 100%;
    justify-content: center;
}

.singleItem {
    flex: 1;

    @include screen(xs, true) {
        max-width: none;
    }
}

.itemCard {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}

.cardImg {
    width: 100%;
    aspect-ratio: 42 / 48;

    img {
        width: 100%;
        height: auto;
        object-fit: contain;
    }
}

.label,
.date {
    width: 100%;
    text-align: start;
}

.label {
    margin-top: 20px;
    min-height: 54px;
    font-size: 22px;
    line-height: 120%;
    letter-spacing: 0%;
    vertical-align: bottom;
}

.date {
    //hsh-148 comment
    font-size: 13px;
    line-height: 24px;
    letter-spacing: 0%;
}

.arrow {
    position: absolute;
    z-index: 2;
    background-color: $origin-black;
    top: 40%;
    transform: translateY(-50%);
    width: 50px;
    aspect-ratio: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;

    [class*=slider-arrow] {
        line-height: 0;
        path {
            fill: #fff;
        }
    }
}

.left {
    left: 0;
}

.next {
    right: 2.5%;

    @include screen(lg) {
        right: 4%;
    }
}