@use '@/styles/_mixins.scss' as *;
@use '@/styles/_colors.scss' as *;
@use '@/styles/_fonts.scss' as *;

.customContainer {
    display: flex;
    width: 100%;
    box-sizing: border-box;
    padding: 0 80px;
    gap: 0 80px;

    @include screen(md, true) {
        flex-direction: column-reverse;
    }

    @include screen(xs, true) {
        padding: 0 20px;
    }
}

[class*=PreviewBoardType3] {
    .customContainer {
        @include screen(md) {
            flex-direction: row-reverse;
        }
    }
}

.flexOne {
    flex: 1;
}

.contentWrapper {
    margin-top: 20px;
    box-sizing: border-box;
    height: 100%;

    @include screen(md, true) {
        padding-right: 0;
    }
}

.rowP {
    padding-top: 30px;
    padding-bottom: 20px;
    border-bottom: 1px solid $pen-border-grey;
}

.desc {
    text-align: start;
    margin-top: 20px;
    margin-bottom: 30px;
    // hsh-201
    font-size: 16px;
    line-height: 24px;

    @include screen(md, true) {
        text-align: center;
    }
}

.imgWrapper {
    padding: 0 10px;

    img {
        width: 100%;
        height: auto;
        object-fit: contain;
    }

    @include screen(md, true) {
        padding: 0;
    }
}

.mobileHeader {
    display: none;

    @include screen(md, true) {
        display: block;
        text-align: center;
        font-weight: 400;
        font-size: 28px;
        line-height: 28px;
        text-align: center;
        text-transform: uppercase;
        margin-bottom: 20px;
        @include applyFont($font-main-display-large, $font-cn-large);
    }
}

.textBlock {
    min-height: 30%;
}

.boardTextType1Title {
    font-weight: 400;
    color: $origin-black;

    @include screen(md, true) {
        display: none;
    }
}

.tabContainer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
    box-sizing: border-box;
    padding-top: 30px;
    padding-bottom: 20px;
}

.tabTitle {
    font-weight: 400;
}

.tabCta {
    display: inline-block;
    min-width: auto;
    background-color: inherit;
    margin-bottom: 30px;
}