@use "@/styles/_colors.scss" as *;
@use "@/styles/_typography.scss" as *;
@use "@/styles/_variables.scss" as *;
@use "@/styles/_mixins.scss" as *;

.scrollToTop{
  height: 45px;
  width: 45px;
  background-color: #867050;
  justify-self: end;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  margin: 0;
  // from comments, no figma for this
  position: fixed;
  bottom: 7%;
  right: 0;
  top: auto;
  left: auto;
  img{
    padding: 0;
    margin: 0;
  }
}

.scrollToTopWrapper{
  height: 0px;
}