@use "../_fonts.scss" as *;
@use "../_colors.scss" as *;
@use "../_mixins.scss" as *;

.hsh-business-detail {

    .container {
        display: flex;
        justify-content: center;
        padding: 0 270px;
        box-sizing: border-box;
        flex-direction: column;

        @include screen(md, true) {
            padding: 0 20px;
        }
    }

    .imgWrap {
        line-height: 0;

        @include screen(md, true) {
            margin-bottom: 10px;
        }
    }

    .imgWrap img {
        width: 100%;
        height: auto;
    }

    .titleContainer {
        max-width: 800px;
    }

    .title {
        margin: 40px auto 0;
        font-weight: 400;
        font-size: 36px;
        line-height: 44px;
        letter-spacing: 0px;
        text-align: center;
        max-width: 550px;

        @include applyFont($font-main-display-large, $font-cn-large);

        @include screen(md, true) {
            max-width: none;
            padding: 0 10px;
            font-size: 28px;
            line-height: 120%;
        }
    }

    .description {
        margin: 40px auto 0;
        text-align: center;
        font-weight: 400;
        letter-spacing: 0%;
        color: $pen-dark-grey;
        max-width: 800px;

        @include descFontSize();

        @include screen(md, true) {
            max-width: none;
            padding: 0 10px;
        }

        p {
            display: inline;
        }
    }

}