@use '@/styles/_mixins.scss' as *;
@use '@/styles/_colors.scss' as *;
@use '@/styles/_fonts.scss' as *;

.wrapper {
    padding-top: 20px;
    padding-bottom: 80px;
}

.container {
    margin-top: 36px;
}

.imgBlk {
    line-height: 0;
    padding: 75px;
    background-color: $background-grey2;
    box-sizing: border-box;

    @include screen(md, true) {
        margin-bottom: 30px;
        padding: 40px;
    }
}

.contentBlk {
    box-sizing: border-box;
    padding: 0 60px;

    @include screen(md, true) {
        padding: 0;
    }

    ul {
        display: flex;
        flex-direction: column;
        gap: 20px 0;
        color: $pen-dark-grey;
    }
}

.heading {
    font-weight: 400;
    letter-spacing: 1px;
    text-transform: uppercase;
    margin-bottom: 20px;

    @include screen(md, true) {
        font-size: 24px;
        line-height: 30px;
        text-align: start;
        @include applyFont($font-main-display-large, $font-cn-large);
    }
}

.subHeading {
    font-weight: 400;
    font-size: 15px;
    line-height: 28px;
    letter-spacing: 0%;
    text-transform: uppercase;
    margin-bottom: 10px;

    @include applyFont($font-main-display, $font-cn);
}

.pointList {
    $left-shift: 24px;
    list-style: disc;
    position: relative;
    left: $left-shift;
    width: calc(100% - $left-shift);
}

.cta {
    font-size: 16px;
    line-height: 100%;
    letter-spacing: 5%;
    padding-left: 0;
    padding-bottom: 0;
    min-height: auto;
    margin-top: 20px;
}

.memberName {
  font-size: 18px;
  line-height: 28px;
  letter-spacing: 0%;
}

.memberJobTitles {
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  letter-spacing: 0%;
  white-space: nowrap;

  @include screen(md,true){
    font-size: 14px;
  }
}