html {
  --var-font-main-display: "Futura Std", Arial, "PingFang TC", "Microsoft JhengHei", serif;
  --var-font-main-display-large: "Baskervville", Georgia, "Songti TC", "SimSun", serif;
  --var-font-main-display-small: "Minion Pro", Georgia, serif;

  --var-font-cn-large: "Songti TC", "SimSun", "宋体", "Baskervville", Georgia, serif;
  --var-font-cn: "PingFang TC", "Microsoft JhengHei", "SimSun","Futura Std", Arial, "宋体", serif;
}

$font-main-display: var(--var-font-main-display);
$font-main-display-large: var(--var-font-main-display-large);
$font-main-display-small: var(--var-font-main-display-small);

$font-cn-large: var(--var-font-cn-large);
$font-cn: var(--var-font-cn);

$font-navigation: $font-main-display;
$font-footer-body: $font-main-display;
$font-Baskervville: $font-main-display-large;

.text-transform-normal {
  text-transform: none;
}

.text-transform-capitalize {
  text-transform: capitalize;
}

.text-transform-uppercase {
  text-transform: uppercase;
}

