@use "../_colors.scss" as *;
@use "../_mixins.scss" as *;

iframe {
    border: 0;
}

#iWrapper {
    height: 100%;
}

.normial-value-box2 {
    font-family: "Minion Pro", Helvetica, Arial, sans-serif;
    color: #a58b52;
    line-height: 0.8;
    margin-top: 40px;
    margin-bottom: 22px;
    font-weight: 400;
    font-style: Regular;
    font-size: 56px;
    leading-trim: NONE;
    line-height: 44px;
    letter-spacing: 0%;
    text-transform: uppercase;

    &:lang(zh-HK),
    &:lang(zh-TW),
    &:lang(zh-Hant) {
        margin-top: 34px;
    }
}

.display-date {
    color: rgba(109, 110, 113, 1);
    font-weight: 400;
    font-size: 12px;
    leading-trim: NONE;
    line-height: 1;
    letter-spacing: 0%;
    text-align: center;
    margin-bottom: 5px;

    &:lang(zh-HK),
    &:lang(zh-TW),
    &:lang(zh-Hant) {
        font-size: 14px;
        line-height: 30px;
    }
}