@use "@/styles/_colors.scss" as *;
@use "@/styles/_mixins.scss" as *;
@use "@/styles/_fonts.scss" as *;

// Main container
.zigzagContentBlock3{
  // @include responsiveWidth();
  @include responsivePadding();

  .wrapper {
    width: 100%;
    box-sizing: border-box;
    display: flex;
    flex-flow: column nowrap;
    row-gap: 75px;

    // @media screen and (max-width: 1440px) {
    //   padding: 0 80px;
    // }

    // @include screen(sm, true) {
    //   padding: 0 20px;
    // }

    h3{
      font-weight: 400;
      letter-spacing: 0%;
      text-align: center;
      font-size: 48px;
      line-height: 65px;
      margin-bottom: 5px;
      color: $origin-black;
      text-transform: none;
      @include applyFont($font-main-display-large, $font-cn-large);

      @include screen(md, true) {
        font-size: 24px;
        line-height: 30px;
        margin-bottom: -25px;
      } 
    }

    // Each comment set
    .set {
      display: flex;
      align-items: center;
      // margin-bottom: 75px;
      gap:20px 80px;

      // Even sets (image left, text right)
      &.even {
        .image {
          order: 1;
        }

        .text {
          order: 2;
        }
      }

      // Odd sets (image right, text left)
      &.odd {
        .image {
          order: 2;
        }

        .text {
          order: 1;
        }
      }

      @include screen(md, true) {
        flex-direction: column;
        &.even,
        &.odd {
          .image,
          .text {
            order: initial;
          }
        }
      }

      // Image styling
      .image {
        flex: 1;
        line-height: 0;

        @include screen(md, true) {
          max-width: none;
          width: 100%;
          // margin-bottom: 20px;
        }

        img {
          width: 100%;
          height: auto;
        }
      }

      // Text content styling
      .text {
        width: 37vw;
        box-sizing: border-box;

        @include screen(md, true) {
          width: 100%;
          max-width: none;
          padding: 0;
        }

        h4{
          font-weight: 400;
          font-size: 24px;
          line-height: 28px;
          letter-spacing: 1px;
          text-transform: uppercase;
          text-align: left;
          margin-bottom: 20px;
          color: $origin-black;
          @include applyFont($font-main-display-large, $font-cn-large);

          @include screen(md, true) {
            line-height: 30px;
            letter-spacing: 0%;
          } 
        }

        .desc{
          font-weight: 400;
          letter-spacing: 0%;
          text-align: left;
          margin-bottom: 30px;
          color: $pen-dark-grey;

          @include applyFont($font-main-display, $font-cn);

          @include descFontSize();
        }

        .link{
          display: inline-block;
          padding: 20px;
          min-width: 0;

          @include screen(md, true){
            padding: 12px;
          }

          span{
            font-weight: 400;
            font-size: 14px;
            line-height: 100%;
            letter-spacing: 5%;
            text-align: center;
            text-transform: uppercase;
            color: $pen-gold;
          }
        }
      }
    }
  }
}