@use "@/styles/_colors.scss" as *;
@use "@/styles/_mixins.scss" as *;


.row {
    display: flex;
    align-items: center;
    width: 100%;
    padding: 0 80px;
    box-sizing: border-box;
    margin-bottom: 60px;

    @include screen(md, true) {
        padding: 0 20px;
    }

    &.odd,
    &.even {

        [class*="img-block"],
        [class*="content-block"] {
            width: 50%;

            @include screen(md, true) {
                width: 100%;
            }
        }
    }

    [class*="img-block"]>img {
        width: 100%;
        height: auto;
        object-fit: contain;
    }

    &.odd {

        // target those exact name not hashed-name
        [class*="img-block"] {
            order: 2;
        }

        [class*="content-block"] {
            order: 1;
        }
    }
}

:nth-last-child(1 of .row) {
    margin-bottom: 0 !important;
}

@include screen(md, true) {
    .row {
        flex-direction: column;
        margin-bottom: 50px;

        &.even,
        &.odd {

            [class*="img-block"],
            [class*="content-block"] {
                order: initial;
            }
        }
    }
}