@use "@/styles/_colors.scss" as *;
@use "@/styles/_mixins.scss" as *;
@use "@/styles/_fonts.scss" as *;

// Main container
:global(.boardDetails) {
  background-color: $background-grey2;

  .wrapper {
    padding-top: 80px;
    padding-bottom: 60px;

    display: flex;
    flex-flow: row wrap;
    column-gap: 50px;

    @include responsiveWidth( $maxWidth: 964px );
    @include responsivePadding();

    @include screen(xl){
      max-width: none;
      padding-left: 240px;
      padding-right: 240px;
    }

    @include screen(md, true) {
      padding-top: 30px;
    }

    .pageHead {
      width: 100%;
      padding-bottom: 30px;
      order: 1;

      @include screen(md, true) {
        padding-top: 20px;
      }

      h1 {
        font-family: $font-main-display-large;
        font-weight: 400;
        font-size: 44px;
        line-height: 48px;
        letter-spacing: 0%;
        text-align: center;
        color: $origin-black;

        @include screen(md, true) {
          font-size: 32px;
          line-height: 100%;
        }

      }

      .position {
        font-family: $font-main-display;
        font-weight: 400;
        font-size: 16px;
        line-height: 24px;
        letter-spacing: 0%;
        text-align: center;
        margin-top: 12px;
        color: $pen-gold;

        @include screen(md, true) {
          margin-top: 10px;
        }
      }

    }

    .info {
      width: 100%;
      order: 2;

      display: flex;
      flex-flow: row nowrap;
      justify-content: center;
      gap: 12.5px 28px;


      padding-bottom: 50px;
      border-bottom: 1px solid $pen-border-grey;
      margin-bottom: 50px;

      @include screen(md, true) {
        order: 3;
        padding-bottom: 40px;
        margin-bottom: 40px;
        justify-content: left;
        flex-wrap: wrap;
      }

      li {
        display: block;
        font-family: $font-main-display;
        font-weight: 400;
        font-size: 16px;
        line-height: 22px;
        letter-spacing: 0%;
        text-align: center;
        color: $pen-mid-grey;

        position: relative;

        @include screen(md, true) {
          text-align: left;
          font-size: 14px;
        }

        &:not(:last-child):after {
          content: "";
          display: block;
          position: absolute;
          right: -13.5px;
          top: 5px;
          background-color: $pen-border-grey;
          height: 14px;
          width: 1px;
        }

      }
    }

    .media {
      width: calc(35.7% - 25px);
      order: 3;

      img {
        width: 100%;
        height: auto;
        display: block;
      }

      @include screen(md, true) {
        order: 2;
        width: 100%;
        margin-bottom: 19px;
      }
    }

    .detailedInfo {
      width: calc(64.3% - 25px);
      order: 4;
      margin-bottom: 60px;

      @include screen(md, true) {
        width: 100%;
        margin-bottom: 30px;
      }

      >div {
        &:not(:last-child) {
          margin-bottom: 50px;

          @include screen(md, true) {
            margin-bottom: 30px;
          }
        }

        h4 {
          font-family: $font-main-display;
          font-weight: 400;
          font-size: 16px;
          line-height: 22px;
          letter-spacing: 0%;
          margin-bottom: 20px;
          color: $origin-black;

          @include screen(md, true){
            font-size: 14px;
          }
        }

        p {
          font-family: $font-main-display;
          font-weight: 400;
          font-size: 18px;
          line-height: 28px;
          letter-spacing: 0%;
          text-align: left;
          margin: 0;
          color: $pen-dark-grey;

          @include screen(md, true) {
            font-size: 16px;
            line-height: 24px;
          }


          :global(.smallTxt) {
            font-family: Futura Std;
            font-weight: 400;
            font-style: Book;
            font-size: 14px;
            leading-trim: NONE;
            line-height: 22px;
            letter-spacing: 0%;
          }
        }
      }
    }

    .footNote {
      width: 100%;
      order: 5;
      border-top: 1px solid $pen-border-grey;
      padding-top: 13px;



      @include screen(md, true) {
        padding-top: 30px;
      }

      >p {
        font-family: $font-main-display;
        font-weight: 400;
        font-size: 12px;
        line-height: 20px;
        letter-spacing: 0%;
        color: $pen-mid-grey;
        text-align: left;
        margin: 0;
      }

    }
  }
}