@use '../../styles/colors' as colors;
@use '../../styles/mixins' as mixins;
@use '../../styles/arrow-settings' as arrowSettings;
@use '../../styles/fonts' as fonts;

/* ----------------------------------------
   Footer - Top / Breadcrumb
   ---------------------------------------- */
#footerTop {

  --gap-space: 37px;

  @include mixins.screen(md, true) {
    --gap-space: 25px;
  }

  $gap-space: var(--gap-space);

  & {
    display: flex;
    flex-wrap: wrap;
    // gap: 0 $gap-space;
    padding: 19px 160px;
    text-transform: uppercase;
    background-color: rgba(249, 249, 249, 255);

    @include mixins.screen(xl, true) {
      padding: 19px 80px;
    }

    @include mixins.screen(sl, true) {
      padding: 19px 60px;
    }

    @include mixins.screen(md, true) {
      padding: 19px 30px;
    }
  }

  >a,
  >span {
    position: relative;
    color: colors.$pen-mid-grey;
    font-size: 13px;
    text-decoration: none;
    letter-spacing: 2px;
    margin-left: $gap-space;

    &:first-child{
      margin-left: 0;
    }
  }

  >span {
    color: colors.$pen-font-color-primary;
  }

  >a:not(:last-child)::after {
    content: '•';
    position: absolute;
    color: colors.$pen-light-grey;
    // the font-size is not equal to display size, suppose 5px width/height
    font-size: 13px;
    margin-left: calc($gap-space / 2);
    transform: translateX(-50%);
  }
}

/* ----------------------------------------
   Footer base styles
   ---------------------------------------- */
footer {

  h3,
  .h3 {
    display: block;
    font-size: 16px;
    font-weight: 400;
    letter-spacing: 0.8px;
    line-height: 24px;
    font-family: fonts.$font-Baskervville;
    text-transform: uppercase;
    font-style: normal;
    margin-bottom: 0;
    color: colors.$origin-black;
    text-align: start;

  }
}

/* ----------------------------------------
   Footer row / columns
   ---------------------------------------- */
.footerRow {
  padding: 10px 30px;
  padding-bottom: 1rem;
  overflow: hidden;

  @include mixins.screen(md) {
    padding: 10px 60px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    a {
      margin-bottom: 15px;
    }
  }

  @include mixins.screen(xl) {
    padding: 10px 160px;
  }

  @include mixins.screen(xl, true) {
    padding: 19px 80px;
  }

  @include mixins.screen(md, true) {
    padding: 19px 30px;
  }
    

  p,
  a,
  h3 {
    display: block;
    font-weight: 400;
  }

  .footerColumn:last-child {
    @include mixins.screen(md, true) {
      border-bottom: 1px solid colors.$pen-border-grey;
    }
  }
}

/* Column */
.footerColumn {
  flex: 1;
  h3 {
    padding: 0;
    margin: 20px 0;
  }

  .pointer {
    cursor: pointer;
  }

  button {
    padding: 0;
    font-size: 20px;
  }

  ul {
    padding: 0;
    margin: 0;
  }

  li {
    list-style: none;
    font-size: 14px;
    max-width: 320px;
  }

  @include mixins.screen(md) {

    p,
    a {
      margin-bottom: 15px;
    }
  }

  @media screen and (max-width: 1024px){
    p,
    a {
      margin-bottom: 15px;
    }
  }


  @media (max-width: 1290px) {
    min-width: 24vw;
  }



  &.mobileAccordion {
    a {
      letter-spacing: 0.65px;
      text-transform: capitalize;
      padding-right: 10px;

      &::first-letter {
        text-transform: uppercase;
      }
    }
  }

  /* ensure button layout */
  h3 {
    button {
      user-select: text;
      pointer-events: inherit;
      cursor: text;
      background: none;
      border: 0;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding-right: 10px;
    }
  }
}

/* ----------------------------------------
   Social share
   ---------------------------------------- */
.footerSocialShare {
  padding: 19px 160px;
  display: flex;
  gap: 5px;

  li {
    display: inline-block;
    // margin: 0 2.5px;
    vertical-align: middle;
    font-size: 0;
    list-style: none;

    @include mixins.screen(md, true) {
      height: 40px;
      width: 40px;
    }
  }

  a {
    font-size: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    border: 1px solid colors.$pen-border-grey;

    &:hover {
      border: 1px solid colors.$pen-gold;
      transition: all 0.3s ease;
    }
  }

  @include mixins.screen(md, true) {
    position: inherit;
    justify-content: center;
    margin-top: 2rem;
    margin-bottom: 0;
    padding: 19px 30px;
  }
  @include mixins.screen(xl, true) {
    padding: 19px 80px;
  }

  @include mixins.screen(sl, true) {
    padding: 19px 60px;
  }
}

/* ----------------------------------------
   Mobile accordion behaviour
   ---------------------------------------- */
.mobileAccordion {

  /* by default hide inline svg arrows on small screens */
  h3 {
    button {
      svg {
        display: none;
      }
    }
  }

  @include mixins.screen(md, true) {
    h3 {
      margin: 0;

      button {
        svg {
          display: block;
        }
      }
    }

    border-top: 1px solid colors.$pen-border-grey;

    ul {
      overflow: hidden;
      max-height: 0;
      transition: all 0.3s ease-in-out;
      visibility: hidden;

      li {
        list-style: none;
      }

      li:first-child {
        margin-top: 20px;
      }

      li:last-child {
        margin-bottom: 20px;
      }
    }

    button {
      display: block;
      width: 100%;
      text-align: left;
      border-bottom: 1px solid colors.$pen-border-grey;
      padding: 15px 0;
    }

    button[aria-expanded="true"] {
      border-bottom: 1px solid colors.$pen-border-grey;

    }

    [data-expanded="true"] {
      visibility: visible;
      max-height: 250px;
    }

    .h3 {
      padding: 0;

      label {
        padding-top: 10px;
        padding-bottom: 10px;
      }
    }

    p,
    a {
      margin-top: 10px;
      margin-bottom: 10px;
      display: block;
    }
  }

  .h3 {
    color: colors.$origin-black;

    label {
      display: block;
      vertical-align: middle;

      &:focus {
        @include mixins.outline;
      }
    }

    @include mixins.screen(md) {
      border-bottom: 0;
    }
  }

  a {
    color: colors.$pen-darker-grey;

    &:hover {
      color: colors.$pen-gold;
    }
  }
}

/* ----------------------------------------
   Footer bottom / cookie links / language
   ---------------------------------------- */
#footerBottom {
  padding: 10px 160px;

  @include mixins.screen(md, true) {
    padding: 10px 30px;
    display: flex;
    justify-content: center;
  }
  @include mixins.screen(xl, true) {
    padding: 10px 80px;
  }

  @include mixins.screen(sl, true) {
    padding: 10px 30px;
  }  
}

.footerBottomContainer {
  margin-top: 15px;
  width: 100%;

  @include mixins.screen(md) {
    border-top: 1px solid colors.$pen-border-grey;
    padding-top: 15px;
  }

  >a {
    order: 2;
    display: inline-block;
  }

  p,
  a {
    color: colors.$pen-mid-grey;

    @include mixins.screen(md, true) {
      width: 100%;
      text-align: center;
    }
  }

  a:hover {
    color: colors.$pen-dark-gold;
  }

  @include mixins.screen(lg) {
    display: flex;
    flex-direction: row;
    clear: both;
    justify-content: space-between;
    flex-wrap: wrap;

    p {
      order: 2;
    }

    ul,
    h3 {
      display: inline-block;
    }

    h3 {
      margin-right: 20px;
    }

    p,
    a,
    h3,
    ul {
      margin: 0 0 10px 0;

      @include mixins.screen(lg) {
        margin-top: 0;
        margin-bottom: 10px;
      }
    }

    a {
      color: colors.$pen-mid-grey;
      font-family: fonts.$font-footer-body;
      font-size: 13px;
      font-style: normal;
      font-weight: 400;
      line-height: 24px;
    }

    ul {
      margin-right: 4px;
    }
  }

  li,
  p {
    font-size: 13px;
    color: colors.$pen-light-grey;
    letter-spacing: 0;
    line-height: 20px;
  }

  li {
    display: inline-block;
    margin-right: 10px;
  }

  #languageFooter {
    @include mixins.screen(md, true) {
      li {
        display: block;
      }

      a {
        color: colors.$pen-light-gold;
        font-size: 13px;
      }
    }
  }

  .footerLanguageSelector a {
    color: colors.$pen-gold;

    &:hover {
      text-decoration: underline;
    }
  }
}

.footerLanguageSelector {
  h3 {
    font-family: fonts.$font-footer-body;
    margin-right: 20px;
    font-size: 13px;
    line-height: 24px;
    letter-spacing: 0.65px;
  }

  li {
    font-size: 13px;
    line-height: 24px;
  }
}


.footerCookieLinksContainer {
    @include mixins.screen (md, true) {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0;
    margin-bottom: 12px;
    text-align: center;
    gap: 10px 0;
  }
}

.footerLangToggle {
  @include mixins.screen (md) {
    display: none;
  }

  &.mobileAccordion {
    border-top: 0;
  }

  button {
    border-bottom: 1px solid #dbdbdb !important;
  }

  li {
    width: 100%;

    >a {
      text-align: start;
      color: colors.$pen-darker-grey;
      margin: 0;
    }
  }
}

