@use "@/styles/_colors.scss" as *;
@use "@/styles/_mixins.scss" as *;
@use "@/styles/_fonts.scss" as *;

// Main container
:global(.pressList){
  background-color: #fff;
  .wrapper {  
    padding-top: 80px;
    padding-bottom: 100px;
    @include screen(md, true) {
      padding-top: 30px;
      margin-bottom:70px;
    }

    // @include responsiveWidth();
    @include responsivePadding();
    
    h1{
      font-family: $font-main-display-large;
      font-weight: 400;
      font-size: 58px;
      line-height: 65px;
      letter-spacing: 0%;
      text-align: center;
      text-transform: uppercase;
      color: $origin-black;
      margin-bottom: 30px;

      @include screen(md, true) {
        font-size: 32px;
        line-height: 36px;
        margin-bottom: 20px;
      }
    }

    :global(.pressSelectCategories){
      :global(.react-dropdown-select-content){
      >span {
        text-transform: uppercase;
      }
    }
    }
    .selectCat{
      padding-bottom: 20px;
      display: flex;
      flex-flow: row nowrap;
      justify-content: stretch;
      align-items: center;
      margin-bottom: 50px;
      border-bottom: 1px solid $pen-border-grey;

      @include screen(md, true) {
        padding-bottom: 0;
        margin-bottom: 30px;
        border-bottom: none;
      }
      h2{
        display: block;
        flex-grow: 1;
        font-family: $font-main-display-large;
        font-weight: 400;
        font-size: 24px;
        line-height: 44px;
        letter-spacing: 0%;
        text-transform: uppercase;
        color: $origin-black;

        @include screen(md, true) {
          display: none;
        }
      }
      >div{
        width: auto;
        @include screen(md, true) {
          width: 100%;
        }
      }
    }
    .pressSubList{
      display: flex;
      flex-flow: column nowrap;
      gap: 50px;
      align-items: center;

      @include screen(md, true) {
        gap: 20.61px;
      }
      >.feeds{
        width: 100%;
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 50px 21px;
      @include screen(md, true) {
        grid-template-columns: 1fr;
      }
      }
      .loadMoreBtn{
        display: inline-block;
        cursor: pointer;
        padding-top: 60px;
        font-family: $font-main-display;
        font-weight: 400;
        font-size: 14px;
        line-height: 100%;
        letter-spacing: 5%;
        text-align: center;
        text-transform: uppercase;
        color: $pen-gold;
        text-align: center;
        position: relative;
        &:before{
          content: "";
          display: block;
          width: 1px;
          height: 40px;
          top: 0;
          left: calc(50% - .5px);
          background-color: $pen-gold;
          position: absolute;
        }

        &:hover{
          text-decoration: underline;
        }
      }
    }
  }
}