@use "sass:map";

@use "./_variables.scss" as *;
@use "./colors.scss" as *;

// Breakpoint variables
$screen-xxs: 376px !default; // Small mobile
$screen-xs: 576px !default; // Mobile
$screen-sm: 768px !default; // Tablet
$screen-md: 1024px !default; // Small desktop
$screen-sl: 1024px !default; // special large size(1024px)
$screen-lg: 1200px !default; // Desktop
$screen-xl: 1440px !default; // Large desktop
$max-width: 1600px !default; // Max content width

@mixin screen($screenSize: null, $reverse: null) {
    @if $reverse ==null {
        @if ($screenSize ==xxs) {
            @media screen and (min-width: #{$screen-xxs}) {
                @content;
            }
        }

        @if ($screenSize ==xs) {
            @media screen and (min-width: #{$screen-xs}) {
                @content;
            }
        }

        @if ($screenSize ==sm) {
            @media screen and (min-width: #{$screen-sm}) {
                @content;
            }
        }

        @if ($screenSize ==md) {
            @media screen and (min-width: #{$screen-md}) {
                @content;
            }
        }

        @if ($screenSize ==lg) {
            @media screen and (min-width: #{$screen-lg}) {
                @content;
            }
        }

        @if ($screenSize ==sl) {
            @media screen and (min-width: #{$screen-sl}) {
                @content;
            }
        }

        @if ($screenSize ==xl) {
            @media screen and (min-width: #{$screen-xl}) {
                @content;
            }
        }

        @if ($screenSize ==mw) {
            @media screen and (min-width: #{$max-width}) {
                @content;
            }
        }
    }

    // Reversed breakpoints (max-width)
    @if $reverse ==true {
        @if ($screenSize ==xxs) {
            @media screen and (max-width: #{$screen-xxs - 1}) {
                @content;
            }
        }

        @if ($screenSize ==xs) {
            @media screen and (max-width: #{$screen-xs - 1}) {
                @content;
            }
        }

        @if ($screenSize ==sm) {
            @media screen and (max-width: #{$screen-sm - 1}) {
                @content;
            }
        }

        @if ($screenSize ==md) {
            @media screen and (max-width: #{$screen-md - 1}) {
                @content;
            }
        }

        @if ($screenSize ==sl) {
            @media screen and (max-width: #{$screen-sl - 1}) {
                @content;
            }
        }

        @if ($screenSize ==lg) {
            @media screen and (max-width: #{$screen-lg - 1}) {
                @content;
            }
        }

        @if ($screenSize ==xl) {
            @media screen and (max-width: #{$screen-xl - 1}) {
                @content;
            }
        }

        @if ($screenSize ==mw) {
            @media screen and (max-width: #{$max-width - 1}) {
                @content;
            }
        }
    }
}

@mixin tablet-only() {
    @media screen and (min-width: #{$screen-sm}) and (max-width: #{$screen-md - 0.5}) {
        @content;
    }
}

@mixin mobile-only() {
    @media screen and (max-width: #{$screen-sm - 1}) {
        @content;
    }
}

@mixin desktop-only() {
    @media screen and (min-width: #{$screen-md}) {
        @content;
    }
}

@mixin animation-arrow-cta-link {

    .icon-right,
    .icon-arrow-right,
    .icon-arrow-left {
        transition: $animation-hover-speed transform;
    }

    &:hover {
        .icon-right,
        .icon-arrow-right {
            transform: translate(.5rem, 0);
        }

        .icon-arrow-left {
            transform: translate(-.5rem, 0);
        }
    }
}

@mixin outline {
    outline: 2px solid $blue-outline-light;
    outline-offset: -2px;

    @include firefox-only {
        outline: 2px solid $blue;
    }
}


@mixin firefox-only {

    /* stylelint-disable */
    @-moz-document url-prefix() {
        @content;
    }

    /* stylelint-ensable */
}

@mixin y-padding($padding) {
    padding-top: $padding;
    padding-bottom: $padding;
}

//screen reader (aria visible)--
@mixin sr-only {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    white-space: nowrap;
    border-width: 0;
}

@mixin not-sr-only {
    position: static;
    width: auto;
    height: auto;
    padding: 0;
    margin: 0;
    overflow: visible;
    clip: auto;
    white-space: normal;
}

//[end ]screen reader (aria visible)--

@mixin custom-scrollbar {

    // Hide scrollbar buttons
    &::-webkit-scrollbar {
        width: 8px;
    }

    &::-webkit-scrollbar-button {
        display: none;
        height: 0;
        width: 0;
    }

    &::-webkit-scrollbar-track {
        background: $pen-slick-grey;
    }

    &::-webkit-scrollbar-thumb {
        background-color: $pen-light-gold;
        border-radius: 4px;
        border: 2px solid $pen-light-gold;
        background-clip: content-box;
    }

    // For Firefox
    scrollbar-width: thin;
    scrollbar-color: $pen-light-gold $pen-slick-grey;
}


@mixin responsiveWidth(
    // $maxWidth: 1280px,
    $maxWidth: 100%,
    $margin: 0 auto,
) {
    & {
        max-width: $maxWidth;
        margin: $margin;
    }
}

@mixin responsivePadding(
    $padding-x: 80px,
    $padding-x-m: 20px,
){
    // @media screen and (max-width: 1440px) {
    & {
        padding-left: $padding-x;
        padding-right: $padding-x;
    }

    @include screen(sm, true) {
        padding-left: $padding-x-m;
        padding-right: $padding-x-m;
    }
}

@mixin descFontSize {
    font-size: 18px;
    line-height: 28px;

    @include screen(md, true){
        font-size: 16px;
        line-height: 24px;
    }
}

@mixin applyFont($default, $CN) {
    font-family: $default;

    &:lang(zh-HK),
    &:lang(zh-TW),
    &:lang(zh-Hant) {
        font-family: $CN;
    }
}

@mixin generalPaddingX {
    padding-left: 160px;
    padding-right: 160px;

    @include screen(xl, true) {
        padding-left: 80px;
        padding-right: 80px;
    }

    @include screen(sl, true) {
        padding-left: 60px;
        padding-right: 60px;
    }

    @include screen(md, true) {
        padding-left: 30px;
        padding-right: 30px;
    }
}

@mixin title-styles($font-main-display-large, $font-cn-large) {
    font-weight: 400;
    letter-spacing: 10%;
    text-transform: uppercase;
    text-align: center;
    margin: 0 20px;
    @include applyFont($font-main-display-large, $font-cn-large);
    color: black;
    font-size: 36px;
    line-height: 44px;

    :global(.digits-data) {
        font-size: 40.5px;
        &:lang(zh-HK) {
            font-size: 38.5px;
        }
        @include screen(md, true) {
            font-size: 26.5px;
            &:lang(zh-HK) {
                font-size: 28.5px;
            }
        }
    }

    @include screen(md, true) {
        font-size: 24px;
        line-height: 30px;
    }
}
