@use '@/styles/_mixins.scss' as *;
@use '@/styles/_colors.scss' as *;
@use '@/styles/_fonts.scss' as *;

$mobile-shift-top: -40px;
$shift-left: 6%;

.contentContainer {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    padding-left: 50px;
}

.mainTitle {
    font-weight: 400;
    text-align: center;
    text-transform: uppercase;
    margin-bottom: 100px;
    color: $origin-black;

    @include screen(md, true) {
        font-size: 28px;
        line-height: 28px;
        letter-spacing: 0%;
        @include applyFont($font-main-display-large, $font-cn-large);
    }
}

.imgWrapper {
    flex: 6;
    line-height: 0;
    position: relative;
    top: -50px;

    img {
        width: 100%;
        height: auto;
        object-fit: contain;
        aspect-ratio: 738 / 496;
    }

    @include screen(md, true) {
        flex: 1;
        top: -60px;
    }
}

.txtContainer {
    flex: 4;

    @include screen(md, true) {
        flex: 1;
    }
}

.txtWrapper {
    position: relative;
    padding-left: 60px;
    padding-top: 40px;

    @media screen and (max-width: 1079px) {
        padding-left: 30px;
    }

    @include screen(md, true) {
        padding: 0;
        top: $mobile-shift-top;
    }
}

.contentTitle {
    font-weight: 400;
    // hsh-178
    font-size: 32px;
    line-height: 36px;
    letter-spacing: 1px;
    text-transform: uppercase;
    margin-bottom: 14px;

    @include applyFont($font-main-display-large, $font-cn-large);

    @media screen and (max-width: 1079px) {
        margin-bottom: 8px;
    }

    @include screen (md, true) {
        margin-bottom: 14px;
    }
}

.locationMark {
    font-size: 16px;
    line-height: 24px;
    letter-spacing: 0%;
    margin-bottom: 18px;

    @media screen and (max-width: 1079px) {
        margin-bottom: 8px;
    }

    @include screen (md, true) {
        margin-bottom: 18px;
    }

    svg {
        position: relative;
        top: 1px;
        margin-right: 4px;
    }
}


.desc {
    position: absolute;
    overflow-y: auto;
    width: calc(100% - 80px);
    text-align: start;
    font-weight: 400;
    letter-spacing: 0%;
    max-height: 24vw;
    padding-right: 10px;
    box-sizing: border-box;
    @include descFontSize();

    @include screen(xl, true) {
        max-height: 240px;
    }

    @include screen(lg, true) {
        max-height: 180px;
    }

    @include screen(md, true) {
        position: static;
        max-height: unset;
        width: 100%;
    }
}

.carouselContainer {
    position: relative;
    width: calc(100% - $shift-left);
    margin: 0 auto;
    margin-left: $shift-left;

    @include screen(md, true) {
        margin: 0;
        width: 100%;
        flex-direction: column;
        padding: 0 50px;
        box-sizing: border-box;
    }

    @include screen(sm, true) {
        padding: 0 20px;
    }

    :global {

        .slick-list {
            position: relative;
            display: block;
            overflow: visible;
            margin: 0;
            padding: 0 50px;
            overflow-x: clip;

            @include screen(md, true) {
                padding: 0;
            }
        }

        .slick-prev {
            left: -3.5%;
        }

        .slick-next {
            right: 6.0%;

            @media screen and (min-width: 1920px) {
                right: 8.0%;
            }
        }

        .slick-prev,
        .slick-next {
            @include screen(lg, true) {
                display: none;
            }
        }
    }
}

.carouselControls {
    display: flex;
    align-items: center;
    gap: 1.2rem;
    padding: 1rem 1.5rem;
    width: fit-content;
    position: relative;
    box-sizing: border-box;
    left: 50%;
    transform: translateX(-50%);
    z-index: 10;

    @include screen(md, true) {
        top: $mobile-shift-top;
    }

    @include screen(xs, true) {
        width: 100%;
    }
}

.playPauseBtn {
    background: none;
    border: none;
    color: $pen-light-gold;
    cursor: pointer;
    padding: 0.5rem;
    display: flex;
    align-items: center;
    transition: transform 0.2s ease;
    border-radius: 50%;
    width: 3.2rem;
    height: 3.2rem;
    justify-content: center;


    &:hover {
        transform: scale(1.1);
        background: rgba(255, 255, 255, 0.1);
    }

    &:active {
        transform: scale(0.95);
    }

    svg {
        width: 2rem;
        height: 2rem;
        fill: currentColor;
    }
}

.progressContainer {
    flex-grow: 1;
    height: 0.2rem;
    background: $pen-slick-grey;
    min-width: 30rem;
    cursor: default;
    position: relative;
    overflow: hidden;

    @include screen(xs, true) {
        min-width: auto;
        width: 100%;
    }
}

.progressBar {
    height: 100%;
    background: $pen-light-gold;
    transition: width 0.1s linear;
    position: absolute;
    left: 0;
    top: 0;
}

.slideCounter {
    color: $pen-mid-grey;
    font-weight: 500;
    font-size: 1rem;
    line-height: 1;
    letter-spacing: 2px;
    text-transform: uppercase;
}