@use '@/styles/_mixins.scss' as *;
@use '@/styles/_colors.scss' as *;
@use '@/styles/_fonts.scss' as fonts;

.bannerCarousel{
    >div{

        .titleBlock{
            padding-top: 80px;
            margin-bottom: 50px;
            text-align: center;
            @include screen(md, true) {
                padding-top: 30px;
            }

            h1{
                font-weight: 400;
                margin-bottom: 15px;   
            }
            .desc{
                font-family: fonts.$font-main-display;
                font-weight: 400;
                letter-spacing: 0%;
                text-align: center;
                color: $pen-dark-grey;
                @include descFontSize();
            }

        }

        
    
        :global(.press-slider){
            :global(.slick-slider){
                :global(.slick-arrow){
                    @include screen(md, true) {
                        top: calc(37.5vw - 22.5px);
                    }
                }
                :global(.slideBody){
                    position: relative;
                    padding-bottom: 104px !important;
                    @include screen(md, true) {
                        padding: 0 20px 0 !important;
   
                    }
                    :global(.featureBtns){
                        position: absolute;
                        bottom: 124px;
                        right: 45px;
                        @include screen(md, true) {
                            display: none;
                        }
                    }
                    img{
                        display: block;
                        object-fit: cover;         /* Crops to fill container */
                        object-position: center; 
                        width: 100%;
                        height: auto;
                        
                        @include screen(md, true) {
                            height: 75vw;
                        }
                    }
                    .textBox{
                        box-sizing: border-box;
                        position: absolute;
                        width: calc(100% - 210px);
                        max-width: 715px;
                        height: 209px;
                        left:50%;
                        transform: translateX(-50%);
                        bottom: 0;
                        z-index: 1;
                        background-color: #fff;
                        padding: 30px 52px;
                        display: flex;
                        flex-flow: column nowrap;
                        justify-content: center;
                        align-items: center;
                        text-align: center;

                        @include screen(md, true) {
                            padding: 25px 45px 41px;
                            width: 100%;
                            max-width: none;
                            left:auto;
                            position: relative;
                            height: auto;
                            transform: none;
                        }

                        >.date{
                            font-family: fonts.$font-main-display;
                            font-weight: 400;
                            font-size: 12px;
                            line-height: 24px;
                            letter-spacing: 0%;
                            text-align: center;
                            margin-bottom: 5px;
                            color: $pen-gold;

                            @include screen(md, true) {
                                margin-bottom: 4px;
                            }
                        }
                        h4{
                            font-family: fonts.$font-main-display;
                            font-weight: 400;
                            font-size: 18px;
                            line-height: 28px;
                            letter-spacing: 0%;
                            text-align: center;
                            color: $origin-black;
                            margin-bottom: 10px;

                            @include screen(md, true) {
                                font-size: 16px;
                                line-height: 24px;
                            }
                        }
                        .date + h4{
                            font-family: fonts.$font-main-display;
                            font-weight: 400;
                            font-size: 16px;
                            line-height: 24px;
                            letter-spacing: 0%;
                            text-align: center;
                            margin-bottom: 18px;
                        }
                        :global(.richText){
                            font-family: fonts.$font-main-display;
                            font-weight: 400;
                            font-size: 16px;
                            line-height: 24px;
                            letter-spacing: 0%;
                            text-align: center;
                            color: $pen-dark-grey;

                            @include screen(md, true) {
                                font-size: 14px;
                            }
                        }
                        .cta{
                            min-width: auto;
                            min-height: auto;
                            >span{
                                display: flex;
                                flex-flow: row nowrap;
                                white-space: nowrap;
                                align-items:center;
                                width: 100%;
                                gap: 8px;
                                .label{
                            font-family: fonts.$font-main-display;
                                    font-weight: 400;
                                    font-size: 14px;
                                    line-height: 100%;
                                    letter-spacing: 5%;
                                    text-transform: uppercase;
                                    padding: 6.5px 0;
                                    display: block;
                                }
                                :global(.gotoSvg){
                                    width: 24px;
                                    display: flex;
                                    align-items:center;
                                    svg{
                                        width: 24px;
                                        height: 24px;
                                    }
                                }
                            }
                        }
                        .badge{
                            padding: 5px 10px;
                            border: 1px solid $pen-gold;
                            font-family: fonts.$font-main-display;
                            font-weight: 400;
                            font-size: 12px;
                            line-height: 100%;
                            letter-spacing: 0%;
                            color: $pen-gold;
                            cursor: pointer;
                        }
                    }
                }
            }
        }
    }
}