@use "./_colors.scss" as *;
@use "./_fonts.scss" as *;
@use "./_typography.scss" as *;
@use "./_mixins.scss" as *;
@use "./_variables.scss" as *;
@use "./_slick.scss" as *;

// server components
@use "./components/HshThreeTiles.scss" as *;
@use "./components/HshTopIntro.scss" as *;
@use "./components/HshAnchorBanner.scss" as *;
@use "./components/HshCommitListing.scss" as *;
@use "./components/HshRedirectBtn.scss" as *;
@use "./components/HshBrandLogo.scss" as *;
@use "./components/HshHomeStat.scss" as *;
@use "./components/HshBusinessDetail.scss" as *;
@use "./components/HshInlineCta.scss" as *;

@use "./components/FullWidthImgNVideo.scss" as *;

// Modal
@use "./components/committeeModal.scss" as *;

// Mapbox
@use "./components/HshMapbox.scss" as *;

html {
    font-size: 10px;
}

body {
    margin: 0;

    &:lang(zh-HK),
    &:lang(zh-TW),
    &:lang(zh-Hant) {
        font-family: $font-cn;
        letter-spacing: 0.13em;

        button {
            font-family: $font-cn;
        }
    }
}

section {
    &.inner {
        max-width: 1280px;
        margin: 0 auto;
    }

    @for $i from 0 through 200 {
        &.y-padding-#{$i} {
            @include y-padding(#{$i}px);
        }
    }

    &.y-standard {
        @include y-padding(80px);

        @include screen(md, true) {
            @include y-padding(50px);
        }
    }
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin-block-start: 0;
    margin-block-end: 0;
}

li,
ol,
ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

a {
    color: $pen-gold;
    text-decoration: none;

    &:focus-visible {
        @include outline;
    }
}

button {
    /* Remove all default styles */
    background: none;
    border: none;
    padding: 0;
    margin: 0;
    font: inherit;
    color: inherit;
    cursor: pointer;
    outline: none;

    /* Optional: Remove platform-specific styling */
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
}

.media {
    position: relative;
}

.subPage-padding {
    padding-bottom: 80px;

    @media screen and (max-width: 1290px) {
        padding-bottom: 60px;
    }
}

#PdfReports {
    section.y-standard {
        @include screen(md, true) {
            padding-top: 30px;
        }
    }
}

video {
    width: 100%;
    height: auto;
}

.videoPlayer {}

.videoPlayer:not(.playing) {
    /* Hide individual control elements (optional) */
    // &::-webkit-media-controls-play-button,
    // &::-webkit-media-controls-timeline,
    // &::-webkit-media-controls-current-time-display,
    // &::-webkit-media-controls-time-remaining-display,
    // &::-webkit-media-controls-mute-button,
    // &::-webkit-media-controls-volume-slider,
    // &::-webkit-media-controls-fullscreen-button,
    // &::-webkit-media-controls-enclosure,

    &::-webkit-media-controls {
        display: none !important;
    }
}

.digits-data {
    font-family: $font-main-display-small;
    position: relative;
    // top: .1em;
}

.hover-link {
    cursor: pointer;
    &:hover {
        color: $pen-gold !important;
    }
}

.AccordionArrowSvg {
    transition: 0.3s;
}

.richText {
    font-weight: 400;
    line-height: 28px;
    letter-spacing: 0;
    text-align: center;
    color: $pen-dark-grey;

    @include applyFont($font-main-display, $font-cn);
    @include descFontSize();

    a {

        &:hover,
        &:focus {
            text-decoration: underline;
        }
    }

    em {
        font-style: italic;
    }

    p {
        display: inline;
    }

    li {
        text-align: start;
    }
}

.theme-white {
    background-color: $origin-white;
}

.theme-dark {
    background-color: $pen-dark;
}

.theme-border-grey {
    background-color: $pen-border-grey;
}

.theme-grey2 {
    background-color: $background-grey2;
}

.theme-beige {
    background-color: $beige-bg;
}

.preview-board {

    &.theme-beige,
    &.theme-grey2 {

        .view-listing,
        .accordion-listing {
            border-top: 1px $origin-black solid;
            gap: 0;
        }
    }
}

.d-flex {
    display: flex;
}

.w-full {
    width: 100%;
}

.h-full {
    height: 100%;
}

.y-scroll {
    overflow-y: scroll;
}


.DevPlaceholder {
    box-sizing: border-box;
    width: 100%;
    padding: 80px 30px;
    text-align: center;
    background-color: #eee;
    border: #999 1px dotted;
}

// Enhancement: put below to a separate scss file
// for /Corporate-Governance/Our-code-of-conduct or most of the cshtml richtext components
.rich-editor-box {
    color: $pen-dark-grey;
    font-size: 16px;

    max-width: 900px;
    margin: 0 auto;

    @include screen(md, true) {
        max-width: none;
        padding: 0 20px;
    }

    ul,
    ol,
    li {
        list-style: inherit;
        list-style-type: disc;
    }

    ul {
        // for bullet points
        margin-left: 30px;
    }

    li {
        text-align: start;
    }

    p,
    li,
    div {
        color: $pen-dark-grey;
        font-size: 16px;
    }

    span {
        color: inherit;
    }

    em {
        // requested by pm
        font-style: normal;
    }

    a {
        text-decoration: underline;
    }
}