@use "@/styles/_colors.scss" as *;
@use "@/styles/_mixins.scss" as *;
@use "@/styles/_fonts.scss" as *;

// Main container
:global(.pressLatestNews){
  .wrapper {  
    text-align: center;
    padding-top: 80px;
    padding-bottom:80px;

    @include screen(md, true) {
      padding-top: 50px;
      padding-bottom:50px;
    }

    h2{
      font-family: $font-main-display-large;
      font-weight: 400;
      font-size: 36px;
      line-height: 44px;
      letter-spacing: 1px;
      text-transform: uppercase;
      color: $origin-black;
      margin-bottom: 20px;
      text-align: center;
      @include screen(md, true) {
        padding-bottom:9px;
      }
    }

    .desktopSlide{
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 20px;
      @include screen(md, true) {
        display: none;
      }
    }

    .desktopSlider{
      display: block;
      @include screen(md, true) {
        display: none;
      }
    }

    .mobileSlider{
      display: none;
      @include screen(md, true) {
        display: block;
      }
    }

    .newsSlider{
      margin-bottom: 50px;
      @include screen(md, true) {
        padding-bottom:30px;
      }

      :global(.slick-slider){
        :global(.slick-arrow){
          &:global(.slick-prev){
            display: none !important;
          }
          &:global(.slick-next){
            right: 30px;
            @include screen(md, true) {
              right: 0;
            }
          }
        }
        :global(.slick-list){
          :global(.slick-track){
            align-items: stretch;
            :global(.slick-slide){
              height: auto;
              display: flex;
              flex-direction: row;
              align-items: stretch;

              >div{
                flex-grow: 1;
                display: flex;
                flex-direction: row;
                align-items: stretch;
                
                :global(.slideBody){
                  width: 100%;
                  max-width: none;
                  padding: 0 10px;
                }
              }
            }
          }
        }
      }
    }

    .cta{
      display: inline-block;
    }
  }
}