@use '@/styles/_colors.scss' as *;
@use '@/styles/_mixins.scss' as *;

$mobile-shift-top: -40px;
$shift-left: 6%;

.topBannerWrapper {
    line-height: 0;
    position: relative;

    :global(.slick-list) {
        @include screen(md) {
            max-height: calc(52vw - 80px);
        }
    }
}

.imageWrapper {
    line-height: 0;
    position: relative;

    &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.2);
    }
}

.pagiContainer {
    position: absolute;
    bottom: 40px;
    display: flex;
    width: 100%;
    padding: 0 80px;
    gap: 0 20px;
    box-sizing: border-box;
    align-items: flex-end;
    opacity: 1;

    >li {
        display: flex;
        flex: 1;
    }

    &:hover {
        opacity: 1;
    }

    @include screen(md, true) {
        display: none;
    }
}

.mobilePagiContainer {
    position: absolute;
    bottom: 20px;
    opacity: 1;
    width: 100%;
    padding: 0 10px;
    display: flex;
    justify-content: center;
    z-index: 30;
    box-sizing: border-box;
}

.mobilePagi {
    background-color: rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(15px);
    -webkit-backdrop-filter: blur(15px);
    padding: 10px 20px;
    border-radius: 30px;
    gap: 0 5px;
    display: flex;
}

.pagi {
    flex: 1;
}

.carouselControls {
    display: flex;
    align-items: center;
    gap: 1.2rem;
    padding: 1rem 1.5rem;
    width: fit-content;
    position: relative;
    box-sizing: border-box;
    left: 50%;
    transform: translateX(-50%);
    z-index: 10;

    @include screen(md, true) {
        top: $mobile-shift-top;
    }

    @include screen(xs, true) {
        width: 100%;
    }
}

.pagiLabel {
    padding-right: 20px;
    white-space: pre-line;
    font-weight: 500;
    font-size: 16px;
    line-height: 26px;
    letter-spacing: 0%;
    color: $banner-inactive-text;
    margin-bottom: 20px;
}

.current {
    .pagiLabel {
        color: $origin-white;
    }
}

.progressContainer {
    flex-grow: 1;
    height: 0.2rem;
    background: $banner-inactive-line;
    cursor: default;
    position: relative;
    overflow: hidden;

    @include screen(xs, true) {
        min-width: auto;
        width: 100%;
    }
}

.bannerPagi {
    flex: 1;
    cursor: pointer;
}

.progressBar {
    height: 100%;
    background: $origin-white;
    transition: width 0.1s linear;
    position: absolute;
    left: 0;
    top: 0;
}

.image {
    width: 100%;
    height: auto;
    max-height: 52vw;
    object-fit: cover;

    img {
        width: 100%;
        height: auto;
        max-height: 52vw;
        object-fit: cover;
    }

    video::-webkit-media-controls {
        display: none !important;
    }

    [class*="playBtn"] {
        display: none;
    }

    @include screen(md, true) {
        max-height: none;
        height: 70vh;

        img {
            max-height: none;
            height: 70vh;
        }

        video {
            // for video have effect like (object-fit:cover) when apply height: 70vh;
            position: absolute;
            top: 50%;
            left: 50%;
            min-width: 100%;
            min-height: 100%;
            width: auto;
            height: auto;
            transform: translate(-50%, -50%);
        }
    }
}

.textWrapper {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.textContainer {
    position: relative;
    z-index: 10;
}

.title {
    margin: 0;
    margin-bottom: 10px;
    font-weight: 400;
    line-height: 60px;
    text-transform: uppercase;

    @include screen(md, true) {
        font-size: 42px;
        line-height: 45px;
        letter-spacing: 0%;
        white-space: normal;
        word-wrap: break-word;
        overflow-wrap: break-word;
    }

    @include screen(xs, true) {
        box-sizing: border-box;
        padding: 0 15px;
    }
}

.desc {
    width: 700px;
    @include descFontSize();
}

.title,
.desc {
    color: $origin-white !important;
    text-align: center;
    width: 100%;
}