@use "@/styles/_colors.scss" as *;
@use "@/styles/_mixins.scss" as *;
@use "@/styles/_fonts.scss" as *;

// Main container
:global(.statistics) {
  .wrapper {
    padding-top: 0;
    padding-bottom: 82px;

    @include screen(md, true) {
      padding-top: 0;
      padding-bottom: 32px;
    }

    // @include responsiveWidth();
    @include responsivePadding();

    @mixin data-block {
      h3 {
        font-family: $font-main-display-large;
        font-weight: 400;
        font-size: 18px;
        line-height: 30px;
        letter-spacing: 0%;
        text-align: center;
        color: $origin-black;

        &:lang(zh-HK),
        &:lang(zh-TW),
        &:lang(zh-Hant) {
          font-family: $font-cn-large;
          font-size: 18px;
          line-height: 30px;
          letter-spacing: 2px;
        }
      }

      .value {
        font-family: $font-main-display-small;
        font-weight: 400;
        font-size: 56px;
        line-height: 44px;
        letter-spacing: 0%;
        text-align: center;
        text-transform: uppercase;
        color: $pen-light-gold;
      }

      :global(.richText) {
        font-weight: 400;
        font-size: 12px;
        line-height: 22px;
        letter-spacing: 0%;
        text-align: center;
        margin: 0 auto;
        max-width: 256px;
        color: $pen-mid-grey;

        &:lang(zh-HK),
        &:lang(zh-TW),
        &:lang(zh-Hant) {
          font-size: 14px;
          line-height: 30px;
        }
      }
    }

    .desktopBlock {
      display: flex;
      flex-flow: row wrap;
      justify-content: center;
      gap: 50px;

      >.dataBlock {
        width: 50%;
        max-width: 336px;
        position: relative;
        text-align: center;

        &:not(:last-child):after {
          content: "";
          display: block;
          position: absolute;
          top: 0;
          right: -24.5px;
          width: 1px;
          height: 100%;
          background-color: $pen-border-grey;
        }

        @include data-block;

        h3 {
          margin-bottom: 31px;
        }

        .value {
          margin-bottom: 24px;
        }
      }

      @include screen(md, true) {
        // display: none;
        @include sr-only;
      }
    }

    .mobileSlider {
      display: none;

      @include screen(md, true) {
        display: block;
      }

      :global(.slideBody) {
        width: 100vw;
        max-width: none;

        .dataBlock {
          text-align: center;
          @include data-block;

          h3 {
            margin-bottom: 20px;
          }

          .value {
            margin-bottom: 20px;
          }
        }
      }
    }
  }
}