@use '@/styles/_mixins.scss' as *;
@use '@/styles/_colors.scss' as *;

.pressCarousel{
    
    :global(.press-slider){
            padding-top: 50px;
        @include screen(md, true) {
            padding-top: 30px;
        }
        :global(.slick-slider){
            :global(.slick-arrow){
                @include screen(md, true) {
                    top: calc(37.5vw - 22.5px);
                }
            }
            :global(.slideBody){
                position: relative;
                :global(.featureBtns){
                    position: absolute;
                    bottom: 20px;
                    right: 45px;
                    @include screen(md, true) {
                        top: Calc(75vw - 60px);
                        right: 50%;
                        transform: translateX(50%);
                    }
                    &.showTxt{
                        display: none;
                        @include screen(md, true) {
                            display: flex;
                            :global(.info){
                                background-color: #fff;
                                cursor: default;
                                svg{
                                    path{
                                        fill: $pen-gold;
                                        stroke: $pen-gold;
                                    }
                                }
                            }

                        }
                    }
                }
                img{
                    display: block;
                    object-fit: cover;         /* Crops to fill container */
                    object-position: center; 
                    width: 100%;
                    height: auto;
                    
                    @include screen(md, true) {
                        height: 75vw;
                    }
                }
                .desc{
                    position: absolute;
                    width: auto;
                    height: auto;
                    max-height: 65%;
            
                    left: 25px;
                    right: 25px;
                    background-color: rgba($origin-black, .8);
                    
                    padding: 49px 0 0 42px;
                    display: flex;
                    align-items: stretch;
                    bottom: 0;
                    transform: translateY(100%);
                    transition: transform .3s;

                    &:global(.show){
                    transform: none; 
                    }

                    :global(.closeBtn){
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        position: absolute;
                        width: 24px;
                        height: 24px;
                        cursor: pointer;
                        right: 20px;
                        top: 20px;
                        >svg{
                            width: 13.79px;
                            height: 13.79px;
                        }
                    }
                    :global(.text){
                        width: 100%;
                        height: auto;
                        overflow-x: hidden;
                        overflow-y: auto;
                        @include custom-scrollbar;
                        padding-bottom: 37px;
    
                        >:global(.richText ){
                            padding-right: 41px;
                            color: #fff;
                            font-family: Futura Std;
                            font-weight: 400;
                            font-style: Book;
                            leading-trim: NONE;
                            letter-spacing: 0%;
                            text-align: left;
                            @include descFontSize();
                        }
                    }

                    @include screen(md, true) {
                        display: none;
                        position: relative;
                        max-height: none;
            
                        left: auto;
                        right: auto;
                        background-color: $pen-dark;
                        transform: none;

                        &:global(.show){
                            display: block; 
                        }

                    }
                }
            }
        }

        &.singleMode{
            padding-bottom: 20px;
            @include screen(md, true) {
                padding-bottom: 0;
            }
            :global(.slick-slider){
                :global(.slick-list){
                    display: flex;
                    justify-content: center;
                    :global(.slick-track){
                        width: auto !important;
                    }
                }
            }
        }
    }
}
