@use '@/styles/_mixins.scss' as *;
@use '@/styles/_colors.scss' as *;
@use '@/styles/_fonts.scss' as *;

.title {
    font-weight: 400;
    letter-spacing: 10%;
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 20px;

    @include screen(md,true) {
        font-size: 24px;
        line-height: 30px;

        @include applyFont($font-main-display-large, $font-cn-large);
    }
}

.linksContainer {
    max-width: 900px;
    margin: 0 auto;

    @include screen(md, true){
        padding: 0 20px;
    }
}

.rowP {
    padding: 26px 40px;
    box-sizing: border-box;

    @include screen(md, true) {
        padding: 23px 20px;
    }
}