@use '@/styles/_mixins.scss' as *;
@use '@/styles/_colors.scss' as *;

.sliderContainer{
    :global(.slick-slider){
        :global(.slick-arrow){
            width: 50px;
            height: 50px;
            background-color: $background-grey2;
            z-index: 10;
            display: flex !important;
            align-items: center;
            justify-content: center;
            top: calc(50% - 25px);

            &:before{
                content:none;
            }
            &:global(.slick-prev){
                left:calc( 50% - 597px);
            }
            &:global(.slick-next){
                right:calc( 50% - 597px);
            }
            >svg{
                width: 20.69px;
                height: 20.69px;
                display: block;
            }

            @include screen(xl, true) {
                &:global(.slick-prev){
                    left:9.5vw;
                }
                &:global(.slick-next){
                    right:9.5vw;
                }
            }
            @include screen(lg, true) {
                &:global(.slick-prev){
                    left:8.5vw;
                }
                &:global(.slick-next){
                    right:8.5vw;
                }
            }
            @include screen(md, true) {
                top: calc(50% - 22.5px);
                width: 45px;
                height: 45px;
                >svg{
                    width: 18.62px;
                    height: 18.62px;
                }
                &:global(.slick-prev){
                    left:0;
                }
                &:global(.slick-next){
                    right:0;
                }
            }
        }
        :global(.slick-list){
            :global(.slick-track){
                display: flex;
                :global(.slick-slide){
                    >div{
                        .slideBody{
                            box-sizing: border-box;
                            width: 74vw; 
                            max-width:1114px;
                            padding: 0 25px;

                            @include screen(md, true) {
                                padding: 0;
                                width: 100%;
                            }
                        }
                    }
                }
            }
        }
        :global(.slick-dots){
            bottom: 0;
        }
    }
}
