@use "@/styles/_colors.scss" as *;
@use "@/styles/_mixins.scss" as *;
@use "@/styles/_fonts.scss" as *;



.wrapper {  
    padding-top: 55px;
    padding-bottom:82px;

    background-color: #f9f9f9;

    @include screen(md, true) {
        padding-top: 30px;
        padding-bottom:32px;
    }

}


.title {
    font-weight: 400;
    letter-spacing: 0.01em;
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 20px;
    font-family: $font-main-display-large;
    color: black;

    @include screen(md,true) {
        font-size: 24px;
        line-height: 30px;
        padding: 0 20px;
    }
}

.desc {
    max-width: 900px;
    margin: 0 auto;
    text-align: center;
    font-family: $font-main-display;
    color: $pen-dark-grey;

    @include descFontSize();

    @include screen(md,true) {
        margin: 0 20px;
    }
}

.dataGroups {
    max-width: 800px;
    margin: 0 auto;
    margin-top: 40px;
    font-size: 16px;
    width: 100%;
    overflow: hidden;

    font-family: $font-main-display;
    text-transform: uppercase;
    color: $pen-dark-grey;

    .dataGroupsTablist{
      margin-left: 0px;
      margin-right: 0px;
      width: 100%;
      @include screen(md, true) {
        margin-bottom: 0;
        margin-left: 0;
        margin-right: 0;
        width: 100%;
      }
      > :global(.selectTab-tabContainer){
        padding: 0 40px;
        gap: 20px;
        width: auto;
        justify-content: center;
        
        @include screen(md, true) {
          width: 100%;
          gap: 20px;
          justify-content:space-between;
          padding: 0 20px 2px 20px;
          width: calc(100% + 40px);
          margin-left: -20px;
          margin-right: -20px;
        }

        >:global(.tab){
          flex-grow: 0;
          max-width: calc((800px - 20px * 2) / 3);

          font-family: $font-main-display;
          font-weight: 400;
          font-size: 16px;
          line-height: 24px;
          letter-spacing: 0%;
          text-transform: uppercase;

          @include screen(md, true) {
            flex-grow: 1;
            // max-width: 30%;
            font-size: 15px;
          }
        }

        >:global(.scrollBtn){
          height: 100%;
          top:0;
          background-color: $background-grey2;
        }
      }
    }
}

.dataList {
    max-width: 900px;
    margin: 20px auto;
    font-size: 18px;
    font-family: $font-main-display;
    color: $pen-dark-grey;

    .list {
        list-style-type: none;
        margin: 20px 0;
        padding: 0;
        border-top: 1px solid #DBDBDB;

        > .listItem {
            background-color: white;
            margin-bottom: 2px;
            padding: 20px 30px;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
            gap: 20px;

            > .itemTitle {
                margin: 0;
                max-width: 80%;
                font-size: 18px;
                line-height: 28px;
            }

            > .itemButtons {
                max-width: 20%;
                display: flex;;
                flex-direction: row;
                justify-content: flex-end;
                margin-left: 0;
                margin-right: -30px;

                .cta {
                    min-width: 0;
                }
            }
            @include screen(md, true) {
                padding: 20px;
                padding-bottom: 10px;
                gap: 10px;
                display: flex;
                flex-direction: column;
                > .itemTitle {
                    max-width: 100%;
                    width: 100%;
                    font-size: 16px;
                }
                > .itemButtons {
                    max-width: 100%;
                    width: 100%;
                    justify-content: flex-start;
                    margin-left: -60px;
                }
            }
        }

    }
    @include screen(md, true) {
        &{
            padding: 0 20px;
        }
    }
}


.moreInfoWrapper{
display: flex;
justify-content: center;
margin-top: 50px;
.moreInfoBtn{
    text-align: center;
    min-width: 188px;
    display: inline-block;
    padding: 20px;
    background-color: transparent;
    >span{
    font-weight: 400;
    font-size: 14px;
    leading-trim: CAP_HEIGHT;
    line-height: 100%;
    letter-spacing: 5%;
    text-align: center;
    text-transform: uppercase;
    color: $pen-gold;
    }
}
}