@use '@/styles/_mixins.scss' as *;
@use '@/styles/_colors.scss' as *;

.carouselControls {
    margin-top: 30px;
    display: flex;
    align-items: center;
    gap: 1.2rem;
    padding: 1rem 1.5rem;
    width: fit-content;
    position: relative;
    box-sizing: border-box;
    left: 50%;
    transform: translateX(-50%);
    z-index: 10;

    @include screen(xs, true) {
        width: 100%;
    }

    .progressContainer {
        flex-grow: 1;
        height: 0.2rem;
        background: $pen-slick-grey;
        min-width: 30rem;
        cursor: default;
        position: relative;
        overflow: hidden;

        @include screen(xs, true) {
            min-width: auto;
            width: 100%;
        }

        .progressBar {
            height: 100%;
            background: $pen-light-gold;
            // transition: width 0.1s linear;
            position: absolute;
            left: 0;
            top: 0;
        }

    }

    .slideCounter {
        color: $pen-light-gold;
        font-family: Futura Std;
        font-weight: 500;
        font-size: 1rem;
        line-height: 1;
        letter-spacing: 2px;
        text-transform: uppercase;
    }

    .playPauseBtn {
        background: none;
        border: none;
        color: $pen-light-gold;
        cursor: pointer;
        padding: 0.5rem;
        display: flex;
        align-items: center;
        transition: transform 0.2s ease;
        border-radius: 50%;
        width: 3.2rem;
        height: 3.2rem;
        justify-content: center;


        &:hover {
            transform: scale(1.1);
            background: rgba(255, 255, 255, 0.1);
        }

        &:active {
            transform: scale(0.95);
        }

        svg {
            width: 2rem;
            height: 2rem;
            fill: currentColor;
        }
    }
}





