@use '@/styles/_colors.scss' as *;
@use '@/styles/_mixins.scss' as *;
@use '@/styles/_fonts.scss' as *;

.mainContainer {
    display: flex;
    align-items: center;
    position: relative;
    width: 100%;
    flex-direction: column;
    margin-bottom: 36px;
    box-sizing: border-box;

    @include screen(md, true) {
        padding: 0 20px;
    }
}

.smallHeading {
    margin-bottom: 40px;
    box-sizing: border-box;
    color: $pen-dark-grey;
}

.tabContainer {
    display: flex;
    overflow-x: auto;
    scroll-behavior: smooth;
    -webkit-overflow-scrolling: touch;
    /* For smooth scrolling on iOS */
    scrollbar-width: none;
    /* Hide scrollbar for Firefox */
    flex: 1;
    padding: 0 170px;
    width: 100%;
    box-sizing: border-box;
    justify-content: space-between;
    min-height: 48px;
    gap: 0 10px;

    @include screen(md, true) {
        padding: 0 25px;
        gap: 0 30px;
    }
}

.tabContainer::-webkit-scrollbar {
    display: none;
}

[class*=OtherCommitteeListing] {
    .tabContainer {
        padding: 0 220px;

        @include screen(md, true) {
            padding: 0 25px;
            gap: 0 30px;
        }
    }
}

.tab {
    flex: 1 1 auto;
    border: none;
    background: none;
    cursor: pointer;
    border-bottom: 2px solid transparent;
    transition: border-color 0.3s ease;
    text-transform: uppercase;
    color: $pen-dark-grey;
    font-size: 16px;
    @include applyFont($font-main-display, $font-cn);

    @include screen(lg, true) {
        max-width: 21%;
    }

    @include screen(md, true) {
        flex-shrink: 0;
        max-width: 50%;
    }

    &:hover,
    &.active {
        border-bottom: 2px solid $pen-gold;
        color: $pen-gold;
    }
}

.scrollBtn {
    position: absolute;
    background: white;
    width: 45px;
    height: 48px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    z-index: 1;
    line-height: 0;

    @include screen(md, true) {
        align-self: center;
    }

    &.left {
        left: 0;
    }

    &.right {
        right: 0;
    }
}

[class*=theme-beige] {
    .scrollBtn {
        background-color: $beige-bg;
    }
}

[class*=theme-grey2] {
    .scrollBtn {
        background-color: $background-grey2;
    }
}

[class*=ReactModal__Body--open] {
    .scrollBtn {
        display: none;
    }
}