@use "@/styles/_colors.scss" as *;
@use "@/styles/_mixins.scss" as *;
@use "@/styles/_fonts.scss" as *;

// Main container
:global(.moreProfiles) {
  background-color: #fff;

  .wrapper {
    padding-top: 60px;
    padding-bottom: 80px;
    text-align: center;

    @include screen(md, true) {
      padding-top: 50px;
      padding-bottom: 50px;
    }

    @include responsiveWidth($maxWidth: 980px, $margin: 0 auto);
    @include responsivePadding();

    >h2 {
      font-family: $font-main-display-large;
      font-weight: 400;
      font-size: 36px;
      line-height: 44px;
      letter-spacing: 1px;
      text-align: center;
      text-transform: uppercase;

      padding-bottom: 30px;

      @include screen(md, true) {
        font-size: 28px;
        line-height: 28px;
        letter-spacing: 0%;

        padding-bottom: 20px;
      }
    }

    >div {
      display: flex;
      // grid-template-columns: repeat(3, 1fr);
      justify-content: center;
      gap: 30px;

      @include screen(md, true) {
        // grid-template-columns: repeat(1, 1fr);
        flex-direction: column;
        gap: 50px;
        max-width: 400px;
        margin: 0 auto;
      }

      @include screen(sm, true) {
        max-width: 200px;
      }

      .block {
        cursor: pointer;
        flex: 1;
        
        @include screen(md){
          max-width: 33%;
        }

        .name {
          color: $origin-black;
        }

        &:hover {
          .name {
            color: $pen-gold;
          }
        }
      }


      >div {

        @mixin inner-width {
          & {
            width: 100%;

            @include screen(md, true) {
              max-width: none;
            }
          }
        }

        >img {
          display: block;
          height: auto;
          margin-bottom: 20px;
          @include inner-width;
        }

        .name {
          font-family: $font-main-display;
          font-weight: 400;
          font-size: 19px;
          line-height: 30px;
          letter-spacing: 0%;
          text-align: center;
          margin-bottom: 10px;

          @include screen(md, true) {
            font-size: 14px;
            line-height: 100%;
          }

          @include inner-width;
        }

        .position {
          font-family: $font-main-display;
          font-weight: 400;
          font-size: 14px;
          letter-spacing: 0%;
          text-align: center;
          color: $pen-mid-grey;

          @include screen(md, true) {
            line-height: 100%;
            letter-spacing: 0%;
          }

          @include inner-width;
        }
      }
    }

    .link {
      margin-top: 50px;
      display: inline-block;
      padding: 20px;
      min-width: 148px;

      >span {
        font-family: $font-main-display;
        font-weight: 400;
        font-size: 14px;
        line-height: 100%;
        letter-spacing: 5%;
        text-align: center;
        text-transform: uppercase;
        color: $pen-gold;
      }
    }
  }
}