@use '@/styles/_mixins.scss' as *;
@use '@/styles/_colors.scss' as *;
@use '@/styles/_fonts.scss' as *;

.imgBlk {
    line-height: 0;
    padding: 75px;
    background-color: $origin-white;
    box-sizing: border-box;

    @include screen(md, true) {
        padding: 40px;
        margin-bottom: 30px;
    }
}

.contentBlk {
    box-sizing: border-box;
    padding: 0 60px;

    @include screen(md, true) {
        padding: 0;
    }
}

.heading {
    font-weight: 400;
    letter-spacing: 1px;
    text-transform: uppercase;
    margin-bottom: 20px;

    @include screen(md, true) {
        font-size: 24px;
        line-height: 30px;
        text-align: start;
    }
}

.subHeading {
    font-weight: 400;
    font-size: 15px;
    line-height: 28px;
    letter-spacing: 0%;
    text-transform: uppercase;
    margin-bottom: 10px;

    @include screen(md, true){
        @include applyFont($font-main-display, $font-cn);
    }
}

.memberRow {
    display: flex;
    align-items: center;
    margin-bottom: 15px;
    color: $pen-dark-grey;
}

.avator {
    width: 90px;
    aspect-ratio: 1;
    flex-shrink: 0;
    line-height: 0;
    margin-right: 20px;

    img {
        width: 100%;
        height: auto;
        object-fit: contain;
    }

    @include screen(md, true) {
        width: 70px;
    }
}

.memberDetail {}

.jobTitle {
    font-size: 18px;
    line-height: 22px;
    letter-spacing: 0%;

    span {
        white-space: nowrap;
    }

    @include screen(md, true) {
        white-space: normal;
        font-size: 14px;

        span {
            white-space: normal;
        }
    }
}

.cta {
    padding-left: 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 100%;
    letter-spacing: 5%;
    text-transform: uppercase;

    :global(.iconRight){
        transform: translateY(-.5px);
    }
}