@use '@/styles/_mixins.scss' as *;
@use '@/styles/_colors.scss' as *;

.lb{
    :global(.banner-lb-slider){
        width: calc(100vw);
        margin: 0 -30px;
        @include screen(md, true) {
            margin: 0 -20px;
        }
        :global(.slick-slider){
            :global(.slick-arrow){
                top: 50%;
                &:global(.slick-prev){
                    left:30px !important;
                }
                &:global(.slick-next){
                    right:30px !important;
                }
                @include screen(md, true) {
                    &:global(.slick-prev){
                        left:16px !important;
                    }
                    &:global(.slick-next){
                        right:16px !important;
                    }
                }
            }

            :global(.slick-list){
                :global(.slick-slide){
                    >div{
                        :global(.slideBody){
                            width: 100vw;
                            max-width: none;
                            height: auto;
                            padding: 0;
                            max-height: calc(100vh - 142px);
                            position: relative;

                            :global(.featureBtns){
                                position: absolute;
                                bottom: 30px;
                                right: 50%;
                                transform: translateX(50%);
                                @include screen(sm, true) {
                                    :global(.info){
                                        display: none;
                                    }
                                    :global(.download){
                                        width: 45px;
                                        height: 45px;
                                        >svg{
                                            width: 27px;
                                            height: 27px;
                                        }
                                    }
                                }
                            }
                            img{
                                display: block;
                                object-fit: cover;         /* Crops to fill container */
                                object-position: center; 
                                width: 100%;
                                height: auto;
                                max-height: calc(100vh - 142px);
                                
                                @include screen(sm, true) {
                                    height: 75vw;
                                    max-height:none;
                                }
                            }
                            .desc{
                                width: calc(100% - 240px);
                                max-width: 1064px;
                                height: auto;
                                max-height: 60%;
                                position: absolute;
                                left: 50%;
                                bottom: 0;
                                background-color: rgba($origin-black, .8);
                                transform: translateY(100%) translateX(-50%);
                                transition: transform .3s;
                                padding: 49px 0 0 42px;
                                display: flex;
                                align-items: stretch;

                                &:global(.show){
                                    transform: translateY(0) translateX(-50%);
                                }

                                :global(.closeBtn){
                                    display: flex;
                                    justify-content: center;
                                    align-items: center;
                                    position: absolute;
                                    width: 24px;
                                    height: 24px;
                                    cursor: pointer;
                                    right: 20px;
                                    top: 20px;
                                    >svg{
                                        width: 13.79px;
                                        height: 13.79px;
                                    }
                                }
                                :global(.text){
                                    width: 100%;
                                    height: auto;
                                    overflow-x: hidden;
                                    overflow-y: auto;
                                    @include custom-scrollbar;
                                    padding-bottom: 37px;
                
                                    >:global(.richText ){
                                        padding-right: 41px;
                                        color: #fff;
                                        font-family: Futura Std;
                                        font-weight: 400;
                                        font-style: Book;
                                        leading-trim: NONE;
                                        line-height: 150%;
                                        letter-spacing: 0%;
                                        text-align: left;
                                        @include descFontSize();
                                    }
                                }
                                @include screen(md, true) {
                                    width: calc(100% - 200px);
                                }
                                @include screen(sm, true) {
                                    display: none !important;
                                }
                            }
                        }
                    }
                }
            }
        }
    }

    .mDesc{
        display: none;
        @include screen(sm, true) {
            box-sizing: border-box;
            display: block;
            height: calc(100vh - 136px - 75vw);
            width: 100vw;
            margin: 0 -20px;
            padding: 21px 0 43px 21px;
            overflow-x: hidden;
            overflow-y: auto;
            @include custom-scrollbar;
            >div{
                padding: 0 21px 0 0;
                >:global(.richText){
                    display: block;
                    margin: 0;
                    text-align: left;
                    color: #fff;

                    font-family: Futura Std;
                    font-weight: 400;
                    font-style: Book;
                    leading-trim: NONE;
                    letter-spacing: 0%;
                    @include descFontSize();
                }
            }
        }
    }
}