@use "@/styles/_colors.scss" as *;
@use "@/styles/_mixins.scss" as *;
@use "@/styles/_fonts.scss" as *;


.wrapper {  
    padding-top: 55px;

    @include screen(md, true) {
        padding-top: 30px;
        padding-bottom:32px;
    }

}


.title {
    font-weight: 400;
    letter-spacing: 10%;
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 20px;
    font-family: $font-main-display-large;
    color: black;

    @include screen(sm,true) {
        margin-left: 20px;
        margin-right: 20px;
        font-size: 24px;
        line-height: 30px;
    }
}

.desc {
    max-width: 900px;
    margin: 40px auto;
    text-align: start;
    font-family: $font-main-display;
    color: $pen-dark-grey;
   
    >:global(.richText){
        text-align: start;
    }

    @include descFontSize();
    @include screen(md, true) {
        margin-left: 20px;
        margin-right: 20px;
    }
}

.dataList {
    max-width: 900px;
    margin: 0 auto;
    font-size: 18px;
    font-family: $font-main-display;
    color: $pen-dark-grey;

    .list {
        list-style-type: none;
        padding: 50px 60px;
        background-color: #f9f9f9;

        > .listItem {
            margin-bottom: 2px;
            padding: 20px 0;
            display: flex;
            flex-direction: row;
            justify-content: flex-start;
            align-items: flex-start;
            gap: 20px;
            border-bottom: 1px solid #DBDBDB;
            &:last-child {
                border-bottom: none;
            }
            > .itemTitle {
                margin: 0;
                width: 30%;
                font-size: 16px;
                line-height: 1.5em;
                color: $pen-dark-grey;
            }

            > .itemContent {
                text-align: left;
                color: $pen-dark-grey;
                :global(.richText ) {
                    font-family: $font-main-display;
                    text-align: left;
                    font-size: 16px;
                    line-height: 1.5em;
                    text-wrap: wrap;
                    word-break: break-word;
                    a {
                        text-decoration: underline;  //keep
                        &:hover {
                            text-decoration: none;
                        }
                    }
                }
                width: 70%;
                overflow: hidden;
            }
            @include screen(md, true) {
                padding: 20px 0;
                gap: 10px;
                display: flex;
                flex-direction: column;
                > .itemTitle {
                    font-size: 14px;
                    width: 100%;
                    text-decoration: underline;  //keep
                }
                > .itemContent {
                    width: 100%;
                    justify-content: flex-start;
                }
            }
        }
        @include screen(md, true) {
            padding: 10px 20px;
        }

    }
    @include screen(md, true) {
        padding: 0 20px;
    }
}


.moreInfoWrapper{
display: flex;
justify-content: center;
margin-top: 50px;
.moreInfoBtn{
    text-align: center;
    min-width: 188px;
    display: inline-block;
    padding: 20px;
    background-color: transparent;
    >span{
    font-weight: 400;
    font-size: 14px;
    leading-trim: CAP_HEIGHT;
    line-height: 100%;
    letter-spacing: 5%;
    text-align: center;
    text-transform: uppercase;
    color: $pen-gold;
    }
}
}