@use "@/styles/_colors.scss" as *;
@use "@/styles/_mixins.scss" as *;
@use "@/styles/_fonts.scss" as *;



.wrapper {  
    max-width: 800px;
    margin: 0 auto;
    margin-top: 20px;
    margin-bottom: 20px;


    .title {
        @include title-styles($font-main-display-large, $font-cn-large);
    }

    .slideDropdownWrapper {
        padding: 0 20px;
    }

    .slideDropdown {
        display: none;
        border: none !important;
        width: calc(100% - 80px);
        margin: 0 auto;
        @include screen(md,true) {
            display: flex;
            justify-content: center;
            :global(.react-dropdown-select-content) {
                flex: none;
                > span {
                    font-family:  $font-main-display-large;
                    color: #867050;

                    font-size: 18px;
                    line-height: 22px;
                }
            }
            :global(.react-dropdown-select-item) {
                text-align: center;
            }
        }
    }


    .slide {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 0 40px;
        text-align: center;

        @include screen(md,true) {
            // width: 100vw;
            max-width: none;
            padding: 0;
        }

        .slideTitle {

            font-weight: 400;
            letter-spacing: 10%;
            text-transform: uppercase;
            text-align: center;
            margin-top: 20px;
            margin-bottom: 0;
            font-family:  $font-main-display-large;
            color: #867050;
            font-size: 24px;

            @include screen(md,true) {
                font-size: 18px;
                line-height: 22px;
                display: none;
            }
        }


        .slideChart {
            width: 100%;
            height: 480px;
            @include screen(md,true) {
                height: 360px;
            }
        }
    }

    .slider {
        position: relative;

        :global(.slick-slider) {
            display: block;
            :global(.slick-arrow.slick-prev) {
                left: 0;
            }
            :global(.slick-arrow.slick-next) {
                right: 0;
            }
            :global(.slick-list) {
                :global(.slick-slide) {
                    width: 800px;
                    @include screen(md,true) {
                        width: 100vw;
                    }
                }
            }
        }

        // override slick base styles if needed
        .slick-dots {
            margin: 0;
            padding: 0;
        }
        :global(.slick-dots) {
            bottom: 0px;
        }

        .customDots {
            display: flex;
            align-items: center;
            justify-content: center;
            list-style: none;
            padding: 0;
            margin: 18px 0 0;

            li {
              margin: 0 8px;
              display: inline-flex;
              align-items: center;
              justify-content: center;

              button {
                border: none;
                background: $pen-slick-grey;   // inactive dot color
                width: 8px;
                height: 8px;
                border-radius: 50%;
                padding: 0;
                cursor: pointer;
                transition: all 220ms ease;
              }

              &.slick-active {
                button {
                  // active pill style
                  width: 90px;          // pill length
                  height: 6px;          // pill thickness
                  border-radius: 6px;
                  background: $pen-light-gold;  // active color
                }
              }
            }
          }
    }
}

// optional responsive tweaks
@media (max-width: 576px) {
  .slider {
    .customDots {
      margin-top: 12px;
      li { margin: 0 6px;
        &.slick-active button { width: 56px; height: 5px; }
      }
    }
  }
}

