@use "@/styles/_colors.scss" as *;
@use "@/styles/_mixins.scss" as *;

.share {
  display: inline-block;
  position: relative;
  overflow: visible;

  .sharePop {
    display: none;
    flex-flow: row nowrap;
    gap: 5px;
    position: absolute;
    background-color: #fff;
    box-shadow: 0px 4px 8px 0px #00000026;
    left: 50%;
    top: calc(100% + 5px);
    transform: translateX(-50%);
    padding: 17.5px 20px;
    z-index: 50;

    &.show {
      display: flex;
    }

    >button {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 40px;
      height: 40px;
      background-color: #fff !important;

      &:hover {
        >svg{
          rect{
            stroke: $pen-light-gold;
          }
        }
      }
    }

  }

  .wechatShare {
    box-shadow: 0px 4px 8px 0px #00000026;
    padding: 17.5px 20px;
    background-color: #fff;
    position: absolute;
    left: 50%;
    top: calc(100% + 75px);
    transform: translateX(-50%);
    z-index: 60;
  }
}