@use '@/styles/_mixins.scss' as *;
@use '@/styles/_colors.scss' as *;
@use '@/styles/_fonts.scss' as *;

.container {
  box-sizing: border-box;
  margin: 30px 80px;
  border: 5px solid $pen-border-grey;
  padding: 40px 0;
  margin-top: -50px; // to "forcely" reduce the gap between topIntro & itself

  @include screen(md, true) {
    margin: 20px;

    margin-top: -20px; // to "forcely" reduce the gap between topIntro & itself
  }
}

.title {
  font-weight: 400;
  letter-spacing: 1px;
  text-align: center;
  text-transform: uppercase;

  @include screen(sm, true) {
    text-align: start;
    padding: 0 25px;
    @include applyFont($font-main-display-large, $font-cn-large);
  }
}

.subHeading {
  font-weight: 400;
  font-size: 15px;
  line-height: 28px;
  text-align: center;
  text-transform: uppercase;
  margin: 20px 0;
  @include applyFont($font-main-display, $font-cn);

  @include screen(sm, true) {
    text-align: start;
    padding: 0 25px;
    margin: 16px 0 10px;
  }
}

.memberGrid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  padding: 0 14.5%;
  gap: 20px 40px;

  @include screen(sm, true) {
    grid-template-columns: 1fr;
    padding: 0 25px 30px;
    gap: 15px 0;
  }
}

.memberCard {
  background: #fff;
  color: $pen-dark-grey;
}

.memberName {
  font-size: 18px;
  line-height: 28px;
  letter-spacing: 0%;
}

.memberJobTitles {
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  letter-spacing: 0%;

  .underline {
    // text-decoration: underline;
    // text-decoration-style: solid;
    // text-decoration-offset: 0%;
    // text-decoration-thickness: 0%;
  }

  @include screen(md, true) {
    font-size: 14px;
  }
}

.ctaWrapper {
  display: flex;
  justify-content: center;
  margin-top: 30px;
}

.nameCta {
  padding: 0;
  min-height: auto;
  height: auto;
}