@use "../_fonts.scss" as *;
@use "../_colors.scss" as *;
@use "../_mixins.scss" as *;

.full-with-images-or-videos {
    width: 100%;
    // max-width: #{$content-max-width}px;
    max-width: 100%;
    margin: 0 auto;
    position: relative;

    &.is-transparent-header-area {
        margin-top: -140px;

        @include screen(md, true) {
            margin-top: -143.32px;
        }
    }

    >.banners-container {
        width: 100%;
        z-index: 10;
        position: absolute;
        left: 0;
        top: 0;

        >.stacking-banner-items {
            width: 100%;
            left: 0;
            top: 0;
            background-size: cover;
            overflow: hidden;
            background-color: transparent;
            position: sticky;

            >.text-box {
                text-align: center;
                position: absolute;
                width: 100%;
                height: 100%;
                left: 0;
                top: 0;
                z-index: 5;
                padding: 40px;
                display: flex;
                justify-content: center;
                align-items: center;
                flex-flow: column nowrap;
                box-sizing: border-box;

                >.section-title {
                    color: $origin-white;
                    margin-bottom: 2px;
                    text-transform: uppercase;
                    font-weight: 400;
                    font-size: 13px;
                    line-height: 22px;
                    letter-spacing: 1.3px;
                    text-transform: uppercase;

                    &:lang(zh-HK),
                    &:lang(zh-TW),
                    &:lang(zh-Hant) {
                        font-size: 18px;
                    }
                }

                >.title {
                    color: $origin-white;
                    font-size: 58px;
                    font-weight: 400;
                    line-height: 65px;
                    letter-spacing: 0;
                    margin-bottom: 10px;
                }

                // >.pargraph {
                //     color: $origin-white;
                //     font-size: 18px;
                //     font-weight: 300;
                //     line-height: 24px;
                //     /* 133.333% */
                //     letter-spacing: 0.36px;
                //     max-width: 410px;
                // }

                .exploreCta {
                    border: 1px solid $pen-border-grey;
                    display: inline-block;
                    min-width: auto;
                    min-height: auto;
                    color: $origin-white;
                    padding: 15px 40px;
                    line-height: 1;

                    &:hover {
                        border-color: $pen-light-gold;
                        color: $pen-light-gold;
                    }
                }

                @include screen(md, true) {
                    padding: 25px;


                    >.section-title {
                        font-weight: 500;
                        font-size: 13px;
                        line-height: 22px;
                        letter-spacing: 1.3px;
                        font-family: $font-main-display;

                        &:lang(zh-HK),
                        &:lang(zh-TW),
                        &:lang(zh-Hant) {
                            font-family: $font-cn;
                            font-size: 18px;
                        }
                    }

                    >.title {
                        font-size: 38px;
                        line-height: 40px;
                        text-align: center;
                    }

                    // >.pargraph {
                    //     // @include body-m;
                    //     max-width: 348px;
                    // }
                }
            }

            &:after {
                content: "";
                display: block;
                width: 100%;
                height: 100%;
                position: absolute;
                left: 0;
                top: 0;
                z-index: 2;
                background-size: cover;
                background-repeat: no-repeat;
                background-color: rgba(0,0,0,.05);
                // }
                // &.video:after{
                // background: linear-gradient(287.32deg, rgba(0, 0, 0, 0) 51.31%, rgba(0, 0, 0, 0.5) 83.81%), linear-gradient(0deg, rgba(0, 0, 0, 0) 71.49%, rgba(0, 0, 0, 0.4) 101.31%);

                @include screen(md, true) {
                    // background: linear-gradient(0deg, #D9D9D9, #D9D9D9), linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), linear-gradient(180deg, rgba(0, 0, 0, 0.4) 8.38%, rgba(0, 0, 0, 0) 68.52%);

                    // background: linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), linear-gradient(180deg, rgba(0, 0, 0, 0.4) 8.38%, rgba(0, 0, 0, 0) 68.52%);
                }

                // }
                // &:not(.video):after{
                //     background: linear-gradient(287.32deg, rgba(0, 0, 0, 0) 51.31%, rgba(0, 0, 0, 0.5) 83.81%), linear-gradient(0deg, rgba(0, 0, 0, 0) 81.49%, rgba(0, 0, 0, 0.4) 93.31%);
                //     @include screen(md, true){
                //         background: linear-gradient(0deg, #D9D9D9, #D9D9D9), linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), linear-gradient(180deg, rgba(0, 0, 0, 0.4) 8.38%, rgba(0, 0, 0, 0) 68.52%);
                //     }
            }
        }
    }

    &.top-banner-style {
        >.banners-container {
            >.stacking-banner-items {
                >.text-box {
                    >.title {
                        font-size: 50px;
                        letter-spacing: 0;
                        margin-bottom: 0;
                        max-width: 80%;
                    }

                    >.pargraph {
                        display: none;
                    }

                    @include screen(md, true) {
                        >.title {
                            font-size: 35px;
                            font-weight: 300;
                            line-height: 114.99999999999999%;
                            letter-spacing: 0%;
                            max-width: 100%;
                        }
                    }
                }

                &:first-child {
                    >.down-arrow {
                        width: 20px;
                        height: 10px;
                        position: absolute;
                        left: calc(50% - 10px);
                        bottom: 38px;
                    }
                }
            }
        }
    }

    >.indicator-container {
        position: absolute;
        width: 0;
        top: 0;
        right: 0;
        overflow: visible;

        >div {
            position: sticky;
            top: 0;
            left: 0;
            overflow: visible;

            display: flex;
            align-items: center;

            >.indicator {
                $w: 30;
                transform: translateX(-40px);
                height: auto;
                padding: 10px;
                border-radius: 15px;
                background-color: rgba(0, 0, 0, 0.1);
                backdrop-filter: blur(15px);
                -webkit-backdrop-filter: blur(15px);
                align-items: center;

                display: flex;
                flex-flow: column nowrap;
                gap: 10px;

                // dots
                >div {
                    width: 4px;
                    height: 4px;
                    border-radius: 50%;
                    background-color: $origin-white;
                    opacity: .5;
                    transition: opacity .5s, height .5s border-radius .5s;

                    &.active {
                        margin: 0 2px;
                        width: 2px;
                        height: 60px;
                        // padding: 0 2px;
                        box-sizing: border-box;
                        opacity: 1;
                        border-radius: 0;
                    }
                }
            }
        }

        @include screen(md, true) {
            display: none;
        }
    }
}