@use '@/styles/_mixins.scss' as *;
@use '@/styles/_colors.scss' as *;

.featureBtns {
    display: flex;
    flex-flow: row nowrap;
    gap: 6px;

    .button {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 40px;
        height: 40px;
        background-color: rgba($origin-black, .6);
        cursor: pointer;
        transition: background .2s;

        svg {
            path {
                transition: fill .2s, stroke .2s;
            }
        }

        &:hover {
            background-color: #fff;

            svg {
                path {
                    fill: $pen-gold;
                    stroke: $pen-gold;
                }
            }
        }

        &.info {
            svg {
                width: 5.77px;
                height: 18.48px;
            }
        }

        &.enlarge {
            svg {
                width: 15.75px;
                height: 15.75px;
            }
        }

        &.download {
            svg {
                width: 24px;
                height: 24px;
            }
        }
    }
}

// for media lib & press list
:global(.pressList) {
    .featureBtns {
        .button {
            &:global(.download) {
                width: 45px;
                height: 45px;
                border: $pen-mid-grey 1px solid;
                background-color: transparent;

                &:hover {
                    border-color: $pen-light-gold;
                    background-color: #fff;

                    >svg {
                        >path {
                            fill: $pen-dark-gold;
                        }
                    }
                }
            }
        }
    }
}