@use '../../styles/colors' as *;
@use '../../styles/mixins' as *;
@use '../../styles/fonts' as *;
@use '../../styles/arrow-settings' as arrowSettings;
.header {
  background: $origin-white;
  position: fixed;
  width:100% ;
  height: 82px;
  font-family: $font-navigation;
  color: $pen-dark-grey;
  z-index: 1000;
  border-bottom: 1px solid $pen-border-grey;   
}
.headerTransparent {
  background: transparent;
  color: $origin-white;
  border: 0;
  width:100%;
}

// .headerTransparent:hover,
.headerTransparent:focus-within {
  background: $origin-white;
  color: $origin-black;
  border-bottom: 1px solid $pen-border-grey;
}

.headerWapper {
  display: flex;
  max-width: 1850px;
  margin: 0 auto;
  align-items: center;
  justify-content: space-between;
  height: inherit;
}

.headerImgWapper{
  display: flex;
  align-items: center;
  margin-left: 10px;
  width: 368px;
  @include screen(sm,true) {
    max-width: 320px;
  }
}


.logo {
  cursor: pointer;
  width: 100%;

}

.nav {
  display: flex;
  align-items: center;
}

.navBtn {
  background: none;
  border: none;
  font-size: 14px;
  cursor: pointer;
  padding: 15px 10px;
  text-transform: uppercase;
  white-space: nowrap;

  >span {
    text-transform: uppercase;
  }
}

.navBtn:hover {
  color: $pen-gold;
}

.dropdown {
  position: relative;
  display: inline-block;
  
}

.dropdownMenu {
  position: absolute;
  top: 60px;
  left: -30px;
  background: $origin-white;
  display: flex;
  flex-direction: column;
  width: max-content;
  padding: 20px;
  overflow: hidden;
  max-height: 0;
  opacity: 0;
  gap:10px;
}

.dropdownMenu.open {
  max-height: 600px;
  opacity: 1;
}

.dropdownItem{
  background: none;
  border: none;
  width: 100%;
  text-align: left;
  padding: 2px 20px;
  cursor: pointer;
  font-size: 14px;
}

.dropdownItem:hover {
  color: $pen-gold;
}

.hamburger{
  display: none;
  background: none;
  border: none;
  font-size: 2rem;
  cursor: pointer;
  position: fixed;
  right: 10px;
}


@media (max-width: 1290px) {
  .header{
    height: 60px;
    padding-top: 0;
    padding-bottom: 0;
    padding-right: 0;
  }
  .nav {
    display: none;
  }
  .hamburger {
    display: flex;
    align-items: center;
  }
  .dropdownMenu {
    display: none;
  }
  .dropdownMenuMobile {
    display: flex;
  }
}

.mobileOverlay {
  position: fixed;
  inset: 0;
  // background: rgba(0,0,0,0.5);
  display: flex;
  justify-content: flex-start; // align menu to left
  pointer-events: none;
}

.mobileMenu {
  background: $origin-white;
  width: 292px;
  height: 100%;
  position: fixed;
  right: 0;
  top: 0;
  transform: translateX(0);
  margin-top: 60px;
}

.closeBtn {
  position: absolute;
  top: -42px;
  background: none;
  border: none;
  right: 28px;
  cursor: pointer;
  font-size: 20px;
  color: $pen-gold;
  pointer-events: auto;
}

.mobileNav {
  display: flex;
  flex-direction: column;
  padding: 0px 26px;
  pointer-events: auto;
  font-size: 14px;
  overflow: auto;
  height: calc(100% - 82px);
  .searchMobile{
    font-size: 14px;
    padding: 0.75rem 1.5rem;
    border-bottom: 1px solid $pen-border-grey;
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    gap: 10px;
    img{
      margin-right: 10px;
    }
    color: $pen-light-grey;
  }
  .mobileDropdown{
    font-size: 14px;
    border-bottom: 1px solid $pen-border-grey;
  }
  .mobileDropdown:last-child {
    border-bottom: none;
  }
}

.goldText {
  color: $pen-gold;
  // font-weight: bold;
}


.mobileNavBtn {
  background: none;
  border: none;
  font-size: 1.1rem;
  text-align: left;
  padding: 0.75rem 1rem;
  cursor: pointer;
  width: 100%;
  display: flex;
  justify-content: space-between;
  font-size: 14px;
  margin: 10px;
  padding-right: 15px;
  align-items: center;
}

.mobileNavBtn[aria-expanded="true"] {
  color: $pen-gold;
}



.dropdownMenuMobile {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  max-height: 0;
  opacity: 0;
  font-size: 16px;
  border-top: 0px solid $pen-border-grey;
}
.dropdownMenuMobile.open {
  padding: 22px 0px;
  gap: 10px;
  border-top: 1px solid $pen-border-grey;
  max-height: 600px;
  opacity: 1;
}


.languageList {
  right: 0;
  left: auto;
}

.activeLang {
  background: none;
  border: none;
  width: 100%;
  text-align: left;
  padding: 2px 20px;
  cursor: default;
  font-size: 12px;
  color: $pen-gold;
}

.search {
  border: none;
  cursor: pointer;
  width: fit-content;
  height: 100%;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
}

.languageButton{
  display: flex;
  align-items: center;
  gap: 10px;
}
.languageButtonImage{
  border: none;
  cursor: pointer;
  width: fit-content;
  height: fit-content;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
}

.headerTransparent .dropdownMenu,
.dropdownMenu.headerTransparent{
  background: transparent;
  color: $origin-black;
}

.arrow {
  @include arrowSettings.accordion-arrow(down, $pen-gold);
  position: relative;
  float: right;
  height: 5px;
  width: 2px;
  margin-right: 20px;
  vertical-align: middle;
  top: 12px;
}

.arrow.open{
  transform: rotate(180deg);
}

.mobileLanguageDropdown{
  display: flex;
  flex-direction: column;
  align-items: center;
}

.mobileLanguageButton{
  margin: 20px 0;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  justify-self: center;
}