@use "@/styles/_colors.scss" as *;
@use "@/styles/_mixins.scss" as *;
@use "@/styles/_fonts.scss" as *;

.feedUnit {
  cursor: pointer;
  width: auto;
  display: flex;
  flex-flow: column nowrap;
  height: 100%;
  gap: 24px;

  &:hover {
    .textBox {
      .title {
        color: $pen-gold;
      }
    }
  }

  // hsh-272 make it center if only 1 at last row
  &:last-child:nth-child(3n + 1) {
    grid-column: 2;
  }

  @include screen(md, true) {
    gap: 20px;
  }

  &:global(.hideDesktop) {
    display: none;

    @include screen(md, true) {
      display: flex;
    }
  }

  &:global(.hideMobile) {
    @include screen(md, true) {
      display: none;
    }
  }

  .img {
    position: relative;

    img {
      display: block;
      width: 100%;
      height: auto;
      aspect-ratio: 413 / 270;
      object-fit: cover; // optional: controls cropping
      // margin-bottom: 24px;

      @include screen(md, true) {
        aspect-ratio: 353 / 230;
        // margin-bottom: 20px;
      }
    }

    >svg {
      position: absolute;
      z-index: 1;
      width: 30px;
      height: 30px;
      right: 20px;
      bottom: 20px;
    }
  }

  .textBox {
    display: flex;
    flex-flow: column nowrap;
    flex-grow: 1;
    gap: 10px;
    text-align: left;
    align-items: flex-start;
    min-height: 111px;

    .title {
      flex-grow: 1;
      font-family: $font-main-display;
      font-weight: 400;
      font-style: Book;
      font-size: 16px;
      line-height: 24px;
      letter-spacing: 0%;
      color: $origin-black;
      min-height: 48px;
    }

    .smallTxt {
      font-family: $font-main-display;
      font-weight: 400;
      font-style: Book;
      font-size: 13px;
      line-height: 24px;
      letter-spacing: 0%;
      color: $pen-gold;
      display: flex;
      flex-flow: row nowrap;
      align-items: center;
      gap: 20px;

      >*:not(:first-child) {
        position: relative;

        &:before {
          content: " ";
          display: block;
          position: absolute;
          border-left: 1px solid $pen-border-grey;
          height: 12px;
          top: calc(50% - 12px/2);
          left: calc(-20px/2);
        }
      }

      .cta {
        display: inline-flex;
        flex-flow: row nowrap;
        align-items: center;
        justify-content: flex-start;
        padding: 0;
        min-height: auto;
        letter-spacing: 0;

        opacity: 0;
        transition: opacity 0.3s ease-in-out;

        @include screen(md, true) {
          opacity: 1;
        }

        :global(.iconRight) {
          margin-left: 0;

          >div {
            width: 17px;
            height: 17px;

            >svg {
              margin-left: 3px;
              width: inherit;
              height: inherit;
            }
          }
        }
      }

    }

    .badge {
      padding: 5px 10px;
      border: 1px solid $pen-gold;
      font-family: $font-main-display;
      font-weight: 400;
      font-style: Book;
      font-size: 12px;
      line-height: 100%;
      letter-spacing: 0%;
      color: $pen-gold;
    }
  }

  &:hover,
  &:focus {
    .textBox {
      .smallTxt {
        .cta {
          opacity: 1;
        }
      }
    }
  }

}