@use "@/styles/_colors.scss" as *;
@use "@/styles/_mixins.scss" as *;
@use "@/styles/_fonts.scss" as *;

// Main container
.zigzagContentBlock4 {
  // @include responsiveWidth();
  @include responsivePadding();

  .wrapper {
    width: 100%;
    box-sizing: border-box;
    display: flex;
    flex-flow: column nowrap;
    row-gap: 75px;

    h3 {
      font-weight: 400;
      letter-spacing: 0%;
      text-align: center;
      font-size: 48px;
      line-height: 65px;
      margin-bottom: 5px;
      color: $origin-black;

      @include applyFont($font-main-display-large, $font-cn-large);

      @include screen(md, true) {
        font-size: 24px;
        line-height: 30px;
        margin-bottom: -25px;
      }
    }

    .mobileTitle {
      display: none;

      @include screen(md, true) {
        width: 100%;
        display: block;
        font-weight: 400;
        font-size: 28px;
        line-height: 28px;
        letter-spacing: 0%;
        text-align: center;
        text-transform: uppercase;
        margin-bottom: 20px;
      }
    }

    // Each comment set
    .set {
      display: flex;
      align-items: flex-start;
      gap: 80px 0;

      .text {
        margin: auto 0;
      }

      //// N-th start from 1
      // Odd sets (image left, text right)
      &:nth-child(odd) {
        .image {
          order: 1;
        }

        .text {
          order: 2;
          padding-left: 60px;
          padding-right: 70px;
        }
      }

      // Even sets (image right, text left)
      &:nth-child(even) {
        .image {
          order: 2;
        }

        .text {
          order: 1;
          padding-left: 40px;
          padding-right: 90px;
        }
      }

      @include screen(md, true) {
        flex-direction: column;
        gap: 0;

        .image {
          display: none;
        }

        &:nth-child(even),
        &:nth-child(odd) {

          .image,
          .text {
            order: initial;
            padding-left: 0;
            padding-right: 0;
          }
        }
      }

      .row {
        display: flex;
        gap: 0 20px;

        @include screen(md, true) {
          gap: 0;
          flex-direction: column;
        }
      }

      // Image styling
      .image {
        flex: 1;
        line-height: 0;
        width: calc(50% - 10px);

        @include screen(md, true) {
          max-width: none;
          width: 100%;
          // margin-bottom: 20px;
        }

        img {
          width: 100%;
          height: auto;
        }
      }

      // Text content styling
      .text {
        width: calc(50% - 10px);
        box-sizing: border-box;

        @include screen(md, true) {
          width: 100%;
          max-width: none;
          padding: 0;
        }

        h4 {
          font-weight: 400;
          font-size: 36px;
          line-height: 44px;
          letter-spacing: 1px;
          text-transform: uppercase;
          text-align: left;
          color: $origin-black;

          @include applyFont($font-main-display-large, $font-cn-large);

          @include screen(md, true) {
            line-height: 30px;
            letter-spacing: 0%;
            display: none;
          }
        }

        .accor {
          border-bottom: 1px solid $pen-border-grey;
          transition: border .3s;

          :global(.tabContainer) {
            padding: 30px 0 20px;
            cursor: pointer;

            h5 {
              font-weight: 400;
              font-size: 20px;
              line-height: 24px;
              letter-spacing: 0%;
              text-transform: capitalize;
              color: rgba($origin-black, .5);
              transition: color .2s;
            }

          }

          :global(.desc) {
            margin-bottom: 30px;
            margin: -10px 0 0;

            font-weight: 400;
            letter-spacing: 0%;
            color: $pen-dark-grey;
            @include descFontSize();
          }

          &:global(.open) {
            border-color: $origin-black;

            :global(.tabContainer) {
              h5 {
                color: $origin-black;
              }
            }
          }
        }
      }

      &:global(.size-half) {
        .text {
          max-width: 50%;

          @include screen(md, true) {
            max-width: none;
          }
        }
      }
    }
  }
}