@use "@/styles/_colors.scss" as *;
@use "@/styles/_mixins.scss" as *;
@use "@/styles/_fonts.scss" as *;



.wrapper {  
    padding-top: 55px;
    padding-bottom:82px;

    background-color: #f9f9f9;

    @include screen(md, true) {
        padding-top: 30px;
        padding-bottom:32px;
    }

}
  

.title {
    font-weight: 400;
    letter-spacing: 10%;
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 20px;
    font-family: $font-main-display-large;
    color: black;

    @include screen(md,true) {
        font-size: 24px;
        line-height: 30px;
    }
}

.desc {
    max-width: 900px;
    margin: 0 auto;
    text-align: center;
    font-family: $font-main-display;
    color: $pen-dark-grey;
    @include descFontSize();

    @include screen(md,true){
        padding: 0 20px;
    }
}
.dataGroups {
    max-width: 900px;
    margin: 0 auto;
    margin-top: 40px;
    margin-bottom: 20px;
    font-size: 16px;


    font-family: $font-main-display;
    text-transform: uppercase;
    color: $pen-dark-grey;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 20px;

    .selectedFilterLabel {
        font-family: $font-main-display-small;
        font-weight: 400;
        font-style: Regular;
        font-size: 24px;
    }

    .filterDropdownWrapper {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
        gap: 20px;
        .filterDropdown {
            background-color: white;
        }
    }

    @include screen(md, true) {
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
        margin: 20px auto 0;
        padding: 0 20px;
        .selectedFilterLabel {
            display: none;
        }
        .filterDropdownWrapper {
            display: grid;
            grid-template-columns: 1fr;
            padding: 20px 0;
            width: 100%;
            > .filterDropdown {
                width: 100%;
            }
        }
    }
}

.dataList {
    max-width: 900px;
    margin: 0 auto;
    font-size: 18px;
    font-family: $font-main-display;
    color: $pen-dark-grey;

    .list {
        list-style-type: none;
        margin: 20px 0;
        padding: 0;
        border-top: 1px solid #DBDBDB;

        > .listItem {
            background-color: white;
            margin-bottom: 2px;
            padding: 20px 30px;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
            gap: 20px;

            > .itemTitle {
                margin: 0;
                max-width: 80%;
                font-size: 18px;
                line-height: 28px;
            }

            > .itemButtons {
                display: flex;
                flex-direction: row;
                justify-content: flex-end;
                margin-left: 0;
                margin-right: -30px;

                .cta {
                    min-width: 0;
                }
            }
            @include screen(md, true) {
                padding: 20px;
                padding-bottom: 10px;
                gap: 10px;
                display: flex;
                flex-direction: column;
                > .itemTitle {
                    max-width: 100%;
                    width: 100%;
                    font-size: 16px;
                }
                > .itemButtons {
                    max-width: 100%;
                    width: 100%;
                    justify-content: flex-start;
                    margin-left: -60px;
                }
            }
        }

    }
    @include screen(md, true) {
        &{
            padding: 0 20px;
        }
    }
}

.loadMoreContainer {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    position: relative;

    &::before {
        display: block;
        content: " ";

        width: 1px;
        height: 40px;
        background-color: $pen-gold;
        color: $pen-gold;
        
    }
}

.loadMoreDecorator {
    width: 1px;
    height: 40px;
    background-color: $pen-gold;
    color: $pen-gold;
}

.loadMoreButton {
    justify-content: center;

    &:hover{
        >span {
            margin-right: 0 !important;
            text-decoration: underline;
        }
    }
}
