@use '@/styles/_mixins.scss' as *;
@use '@/styles/_colors.scss' as *;

.title {
    font-weight: 400;
    font-size: 36px;
    line-height: 44px;
    letter-spacing: 1px;
    text-align: center;
    text-transform: uppercase;
    margin-bottom: 30px;
    color: $origin-black;

    @include screen(md, true) {
        font-size: 28px;
        line-height: 28px;
        padding: 0 20px;
        margin-bottom: 20px;
    }
}

.mediaItemsContainer {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    width: 100%;
    gap: 0 5.5%;
    padding: 0 24px;
    box-sizing: border-box;

    @include screen(xl) {
        gap: 0 80px;
    }

    @include screen(md, true) {
        padding: 0 80px;
    }

    @include screen(xs, true) {
        padding: 0 20px;
    }
}

.mediaContainer {
    width: 33%;
    max-width: 280px;

    @include screen(md, true) {
        max-width: none;
        width: auto;
        flex: 1;
        min-width: 33%;
        // 2.75 = row gap / 2;
        max-width: calc(50% - 2.75%);
    }
}

.imageWrapper {
    border: 1px solid $pen-border-grey;
    box-sizing: border-box;
    line-height: 0;

    img {
        width: 100%;
        height: auto;
        object-fit: contain;

    }
}

.nameCta {
    justify-content: center;
    font-weight: 400;
    font-size: 18px;
    line-height: 28px;
    letter-spacing: 0;
    text-align: center;
    text-transform: capitalize;
    color: $origin-black;

    @include screen(md, true) {
        min-width: auto;
        font-size: 16px;
    }

    &:hover {
        >span {
            color: $pen-gold;
            margin-right: 0 !important;
        }
    }
}

.loadMoreContainer {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.line {
    width: 1px;
    height: 40px;
    background-color: $pen-gold;
    color: $pen-gold;
}

.loadMoreCta {
    justify-content: center;
    color: $pen-gold;

    &:hover {
        >span {
            margin-right: 0 !important;
            text-decoration: underline;
        }
    }
}