@use "../_colors.scss" as *;
@use "../_mixins.scss" as *;
@use '../fonts.scss' as *;

.hsh-commit-listing {

    .container {
        padding: 0 80px;

        @include screen(sm, true) {
            padding: 0 20px;
        }
    }

    .title {
        font-weight: 400;
        font-size: 36px;
        line-height: 44px;
        letter-spacing: 1px;
        text-align: center;
        text-transform: uppercase;

        @include screen(md, true) {
            font-size: 28px;
            line-height: 28px;
            letter-spacing: 0;
            @include applyFont($font-main-display-large, $font-cn-large);
        }
    }

    .content-container {
        margin-top: 40px;
    }

    .row {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        margin-bottom: 50px;
        gap: 0 20px;

        &.odd {
            .imgWrap {
                order: 2;
            }
        }

        @include screen(md, true) {
            gap: 0;
        }
    }

    .imgWrap,
    .citation__content {
        width: 50%;
    }

    .imgWrap {
        line-height: 0;

        @include screen(md, true) {
            margin-bottom: 20px;
        }
    }

    .imgWrap img {
        width: 100%;
        height: auto;
    }

    .citation__content {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        .citation__wrap {
            box-sizing: border-box;
            padding: 0 65px;

            @include screen(lg, true) {
                padding: 0 20px;
            }

            @include screen(md, true) {
                padding: 0;
            }
        }
    }

    .small-heading {
        font-weight: 400;
        font-size: 24px;
        line-height: 28px;
        letter-spacing: 1px;
        text-transform: uppercase;
        width: 100%;
        margin-bottom: 20px;

        @include applyFont($font-main-display-large, $font-cn-large);
    }

    .description {
        font-weight: 400;
        letter-spacing: 0%;
        color: $pen-dark-grey;

        @include descFontSize();
    }

    @include screen(md, true) {
        .row {
            flex-direction: column;

            .citation__content {
                order: 2;
            }
        }

        .imgWrap,
        .citation__content {
            width: 100%;
        }
    }
}