@use '@/styles/_mixins.scss' as *;
@use '@/styles/_colors.scss' as *;
@use '@/styles/_fonts.scss' as *;

$label-lh: 24px;

.title {
    text-align: center;
    margin-bottom: 40px;
    font-weight: 400;
    color: $origin-black;

    @include screen(md, true) {
        font-size: 28px;
        line-height: 28px;
        letter-spacing: 0%;
    }
}

.sliderWrapper {
    width: 100%;
    display: flex;
    justify-content: center;
}

.slider {
    width: 70%;
}

.slickContainer {
    justify-content: center;
}

.slickRow {
    display: flex;
    width: 100%;
    justify-content: center;
}

.singleItem {
    flex: 1;
    max-width: 33%;

    @include screen(xs, true) {
        max-width: none;
    }
}

.itemCard {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    :global(.richText){
        color: $pen-light-gold;
        @include applyFont($font-main-display-small, $font-cn);
    }
}

.cardImg {
    width: 60px;
    height: 60px;

    img {
        width: 100%;
        height: auto;
        object-fit: contain;
    }
}

.label {
    margin-top: 17px;
    margin-bottom: 13px;
    text-align: center;
    font-size: 16px;
    line-height: $label-lh;
    letter-spacing: 0%;
    min-height: calc(2 * $label-lh);
}

.statData {
    display: flex;
    align-items: flex-end;

    div {
        align-content: flex-end;
        line-height: 1;
    }
}

.statValue,
.statIcon {
    color: $pen-light-gold;
    font-family: Minion Pro;
}

.statValue {
    font-size: 56px;
    line-height: 44px;
    letter-spacing: 0%;
    text-transform: uppercase;
}

.statIcon {
    font-size: 32px;
    line-height: 44px;
    text-transform: uppercase;
}

.remarks {
    margin-top: 50px;
    text-align: center;
    font-weight: 500;
    font-style: Medium;
    font-size: 12px;
    line-height: 1;
    letter-spacing: 0%;
    color: $pen-mid-grey;
}