@use '@/styles/_mixins.scss' as *;
@use '@/styles/_colors.scss' as *;
@use '@/styles/_fonts.scss' as *;
@use '@/styles/_slick.scss' as *;
@use '../BannerCarousel/index.module.scss';


.bannerCarousel {

    :global(.slick-slider) {
        position: relative;
    }

    &:global(.BannerCarouselDots) {
        :global(.slick-arrow) {
            cursor: pointer;
            position: absolute;
            top: calc(50% - $slideBodyBottomP) !important;
        }

        :global(.slick-slider ){
            :global(.slick-dots) {
                bottom: 20px;
            }
        }

        .carouselTitle {
            padding: 50px 80px 0;
            font-weight: 400;
            font-size: 36px;
            line-height: 44px;

            // hsh-316: spacing update to 0;
            letter-spacing: 0px;
            text-align: center;
            @include applyFont($font-main-display-large, $font-cn-large);

            @include screen(md, true) {
                padding: 40px 20px 0;
                font-size: 28px;
                line-height: 28px;
                letter-spacing: 0;

            }
        }

        :global(.press-slider) {
            :global(.slick-slider) {
                :global(.slideBody) {
                    .textBox h4 {
                        font-weight: 400;
                        font-size: 24px;
                        line-height: 28px;
                        letter-spacing: 1px;
                        text-transform: uppercase;
                        @include applyFont($font-main-display-large, $font-cn-large);
                    }
                }
            }
        }

        >div {
            padding-top: 50px;

            @include screen(md, true) {
                padding-top: 40px;
            }
        }
    }

    :global(.slick-dots) {
        margin-top: 40px;
    }
}
